    /**
     * 播放视频
     * @param tab 
     * @see name        播放的视频的名字，一般情况下不需要传，用AppConfig中配置好的，如果ios,android name=="full"则为全屏视频，不填为激励视频
     * @see onClose   视频播放完成的回调  true 完整观看，false为完整观看
     * @see onFailed  视频播放失败的回调
     */
    public static playVideo_csryw(tab: { name?: string, onClose: Function, onFailed: Function }): void {
        if (AppPlatform.is_KS_csryw()) {
            KSAPI.showRewardedVideoAd_csryw(tab.onClose, tab.onFailed, tab.onClose);
        } else if (AppPlatform.is_WECHAT_GAME_csryw()) {
            WXAPI.showRewardedVideoAd_csryw(tab.onClose, tab.onFailed, tab.name);
        } else if (AppPlatform.is_TT_GAME_csryw()) {
            TTAPI.showRewardedVideoAd_csryw("未知", tab.onClose, tab.onFailed);
        } else if (AppPlatform.is_VIVO_GAME_csryw()) {
            VIVOAPI.showRewardedVideoAd_csryw(tab.onClose, tab.onFailed);
        } else if (AppPlatform.is_OPPO_GAME_csryw()) {
            OPPOAPI.showRewardedVideoAd_csryw(tab.onClose, tab.onFailed);
        } else if (AppPlatform.is_QQ_PLAY_csryw()) {
            QQMiniGameAPI.showRewardedVideoAd_csryw(tab.onClose, tab.onFailed);
        } else if (AppPlatform.is_Android_csryw() || AppPlatform.is_Iphone_csryw()) {
            LogUtils.log_csryw("playVideo android ios 未实现")
        }
        else {//其他平台
            LogUtils.log_csryw("playVideo 其他平台 未实现")
            if (tab.onClose) {
                tab.onClose(true);
            }
        }
    }

