    //检测更新
    public static checkUpdate_csryw() {
        let platform = "";
        if (AppPlatform.is_WECHAT_GAME_csryw()) {
            platform = "wx";
        } else if (AppPlatform.is_TT_GAME_csryw()) {
            platform = "tt";
        }
        //支持 wx 与 头条
        if (platform != "") {
            console.log("启动了版本更新机制。。。。")
            let updateManager = window[platform].getUpdateManager()
            updateManager.onCheckForUpdate(function (res) {
                console.log("是否需要更新 : ", res.hasUpdate)
            })
            updateManager.onUpdateReady(function () {
                window[platform].showModal({
                    title: '更新提示',
                    content: '新版本已经准备好，是否重启小游戏？',
                    success: function (res) {
                        if (res.confirm) {
                            updateManager.applyUpdate()
                        }
                    }
                })
            })
            updateManager.onUpdateFailed(function () {
                console.log("新版本下载失败!!!")
            })
        }
    }


    /**
     * 跳转其他小游戏
     * @param data 
     * @param ad_tag 标签
     */
    public static navigateToMiniProgram_csryw(data: any, ad_tag?: any) {
        if (data) {
            LogUtils.log_csryw("跳转游戏： " + data.title);
            if (AppPlatform.is_TT_GAME_csryw()) {//抖音
                TTAPI.showMoreGamesModal_csryw(() => {
                    LogUtils.log_csryw("跳转成功")
                }, () => {
                    LogUtils.log_csryw("跳转失败")
                    EventMgr.emitEvent_csryw(ryw_Event.ryw_ADKRQ_ClickQuit_csryw);
                });
            } else if (AppPlatform.is_WECHAT_GAME_csryw()) {//微信
                RYPlatformMgr.sendClickAd_csryw(data.id);
                //点击广告 
                UmengMgr.sendReportAdClickStart_csryw(data.title, data.appid);
                WXAPI.navigateToMiniProgram_csryw(data.appid, data.url, (res) => {
                    LogUtils.log_csryw("跳转成功")
                    //ALD.aldSendReportAdClickSuccess_csryw(data);
                    UmengMgr.sendReportAdClickSuccess_csryw(data.title, data.appid);
                    RYPlatformMgr.sendClickAdAllow_csryw(data.id);
                }, (res: any) => {
                    LogUtils.log_csryw("跳转失败")
                    EventMgr.emitEvent_csryw(ryw_Event.ryw_ADKRQ_ClickQuit_csryw);
                    if (res.errMsg == "navigateToMiniProgram:fail cancel") {
                        LogUtils.log_csryw("用户取消跳转");
                        //ALD.aldSendReportAdClickFail_csryw(data);
                        UmengMgr.sendReportAdClickFail_csryw(data.title, data.appid);
                        EventMgr.emitEvent_csryw(ryw_Event.ryw_ADKRQ_ClickQuit_csryw, ad_tag)
                    }
                }, (res: any) => {
                    LogUtils.log_csryw("跳转完成")
                });
            }
            else if (AppPlatform.is_OPPO_GAME_csryw()) {
                OPPOAPI.navigateToMiniProgram_csryw(data.appid, data.title, data.url, (res) => {
                    LogUtils.log_csryw("跳转成功")
                }, (res) => {
                    LogUtils.log_csryw("跳转失败")
                    EventMgr.emitEvent_csryw(ryw_Event.ryw_ADKRQ_ClickQuit_csryw);
                }, (res: any) => {
                    LogUtils.log_csryw("跳转完成")
                });


            }
            else if (AppPlatform.is_QQ_PLAY_csryw()) {  //qq小游戏
                QQMiniGameAPI.navigateToMiniProgram_csryw(data.appid, data.url, (res) => {
                    LogUtils.log_csryw("跳转成功")
                }, (res: any) => {
                    LogUtils.log_csryw("跳转失败")
                    EventMgr.emitEvent_csryw(ryw_Event.ryw_ADKRQ_ClickQuit_csryw);
                }, (res: any) => {
                    LogUtils.log_csryw("跳转完成")
                });
            }
            else if (AppPlatform.is_VIVO_GAME_csryw()) {
                VIVOAPI.navigateToMiniProgram_csryw(data.appid, data.url, (res) => {
                    LogUtils.log_csryw("跳转成功")
                }, (res: any) => {
                    LogUtils.log_csryw("跳转失败")
                    EventMgr.emitEvent_csryw(ryw_Event.ryw_ADKRQ_ClickQuit_csryw);
                }, (res: any) => {
                    LogUtils.log_csryw("跳转完成")
                });
            }
        }
    }

