const { ccclass, property } = cc._decorator;

var _temp: cc.Vec2 = new cc.Vec2;

export class Input {
    public slide: boolean;
    public direction: cc.Vec2 = new cc.Vec2;

    public reset(): void {
        this.slide = false;
        this.direction.set(cc.Vec2.ZERO);
    }
}

@ccclass
export default class InputSysten extends cc.Component {
    @property(cc.Float)
    slideThreshold: number = 5;

    private _touchId: number;

    private _touching: boolean = false;
    private _touchStartPos: cc.Vec2 = new cc.Vec2;
    private _touchPos: cc.Vec2 = new cc.Vec2;

    private _lastTouchPos: cc.Vec2 = new cc.Vec2;
    private _direction: cc.Vec2 = new cc.Vec2;
    private _lastSlidePos: cc.Vec2 = new cc.Vec2;
    private _slide: boolean = false;

    protected onLoad(): void {
        this.node.on(cc.Node.EventType.TOUCH_START, this.onTouchStart, this);
    }

    private onTouchStart(e: cc.Event.EventTouch): void {
        this.node.on(cc.Node.EventType.TOUCH_MOVE, this.onTouchMove, this);
        this.node.on(cc.Node.EventType.TOUCH_END, this.onTouchEnd, this);
        this.node.on(cc.Node.EventType.TOUCH_CANCEL, this.onTouchEnd, this);

        this._touching = true;
        this._touchId = e.getID();
        this._touchStartPos.set(e.getLocation());
        this._touchPos.set(e.getLocation());
        this._lastTouchPos.set(e.getLocation());
        this._lastSlidePos.set(e.getLocation());
    }

    private onTouchMove(e: cc.Event.EventTouch): void {
        if (this._touchId != e.getID()) {
            return;
        }

        this._touchPos.set(e.getLocation());
        this.processSlide(true);
    }

    private onTouchEnd(e: cc.Event.EventTouch): void {
        if (this._touchId != e.getID()) {
            return;
        }
        this._touching = false;
        this._touchPos.set(e.getLocation());
        this.processSlide(false);
    }

    private processSlide(move: boolean): void {
        if (this._touchPos.equals(this._lastSlidePos)) {
            return;
        }
        let distance = 0;
        var targetPos = move ? this._lastSlidePos : this._touchStartPos;
        var slideThreshold = move ? 160 : this.slideThreshold;
        distance = cc.Vec2.distance(targetPos, this._touchPos);
        if (distance < slideThreshold) {
            return;
        }
        this._slide = true;
        this._touchPos.sub(targetPos, this._direction);
        this._direction.divSelf(distance);
        this._lastSlidePos.set(this._touchPos);

        // if (move) {
        //     distance = cc.Vec2.distance(this._lastTouchPos, this._touchPos);
        //     if (distance < 80) {
        //         return;
        //     }
        //     this._touchPos.sub(this._lastTouchPos, this._direction);
        //     this._slide = true;
        // } else {
        //     distance = cc.Vec2.distance(this._touchPos, this._lastSlidePos);
        //     if (distance < this.slideThreshold) {
        //         return;
        //     }
        //     this._touchPos.sub(this._lastSlidePos, this._direction);
        //     this._slide = true;
        // }
    }

    public accumulateInput(input: Input, dt: number): void {
        if (this._slide) {
            this._slide = this._slide;
            input.slide = true;
            input.direction.set(this._direction);
            this._slide = false;
        }
    }

    public clear(): void {
        this._slide = false;
        this._direction.set(cc.Vec2.ZERO);
    }

    protected update(dt: number): void {
        // var distance = cc.Vec2.distance(this._lastTouchPos, this._touchPos);
        this._lastTouchPos.lerp(this._touchPos, 0.8, this._lastTouchPos);
        // var speed = distance / dt;
        // console.log(speed);
    }
}
