import FixComponent from "../../common/fixcomponent/FixComponent";
import FixManager from "../../common/fixcomponent/FixManager";
import GamaManager from "../manager/GamaManager";

const { ccclass, property, executionOrder } = cc._decorator;

@ccclass
export default class UnitMovement extends FixComponent {
    private canMove: boolean = true;

    private velocity: cc.Vec2 = new cc.Vec2;

    public curVelocity: cc.Vec2 = new cc.Vec2;

    public bodyRadius: number = 0;

    public hit: boolean = false;
    public hitY: boolean = false;
    public hitX: boolean = false;
    public hitPoint: cc.Vec2 = new cc.Vec2;

    public move(velocity: cc.Vec2) {
        if (this.canMove == false) {
            return;
        }
        this.velocity.set(velocity);
    }

    private StopMoving(){
        this.velocity.set(cc.Vec2.ZERO);
    }

    public DisableMove() {
        this.StopMoving();
        this.canMove = false;
    }

    public EnableMove() {
        this.canMove = true;
    }

    private ResetHit(): void {
        this.hit = false;
        this.hitX = false;
        this.hitY = false;
        this.hitPoint.set(cc.Vec2.ZERO);
    }

    public fixUpdate(): void {
        this.ResetHit();
        if (this.canMove == false || this.velocity.lengthSqr() <= 1e-5) return;   
        this.curVelocity.set(this.velocity);
        var fixedDeltaTime = FixManager.fixedDeltaTime;
        var targetPos = cc.v2(
            this.velocity.x * fixedDeltaTime + this.node.x,
            this.velocity.y * fixedDeltaTime + this.node.y, 
        );
        
        var result = cc.v2();
        if (GamaManager.inst.fixMovementPosition(targetPos, result)) {
            this.hit = true;
            this.hitX = targetPos.x != result.x;
            this.hitY = targetPos.y != result.y;
            this.hitPoint.set(result);
        }

        this.node.setPosition(result);
        this.velocity.set(cc.Vec2.ZERO);
        this.node.zIndex = Math.floor(cc.macro.MAX_ZINDEX - (this.node.y - GamaManager.inst.minY));
    }
}