/**
 * 远程资源管理
 * 下载后的 cc.Asset 计数加1后 放入 数组存储，下次直接拿取
 */
export default class RemoteMgr {
    private static remoteArrayTexture_csryw: { [key: string]: cc.Texture2D; } = {}

    /**
     * 移除所有已加载的
     */
    public static removeAllRemote_csryw() {
        for (let k in this.remoteArrayTexture_csryw) {
            this.remoteArrayTexture_csryw[k].decRef();
        }
        this.remoteArrayTexture_csryw = {};
    }

    /**
     * 下载远程图片资源,成功后放入数组，计数自增
     * @param url 
     * @param listener 
     */
    public static loadRemote_csryw(url: string, listener: FMListener) {
        let texture2D = this.remoteArrayTexture_csryw[url];
        if (texture2D) {
            callFM_csryw(listener, null, this.remoteArrayTexture_csryw[url]);
        } else {
            cc.assetManager.loadRemote(url, (error, data) => {
                if (error) {
                    callFM_csryw(listener, error, null);
                    return;
                } else {
                    if (!this.remoteArrayTexture_csryw[url]) {
                        this.remoteArrayTexture_csryw[url] = data;
                        this.remoteArrayTexture_csryw[url].addRef();//计数+1
                    }
                    callFM_csryw(listener, null, this.remoteArrayTexture_csryw[url]);
                }
            })
        }
    }

}