import SoundMgr from "../../../FrameWork/Mgr/SoundMgr";
import AssetManager from "../../common/AssetManager";

export default class AudioManager {
    private static _inst: AudioManager;

    public static get inst(): AudioManager {
        if (this._inst == null) {
            this._inst = new AudioManager;
        }
        return this._inst;
    }

    public bgmEnable: boolean = true;
    public sfxEnable: boolean = true;

    public bgmVolume: number = 1;
    public sfxVolume: number = 1;

    public static DEFAULT_VOLUME = 0.3

    bgmAudioID: number = -1;
    audioId: number = -1;

    private bgm_url: string = "music1"
    async playBGM(url: string) {
        if (!this.bgmEnable) {
            return;
        }
        this.bgm_url = url;
        var audioUrl: cc.AudioClip = (await AssetManager.loadRes("" + url, cc.AudioClip)) as cc.AudioClip;// this.getUrl(url);

        if (this.bgmAudioID >= 0) {
            cc.audioEngine.stop(this.bgmAudioID);
        }
        if (this.bgmVolume > 0) {
            this.bgmAudioID = cc.audioEngine.play(audioUrl, true, this.bgmVolume);
        }
    }

    stopSFX(audioId: any) {
        var ok = cc.audioEngine.stop(audioId);
        return ok;
    }

    private lastplaysfxtime = {};
    async playSFX(url: string) {
        if (!this.sfxEnable) {
            return;
        }

        if (this.lastplaysfxtime[url]) {
            if (Date.now() - this.lastplaysfxtime[url] < 100) {
                // console.log(url,"跳过")
                return;
            }
        }

        this.lastplaysfxtime[url] = Date.now();
        var audioUrl: cc.AudioClip = (await AssetManager.loadRes(url, cc.AudioClip)) as cc.AudioClip;// this.getUrl(url);

        if (this.sfxVolume > 0) {
            this.audioId = cc.audioEngine.play(audioUrl, false, this.sfxVolume);
            return this.audioId;
        }
    }

    pauseBGM() {
        if (this.bgmAudioID >= 0) {
            cc.audioEngine.pause(this.bgmAudioID);
            // cc.log("暂停bgm")
        }
    }

    resumBGM() {
        if (!this.bgmEnable) {
            return;
        }
        if (this.bgmAudioID >= 0) {
            cc.audioEngine.resume(this.bgmAudioID);
            // cc.log("恢复bgm")
        }
    }

    setBGMVolume(v: number, force: boolean = false) {
        if (this.bgmVolume != v || force) {
            cc.sys.localStorage.setItem("bgmVolume", v);
            this.bgmVolume = v;
            cc.audioEngine.setVolume(this.bgmAudioID, v);
        }
        if (this.bgmAudioID >= 0) {
            if (v > 0) {
                cc.audioEngine.resume(this.bgmAudioID);
            } else {
                cc.audioEngine.pause(this.bgmAudioID);
            }
        } else {
            this.playBGM(this.bgm_url);
        }
    }

    setSFXVolume(v: number, force: boolean = false) {
        if (this.sfxVolume != v || force) {
            cc.sys.localStorage.setItem("sfxVolume", v);
            this.sfxVolume = v;
            //设置音效大小会同时设置背景音乐的声音，不设置音效大小，本地音效依然可以受控使用，暂未找到原因
            // cc.audioEngine.setEffectsVolume(v);
        }
    }
}