import * as CryptoJS from "./aes.js"

export default class AesTools {
    private static readonly KEY_csryw = 'b#63fFJ6AvkK3YT*';
    private static readonly IV_csryw = 'J$f4DU%sNL73M&Go';

    //加密
    public static encrypt_csryw(str: string) {
        var key = CryptoJS.enc.Utf8.parse(AesTools.KEY_csryw);// 秘钥
        var iv = CryptoJS.enc.Utf8.parse(AesTools.IV_csryw);//向量iv
        var encrypted = CryptoJS.AES.encrypt(str, key, { iv: iv, mode: CryptoJS.mode.CBC, padding: CryptoJS.pad.Pkcs7 });
        return encrypted.toString();
    }

    //解密
    public static decrypt_csryw(str: string) {
        var key = CryptoJS.enc.Utf8.parse(AesTools.KEY_csryw);// 秘钥
        var iv = CryptoJS.enc.Utf8.parse(AesTools.IV_csryw);//向量iv
        var decrypted = CryptoJS.AES.decrypt(str, key, { iv: iv, padding: CryptoJS.pad.Pkcs7 });
        return decrypted.toString(CryptoJS.enc.Utf8);
    }


}