import FixComponent from "../../../common/fixcomponent/FixComponent";
import FixManager from "../../../common/fixcomponent/FixManager";
import ActionInfo from "./ActionInfo";
import { IActionConsumer } from "./IActionConsumer";
const { ccclass, property } = cc._decorator;

@ccclass
export default class UnitAction extends FixComponent {
    public animation: cc.Animation;
    
    private actionConsumer: IActionConsumer
    private animationState: cc.AnimationState;

    private actions: ActionInfo[] = [];
    public currentAction: ActionInfo;
    
    private priorityDuration: number = 0;
    private get currentAnimPriority(): number {
        return this.currentAction == null ? 0 : 
        (this.priorityDuration <= 0 ? 0 : this.currentAction.priority);
    }

    protected onLoad(): void {
        this.animation = this.getComponent(cc.Animation) ?? this.getComponentInChildren(cc.Animation);
        if (this.animation != null) {
            this.hookAnimation(this.animation);
            this.animation.on(cc.Animation.EventType.PLAY, this.onAnimationPlay, this);
            this.animation.on(cc.Animation.EventType.FINISHED, this.onAnimationEnd, this);
        }
    }

    private hookAnimation(animation: cc.Animation): void {
        if (this.animation["_hook_"]) {
            return;
        }
        var chips = this.animation.getClips();
        for (let i = 0; i < chips.length; i++) {
            var state = this.animation.getAnimationState(chips[i].name);
            var func = state["update"];
            state["update"] = () => {};
            state["hookUpdate"] = func;
        }
        this.animation["_hook_"] = true;
    }

    public setAction(actions: ActionInfo[]): void {
        this.actions = actions;
    }

    public setActionConsumer(consumer: IActionConsumer): void {
        this.actionConsumer = consumer;
    }

    public playAction(name: string): boolean {
        if (this.animation == null) {
            return false;
        }

        var action = this.actions.find(item => item.key == name);
        if (action == null || (this.currentAction && this.currentAction.key == action.key) || this.currentAnimPriority > action.priority) {
            return false;
        }

        this.currentAction = action;
        var state = this.animation.getAnimationState(this.currentAction.key);
        if (state) {
            this.priorityDuration = state.duration;
        } else {
            console.error(this.node.name, this.currentAction.key);
        }
        this.animation.play(this.currentAction.key);
        return true;
    }

    public fixUpdate(): void {
        var dt = FixManager.fixedDeltaTime;
        if (this.animationState) {
            this.animationState["hookUpdate"](dt);
        }
        this.priorityDuration -= dt;
    }

    private onAnimationPlay(): void {
        var state = arguments[1] as cc.AnimationState;
        this.animationState = state;
    }

    private onAnimationEnd(): void {
        var state = arguments[1] as cc.AnimationState;
        if (this.currentAction && this.currentAction.key == state.name) {
        }
    }

    private onActionEvent(): void {
        var args: any[] = [];
        for (var i = 0; i < arguments.length; i++) {
            args.push(arguments[i]);
        }
        var action = args[0];
        args.shift();
        this.actionConsumer && this.actionConsumer.onConsumeAction(action, args);
    }
}