import User from "../../../FrameWork/User/User";
import Common from "../../../FrameWork/Util/Common";
import ConfigManager from "../../ConfigManager";
import AssetManager from "../../common/AssetManager";
import FixManager from "../../common/fixcomponent/FixManager";
import { GlobalMessage } from "../../tools/GlobalMessage";
import AudioManager from "../manager/AudioManager";
import GamaManager, { GameState } from "../manager/GamaManager";
import { BlackMaskShow, HeroDead, HeroHurt, HeroSuperEnd, HeroSuperStart, KillMonster } from "../message/Messages";
import WeaponConfig from "../structs/WeaponConfig";
import ChaProperty from "../structs/battle/ChaProperty";
import { DamageResult } from "../structs/battle/DamageInfo";
import UltSkillConfig from "../structs/character/UltSkillConfig";
import Character from "./Character";
import Fbsd from "./hero/Fbsd";
import Feidao from "./hero/Feidao";
import WeaponTw from "./hero/WeaponTw";
import BallMove from "./movement/BallMove";
import UltSkill from "./skill/UltSkill";
const { ccclass, property, executionOrder } = cc._decorator;

@ccclass
export default class Hero extends Character {
    public invincible: boolean = false;

    public energy: number = 0;
    public maxEnergy: number = 100;
    public killEnergyGet: number = 0.5;

    private energyTimer: number = 0;

    @property(cc.Node)
    tuoweiParent: cc.Node = null;

    private initWeaponFlag: boolean = false;
    private weaponConfig: WeaponConfig = null;
    private weaponTw: WeaponTw = null;
    private ultSkillConfig: UltSkillConfig = null;
    private ultSkill: UltSkill;

    public ballMove: BallMove;
    public feidaoAbility: Feidao;
    public fbsdAbility: Fbsd;

    public attackBoss: Character;

    public inSuper: boolean = false;
    public get isSuper(): boolean {
        return this.getProperty("supper") != 0;
    }

    protected onLoad(): void {
        super.onLoad();
        this.ballMove = this.getComponent(BallMove);
        this.feidaoAbility = this.getComponent(Feidao);
        this.fbsdAbility = this.getComponent(Fbsd);
    }

    protected start(): void {
        
    }

    public InitProperty(property: ChaProperty): void {
        super.InitProperty(property)
        if (this.initWeaponFlag == false) {
            this.initWeapon();
        }
        this.weaponTw && this.weaponTw.updateTuowei();
    }

    public initWeapon(): void {
        this.initWeaponFlag = true;;
        this.weaponConfig = ConfigManager.getWeaponConfig(User.useWeaponId);
        var buffs = (this.weaponConfig.buff ?? "").split("|");
        for (var i = 0; i < buffs.length; i++) {
            this.removeBuff(buffs[i]);
            this.addBuff(buffs[i]);
        }

        this.ultSkillConfig = ConfigManager.ultSkillConfig["hero_ultskill_1"];
        this.ultSkill = this.addComponent(this.ultSkillConfig.class);
        this.ultSkill.init(this, this.ultSkillConfig);

        var prefab = AssetManager.getPrefab("WeaponTw_" + this.weaponConfig.id);
        if (prefab) {
            var node = cc.instantiate(prefab);
            node.parent = this.tuoweiParent;
            this.weaponTw = node.getComponent(WeaponTw);
            this.weaponTw.init(this);
        }
    }

    public catapult(direction: cc.Vec2): void {
        AudioManager.inst.playSFX("Sound/slide" + Math.floor(Math.random() * 1.4));
        this.ballMove.catapult(direction.mul(this.getProperty("catapultForce") * 0.5), 0.3);
        this.setDirection(Math.sign(direction.x));
        if (this.getProperty("feidao") > 0) {
            this.feidaoAbility?.fire(direction, this.getProperty("feidao"));
        }
    }

    protected updateReadProperty(name: string): void {
        super.updateReadProperty(name);
        this.weaponTw && this.weaponTw.updateTuowei();
    }

    public hit(target: Character): void {
        if (target.config.type > 1) {
            this.attackBoss = target;
        }

        if (target.isDead) {
            if (this.attackBoss == target) {
                this.attackBoss = null;
            }

            this.addEnergy(true);
            GlobalMessage.emit(KillMonster.create(target));
        }
    }

    private addEnergy(isKill: boolean): void {
        if (this.ultSkill.isRunning) {
            return;
        }

        var add = isKill ? this.getProperty("killEnergyGet") : 1;
        this.energy += add;
        if (this.energy >= this.maxEnergy) {
            this.useBigSkill();
        }
    }

    public useBigSkill(): void {
        if (this.ultSkill == null || this.ultSkill.isRunning) {
            return;
        }
        this.energy = 0;
        this.ultSkill.castSkill();
    }

    public beHurt(result: DamageResult): void {
        if (this.invincible) {
            return;
        }
        super.beHurt(result);
        GlobalMessage.emit(new HeroHurt);
    }

    public dead(): void {
        super.dead();
        GlobalMessage.emit(new HeroDead);
    }

    protected deadRemove(): void {
        
    }

    public resurrect(): void {
        this.hp = this.getProperty("maxHp");
        this.isDead = false;
    }

    public fixUpdate(): void {
        super.fixUpdate();

        if (GamaManager.inst.state != GameState.Run) {
            return;
        }

        this.energyTimer -= FixManager.fixedDeltaTime;
        if (this.energyTimer <= 0) {
            this.energyTimer = 1;
            this.addEnergy(false);
        }

        if (this.isSuper == true) {
            if (this.inSuper == false) {
                this.inSuper = true;
                GlobalMessage.emit(new HeroSuperStart);
            }
        } else {
            if (this.inSuper == true) {
                this.inSuper = false;
                GlobalMessage.emit(new HeroSuperEnd);
            }
        }
    }
}