import AssetManager from "../../common/AssetManager";
import { GlobalMessage } from "../../tools/GlobalMessage";
import AudioManager from "../manager/AudioManager";
import EffectManager from "../manager/EffectManager";
import { MonsterDead } from "../message/Messages";
import { DamageResult } from "../structs/battle/DamageInfo";
import Character from "./Character";
const { ccclass, property, executionOrder } = cc._decorator;

@ccclass
export default class Monster extends Character {

    protected start(): void {
        var enterArr = ["Enter", "Enter2"];
        var enter = enterArr[Math.floor(Math.random() * 1.8)];
        if (!this.unitAction.playAction(enter)) {
            this.unitAction.playAction("Enter");
        }
    }

    public dead(): void {
        super.dead();
        GlobalMessage.emit(new MonsterDead); 
        if (this.config.type > 1) {
            EffectManager.inst.addEffect("Effect_batter_good", this.node, cc.v2(this.node.x, this.node.y));
        }
    }

    public beHurt(result: DamageResult): void {
        super.beHurt(result);
        if (this.config.hurtSound != null && this.config.hurtSound != "") {
            AudioManager.inst.playSFX("Sound/" + this.config.hurtSound);
        }
    }
}