import { ResSubMgr } from "../../../FrameWork/Mgr/ResSubMgr";
import TweenScale from "../../../FrameWork/Tween/TweenScale";
import User from "../../../FrameWork/User/User";
import Common from "../../../FrameWork/Util/Common";
import TTAPI from "../../../Platform/tt/TTAPI";
import ConfigManager from "../../ConfigManager";
import { GlobalMessage } from "../../tools/GlobalMessage";
import GamaManager from "../manager/GamaManager";
import HeroManager from "../manager/HeroManager";
import { SelectTco } from "../message/Messages";
import TcoInfo from "../structs/character/TcoInfo";

const {ccclass, property} = cc._decorator;

@ccclass
export default class TcoView extends cc.Component {
    @property(cc.Node)
    mask: cc.Node = null;

    @property([cc.Node])
    private items: cc.Node[] = [];

    @property(cc.Node)
    private allBtn: cc.Node = null;

    @property(cc.Node)
    private sharedBtn: cc.Node = null;

    private tcoOptions: string[] = [];

    public closeCallBack: Function;

    protected start(): void {
        this.getComponentInChildren(TweenScale).openAction({ callback: () => {
            this.mask.active = false;
        }, target: this});
        this.refreshTcoOption();
    }

    public init(options: string[], closeCallBack: Function): void {
        this.tcoOptions = options;
        this.closeCallBack = closeCallBack;

        var needShow = !GamaManager.inst["sharedShowed"] && GamaManager.inst.totalTime >= 60 && Math.random() <= 0.1;
        this.allBtn.active = !needShow;
        this.sharedBtn.active = needShow;
        if (needShow) {
            GamaManager.inst["sharedShowed"] = true;
            TTAPI.reportAnalytics("sharedVideoShow");
            User.updateNoSharedTime();
        }
    }

    private refreshTcoOption(): void {
        for (var i = 0; i < this.items.length; i++) {
            var item = this.items[i];
            var data = this.tcoOptions[i];
            if (data == null) {
                item.active = false;
            } else {
                this.setItemView(item, data);
            }
        }
    }

    private setItemView(item: cc.Node, optionId: string): void {
        var info = ConfigManager.tcoConfig.find(item => item.id == optionId);
        var level = HeroManager.inst.getTcoLevel(info.id);
        item.active = true;
        var name = item.getChildByName("name").getComponent(cc.Label);
        name.string = info.name;
        var lv = item.getChildByName("level").getComponent(cc.Label);
        lv.string = "Lv." + (level + 1);
        var desc = item.getChildByName("desc").getComponent(cc.Label);
        desc.string = info["desc" + (level + 1)];
        var sprite = item.getChildByName("icon").getComponent(cc.Sprite);
        Common.loadSpriteFrame("subResGame", "Texture/Icon/" + info.icon, (spriteFrame) => {
            sprite.spriteFrame = spriteFrame;
        });
        var bgSprite = item.getComponent(cc.Sprite);
        Common.loadSpriteFrame("subResGame", "Texture/View/Tco/" + (level + 1), (spriteFrame) => {
            bgSprite.spriteFrame = spriteFrame;
        });
        item["data"] = info;
    }

    private onClickItem(event: cc.Event.EventTouch): void {
        var btn = event.currentTarget;
        var info = btn["data"] as TcoInfo;
        GlobalMessage.emit(SelectTco.create(info.id));
        this.close();
    }

    private onClickRefresh(): void {
        var self = this;
        TTAPI.showRewardedVideoAd_csryw("刷新技能", (finish) => {
            if (finish) {
                self.tcoOptions = HeroManager.inst.getSelectOptions(); 
                self.refreshTcoOption();
            }
        }, () => { })
    }

    private getAll(): void {
        for (var i = 0; i < this.tcoOptions.length; i++) {
            GlobalMessage.emit(SelectTco.create(this.tcoOptions[i]));
        }
    }

    private onClickAll(): void {
        var self = this;
        TTAPI.showRewardedVideoAd_csryw("获得所有", (finish) => {
            if (finish) {
                self.getAll();
                this.close();
            }
        }, () => { })
    }

    private onClickShared(): void {
        var self = this;
        TTAPI.ShowLoading();
        TTAPI.stopRecord_csryw(() => {
            TTAPI.HideLoading();
            GamaManager.inst.tryStartRecord();
            TTAPI.shareRecord_csryw(() => {
                self.getAll();
                this.close();
                TTAPI.reportAnalytics("sharedVideo");
            });
        });
    }

    private close(): void {
        this.node.destroy();
        this.closeCallBack && this.closeCallBack();
    }
}