import User from "../User/User";
import { LogUtils } from "../Util/LogUtils";
import AppConfig from "../Config/AppConfig";


//游戏管理器，游戏代码的入口
export default class GameMgr {
    public isFirstLoadAD: boolean = false;
    public isWinner: boolean = false;
    private static _instance_csryw: GameMgr = new GameMgr();

    public static getInstance_csryw(): GameMgr {
        return GameMgr._instance_csryw;
    }

    /**
     * 预加载一些场景 在Main.ts中
     */
    public preloadScene_csryw() {
        // cc.director.preloadScene("FMMainScene");
        // cc.director.preloadScene("FMGameScene");
        // cc.director.preloadScene("FMGameSettleWinScene");
        // cc.director.preloadScene("FMGameSettleFailScene");
        LogUtils.warn_csryw("GameMgr.getInstance().preloadScene_csryw这里可以加载需要预加载的场景");
    }

    //在合适的时候  cc.director.preloadScene 预加载你的场景

    /**
     * 登录加载后跳转自己的场景
     */
    public onLoadToWorldScene_csryw() {
        cc.director.loadScene("FMMainScene", function (err, scene) {
            console.log("跳转游戏场景 FMMainScene")
        });

    }



    //游戏存档,合适的时候调用它来保存
    public saveGameData_csryw() {
        //存本地
        cc.sys.localStorage.setItem("data" + AppConfig.AppID_csryw, User.getSaveData_csryw());
    }


}