import { EndlessTable } from "./battle/designer/table/EndlessTable";
import GameConfig from "./battle/structs/GameConfig";
import WeaponConfig from "./battle/structs/WeaponConfig";
import ActionEffectConfig from "./battle/structs/battle/ActionEffectConfig";
import { EndlessInfo } from "./battle/structs/battle/EndlessStage";
import BuffConfig from "./battle/structs/battle/buff/BuffConfig";
import CharacterInfo from "./battle/structs/character/CharacterInfo";
import CharacterLevel from "./battle/structs/character/CharacterLevel";
import UltSkillConfig from "./battle/structs/character/UltSkillConfig";
import TcoInfo from "./battle/structs/character/TcoInfo";

export default class ConfigManager {
    static loadLocal: boolean = false;
    static basePath: string = "https://oss.renyouwangluo.cn/qgdbl/json/"

    static endlessConfig: EndlessInfo[] = [];

    static characterConfig: { [id: string]: CharacterInfo } = {};

    static characterLevelConfig: CharacterLevel[] = []; 

    static tcoConfig: TcoInfo[] = [];

    static buffConfig: { [id: string]: BuffConfig } = {};

    static gameConfig: GameConfig = new GameConfig;

    static weaponConfig: WeaponConfig[] = [];

    static ultSkillConfig: { [id: string]: UltSkillConfig } = {};

    static loadConfigs(callback: () => void): void {
        var self = this;
        let p1 = new Promise((resove) => {
            self.loadJson(self.basePath + "CharacterConfig.json", (jsonAsset) => {
                this.characterConfig = jsonAsset.json;
                resove(null);
            });
        });

        let p2 = new Promise((resove) => {
            self.loadJson(self.basePath + "CharacterLevelConfig.json", (jsonAsset) => {
                this.characterLevelConfig = jsonAsset.json;
                resove(null);
            });
        });

        let p3 = new Promise((resove) => {
            self.loadJson(self.basePath + "EndlessConfig.json", (jsonAsset) => {
                this.endlessConfig = jsonAsset.json;
                resove(null);
            });
        });

        let p4 = new Promise((resove) => {
            self.loadJson(self.basePath + "TcoConfig.json", (jsonAsset) => {
                this.tcoConfig = jsonAsset.json;
                resove(null);
            });
        });

        let p5 = new Promise((resove) => {
            self.loadJson(self.basePath + "BuffConfig.json", (jsonAsset) => {
                this.buffConfig = jsonAsset.json;
                resove(null);
            });
        });

        let p6 = new Promise((resove) => {
            self.loadJson(self.basePath + "GameConfig.json", (jsonAsset) => {
                this.gameConfig = jsonAsset.json;
                resove(null);
            });
        });

        let p7 = new Promise((resove) => {
            self.loadJson(self.basePath + "WeaponConfig.json", (jsonAsset) => {
                this.weaponConfig = jsonAsset.json;
                resove(null);
            });
        });

        let p8 = new Promise((resove) => {
            self.loadJson(self.basePath + "UltSkillConfig.json", (jsonAsset) => {
                this.ultSkillConfig = jsonAsset.json;
                resove(null);
            });
        });

        Promise.all([p1, p2, p3, p4, p5, p6, p7, p8]).then(() => {
            callback && callback();
        });
    }

    static loadJson(path: string, callback:(asset: cc.JsonAsset) => void): void {
        if (this.loadLocal) {
            this.loadLocalJson(path, callback);
        } else {
            cc.assetManager.loadRemote<cc.JsonAsset>(path, (err, asset: cc.JsonAsset) => {
                callback && callback(asset);
            });
        }
    }

    static loadLocalJson(path: string, callback:(asset: cc.JsonAsset) => void): void {
        path = path.replace(this.basePath, "");
        path = path.replace(".json", "");
        cc.resources.load<cc.JsonAsset>(path,(err, asset: cc.JsonAsset) => {
            callback && callback(asset);
        });
    }

    static loadEndlessConfig(path: string, callback: Function): void {
        cc.assetManager.loadRemote<cc.JsonAsset>(path, (err, asset: cc.JsonAsset) => {
            this.endlessConfig.length = 0;
            var json = asset.json as any[];
            for (var i = 0; i < json.length; i++) {
                this.endlessConfig.push(json[i]);
            }

            callback && callback();
        });
    }

    static loadCharacterConfig(path: string, callback: Function): void {
        cc.assetManager.loadRemote<cc.JsonAsset>(path, (err, asset: cc.JsonAsset) => {
            var character = {};
            var json = asset.json;
            for (var key in json) {
                character[key] = json[key];
            }
            this.characterConfig = character;
            callback && callback();
        });
    }

    static getTcoInfoById(id: string): TcoInfo {
        return this.tcoConfig.find(item => item.id == id);
    }

    static getTcoBuffList(id: string, level: number) {
        var info = this.getTcoInfoById(id);
        if (!info) return [];
        var data = info["level" + level];
        return data == "" ? [] : data.split("|");
    }

    static getBuffConfig(id: string): BuffConfig {
        return this.buffConfig[id];
    }

    static getWeaponConfig(id: string): WeaponConfig {
        return this.weaponConfig.find(item => item.id == id);
    }

    static getActionEffectConfig(key: string): ActionEffectConfig {
        return null
    }
}