/*
 * @Author: YeeChan
 * @Date: 2021-12-22 17:59:20
 * @Description: 弹出缩放换的
 */
const { ccclass, property } = cc._decorator;

@ccclass
export default class TweenScale extends cc.Component {
    @property({ tooltip: "初始缩放比例", type: cc.Float })
    public scaleStartNum: number = 0.65;
    @property({ tooltip: "结束缩放比例", type: cc.Float })
    public scaleEndNum: number = 1.0;
    @property({ tooltip: "缩放时间", type: cc.Float })
    public scaleTime: number = 0.3;

    /**回调 */
    private listener: FMListener = null;

    /**
     * 打开动作
     */
    public openAction(listener?: FMListener) {
        this.listener = listener;
        cc.Tween.stopAllByTarget(this);
        this.node.scale = this.scaleStartNum;
        cc.tween(this.node)
            .to(this.scaleTime, { scale: this.scaleEndNum }, { easing: cc.easing.backOut })
            .call(() => {
                if (this.listener) {
                    callFM_csryw(listener);
                }
            })
            .start();
    }

    /**
     * 关闭动作
     */
    public closeAction(listener?: FMListener) {
        this.listener = listener;
        cc.Tween.stopAllByTarget(this);
        cc.tween(this.node)
            .to(this.scaleTime, { scale: this.scaleStartNum }, { easing: cc.easing.backIn })
            .call(() => {
                if (this.listener) {
                    callFM_csryw(listener);
                }
            })
            .start();

    }

}
