import { ryw_Event } from "../../../../FrameWork/Event/EventEnum";
import EventMgr from "../../../../FrameWork/Event/EventMgr";
import ConfigManager from "../../../ConfigManager";
import WeaponItem from "./WeaponItem";

const {ccclass, property} = cc._decorator;

@ccclass
export default class WeaponView extends cc.Component {
    @property(cc.Node)
    content: cc.Node = null;

    @property(cc.Prefab)
    itemPrefab: cc.Prefab = null;

    protected onLoad(): void {
        this.node.getChildByName("mask").on("click", this.onClickMask, this);
    }

    protected start(): void {
        this.refreshList();
    }

    protected onEnable(): void {
        EventMgr.onEvent_csryw(ryw_Event.weaponRefresh, this.refreshList, this);
    }

    protected onDisable(): void {
        EventMgr.offEvent_csryw(ryw_Event.weaponRefresh, this.refreshList, this);
    }

    private refreshList(): void {
        var weaponConfigs = ConfigManager.weaponConfig;
        var childCount = this.content.childrenCount;
        var createCount = ConfigManager.weaponConfig.length - childCount;
        for (var i = 0; i < createCount; i++) {
            var config = weaponConfigs[i];
            var node = cc.instantiate(this.itemPrefab);
            node.parent = this.content;
        }

        for (var i = 0; i <this.content.childrenCount; i++) {
            var config = weaponConfigs[i];
            var node = this.content.children[i];
            if (config) {
                node.active = true;
                var item = node.getComponent(WeaponItem);
                item.setData(config);
            } else {
                node.active = false;
            }
        }
    }

    private onClickMask(): void {
        this.node.destroy();
    }
}
