const { ccclass, property } = cc._decorator;

@ccclass
export default class BloodBar extends cc.Component {
    @property([cc.SpriteFrame])
    public frame: cc.SpriteFrame[] = [];

    @property(cc.Sprite)
    public topBar: cc.Sprite = null;

    @property(cc.Sprite)
    public downBar: cc.Sprite = null;

    @property(cc.Label)
    public label: cc.Label = null;

    private m_Cur: number = 0;
    private m_Total: number = 0;
    private m_PerValue: number = 0;

    private m_TargetValue: number = 0;
    private m_TargetIndex: number = -1;
    private m_MaxIndex: number = 0;

    public setBar(total: number, perValue: number, cur: number = -1) {
        this.m_Total = total;
        this.m_Cur = cur == -1 ? this.m_Total : cur;
        this.m_PerValue = perValue;
        this.m_MaxIndex = Math.floor(this.m_Total / this.m_PerValue) - 1;
        this.updateIndexAndValue();
    }

    public updateColor() {
        this.topBar.fillRange = this.downBar.fillRange = 1;
        this.topBar.spriteFrame = this.frame[this.m_TargetIndex % this.frame.length];
        if (this.m_TargetIndex >= this.m_MaxIndex) {
            this.downBar.node.active = false;
        } else {
            this.downBar.node.active = true;
            this.downBar.spriteFrame = this.frame[(this.m_TargetIndex + 1) % this.frame.length];
        }
    }

    public updateIndexAndValue() {
        var cur = this.m_Cur;

        var old = this.m_TargetIndex;
        var targetIndex = Math.floor((this.m_Total - cur) / this.m_PerValue);
        this.m_TargetIndex = targetIndex;

        var targetValue = 0;
        if (cur % this.m_PerValue == 0) {
            targetValue = this.m_PerValue;
        }
        else {
            targetValue = cur % this.m_PerValue;
        }
        this.m_TargetValue = targetValue;

        if (old != this.m_TargetIndex) {
            this.updateColor();
        }
        this.updateBar();
    }

    public updateBar() {
        this.topBar.fillRange = this.m_TargetValue / this.m_PerValue;
        this.label.string = "x" + (this.m_MaxIndex - this.m_TargetIndex);
    }

    public ChangeValue(value: number) {
        this.m_Cur = value;
        this.m_Cur = Math.max(0, Math.min(this.m_Cur, this.m_Total));
        this.updateIndexAndValue();
    }
}