import FMComponentExtend from "../../Base/FMComponentExtend";
import TweenScale from "../../Tween/TweenScale";

/*
 * @Author: YeeChan
 * @Date: 2022-01-11 19:17:19
 * @Description:
 */
const { ccclass, property } = cc._decorator;

@ccclass
export default class DialogUIBase extends FMComponentExtend {
    protected EventEnumView_csryw: { [key: string]: string; } = {}

    /**显示节点 */
    protected showNode: cc.Node = null;
    /**缩放动作 */
    protected tweenScale: TweenScale = null;
    /**回调 */
    protected listener: FMListener = null;


    protected initView_csryw(): void {
        this.showNode = this.node.getChildByName("showNode");
        /**获取缩放动作 */
        this.tweenScale = this.showNode.getComponent(TweenScale);
    }

    /**
     * 打开弹窗
     */
    public openDialog(listener: FMListener) {
        this.listener = listener;
        this.__onInit_csryw();//强制初始化
        this.tweenScale.openAction()
    }

    /**
     * 关闭弹窗
     */
    public closeDialog(data?: any) {
        this.tweenScale.closeAction(handleFM_csryw(() => {
            callFM_csryw(this.listener, true, data);
            this.node.destroy();
        }, this))
    }


}