import DialogMgr from "../../../FrameWork/Mgr/DialogMgr";
import User from "../../../FrameWork/User/User";
import TTAPI from "../../../Platform/tt/TTAPI";
import ConfigManager from "../../ConfigManager";
import { GlobalMessage } from "../../tools/GlobalMessage";
import WeigthsRandom from "../../tools/WeigthsRandom";
import Hero from "../character/Hero";
import { StartSelectTco, KillMonster, SelectTco, BatterShow } from "../message/Messages";
import ChaProperty from "../structs/battle/ChaProperty";
import CharacterLevel from "../structs/character/CharacterLevel";
import TcoInfo from "../structs/character/TcoInfo";
import EffectManager from "./EffectManager";
import GamaManager from "./GamaManager";
const {ccclass, property} = cc._decorator;

@ccclass
export default class HeroManager extends cc.Component {
    private static _inst: HeroManager;

    public static get inst(): HeroManager {
        return this._inst;
    }

    public currIndex: number;
    public exp: number;

    public tcoLevelMap: { [id: string]: number } = {};

    public batterCount: number = 0;
    public batterTime: number = 0;

    public batterGoodCount: number = 0;
    public batterGoodTime: number = 0;

    public get currentModel(): CharacterLevel {
        return ConfigManager.characterLevelConfig[this.currIndex];
    }

    public get level(): number {
        return this.currIndex + 1;
    }

    public get maxExp(): number {
        return this.currentModel.exp;
    }

    protected onLoad(): void {
        HeroManager._inst = this;

        this.currIndex = 0;
        this.exp = 0;
    }

    protected start(): void {
        this.currIndex = 0;
    }

    protected onEnable(): void {
        GlobalMessage.on(KillMonster, this.onKillEnemy, this);
        GlobalMessage.on(SelectTco, this.onSelectTco, this);
    }

    protected onDisable(): void {
        GlobalMessage.offAll(this);
    }

    public upgrade(): void {
        this.exp = 0;
        if (this.currIndex == ConfigManager.characterLevelConfig.length - 1) {
            return;
        }
        this.currIndex++;
        this.currIndex = Math.min(this.currIndex, ConfigManager.characterLevelConfig.length - 1);

        var hero = GamaManager.inst.mainCharacter;
        var prop = new ChaProperty;
        prop.hp = this.currentModel.hp;
        prop.attack = this.currentModel.attack;
        prop.cirt = this.currentModel.cirt;
        prop.catapultForce = this.currentModel.catapultForce;
        prop.range = this.currentModel.range;
        prop.moveSpeed = this.currentModel.moveSpeed;
        hero.InitProperty(prop);

        var options = this.getSelectOptions();
        if (options.length != 0) {
            GlobalMessage.emit(StartSelectTco.create(options));
        }
    }

    private randomSelectTcoOptions(): string[] {
        var selected: string[] = [];
        for (var key in this.tcoLevelMap) {
            var level = this.getTcoLevel(key);
            var info = ConfigManager.getTcoInfoById(key);
            if (level < info.maxLevel) {
                selected.push(key);
            }
        }
        var result: string[] = [];
        if (selected.length > 0) {
            var index = Math.floor(Math.random() * selected.length);
            index = Math.min(index, selected.length - 1);
            result.push(selected[index]);
        }
        return result;
    }

    private randomAllTcoOptions(exclude: string[], count: number): string[] {
        var list: { key: string, weight: number }[] = [];
        var tcoConfig = ConfigManager.tcoConfig;
        for (var i = 0; i < tcoConfig.length; i++) {
            var item = tcoConfig[i];
            var level = this.getTcoLevel(item.id);
            if (level < item.maxLevel && !exclude.includes(item.id)) {
                list.push({ key: item.id, weight: item.weight });
            }
        }

        var result: string[] = [];
        var weigthsRandom = new WeigthsRandom();
        weigthsRandom.weights = list;
        count = Math.min(count, list.length);
        for (var i = 0; i < count; i++) {
            var id = weigthsRandom.Random();
            weigthsRandom.Delete(id);
            result.push(id);
        }
        return result;
    }

    public getSelectOptions(): string[] {
        var list1 = [];
        if (User.getGameTime() % ConfigManager.gameConfig.time == 0) {
            list1 = this.randomSelectTcoOptions();
        }
        var list2 = this.randomAllTcoOptions(list1, list1.length != 0 ? 2 : 3);
        var arr = [...list1, ...list2];
        return arr;
    }

    private setTcoLevel(id: string, level: number = 1): void {
        this.tcoLevelMap[id] = level;
    }

    public getTcoLevel(id: string): number {
        return this.tcoLevelMap[id] ?? 0;
    }

    private onKillEnemy(data: KillMonster): void {
        this.exp += 100;
        if (this.exp >= this.currentModel.exp) {
            this.upgrade();
        }

        var hero = GamaManager.inst.mainCharacter as Hero;
        if (hero.isSuper) {
            if (this.batterTime < GamaManager.inst.totalTime) {
                this.batterCount = 0;
            }
            this.batterCount++;
            this.batterTime = GamaManager.inst.totalTime + 3;
            GlobalMessage.emit(new BatterShow);
    
            if (this.batterGoodTime < GamaManager.inst.totalTime) {
                this.batterGoodCount = 0;
            }
    
            this.batterGoodCount++;
            if (this.batterGoodCount >= 3 || data.character.config.type > 1) {
                this.batterGoodCount = 0;
                EffectManager.inst.addEffect("Effect_batter_good", this.node, cc.v2(data.character.node.x, data.character.node.y));
            }
            this.batterGoodTime = GamaManager.inst.totalTime + 0.2;
        }
    }

    public getExpProgress(): number {
        return Math.min(1, this.exp / this.currentModel.exp);
    }

    private addTcoBuff(info: TcoInfo, level: number): void {
        var hero = GamaManager.inst.mainCharacter;
        this.setTcoLevel(info.id, level);
        var buffIds = ConfigManager.getTcoBuffList(info.id, level);
        for (var i = 0; i < buffIds.length; i++) {
            hero.addBuff(buffIds[i]);
        }
    }

    private removeTcoBuff(info: TcoInfo, level: number): void {
        var hero = GamaManager.inst.mainCharacter;
        var buffIds = ConfigManager.getTcoBuffList(info.id, level);
        for (var i = 0; i < buffIds.length; i++) {
            hero.removeBuff(buffIds[i]);
        }
    }

    private onSelectTco(data: SelectTco): void {
        var info = ConfigManager.getTcoInfoById(data.id);
        var level = this.getTcoLevel(data.id);
        if (level == 0) {
            this.addTcoBuff(info, 1);
        } else {
            this.removeTcoBuff(info, level);
            this.addTcoBuff(info, level + 1);
        }

        TTAPI.reportAnalytics("selectSkill", {
            id: data.id,
        });
    }
}