    public velocity: cc.Vec2 = new cc.Vec2;

    private totalTime: number = 0;

    public duration: number = 0;

    get finished(): boolean {
        return false;
    }

    public catapult(velocity: cc.Vec2, duration: number) {
        this.velocity.set(velocity);
        this.duration = duration;
        this.totalTime = duration;
    }

    public updateMovement(time: number): cc.Vec2 {
        if (this.duration <= 0) {
            return cc.Vec2.ZERO;
        }

        if (time >= this.duration) {
            this.duration = 0;
        } else {
            this.duration -= time;
        }

        var movement = this.character.movement;
        if (movement.hit) {
            if (movement.hitX) {
                this.velocity.x *= -1;
            }
            if (movement.hitY) {
                this.velocity.y *= -1;
            }
            AudioManager.inst.playSFX("Sound/hitWall");
            EffectManager.inst.addEffect("Effect_HitWall", this.node, cc.v2(movement.hitPoint.x * 1.1, movement.hitPoint.y));
        }

        if (this.totalTime <= 0) {
            return this.velocity
        } else {
            var value = this.duration / this.totalTime;
            value = Math.max(0, 1 - value);
            return this.velocity.div(this.totalTime);
        }
    } 
