import RYAD from "./RYAD";
import RYSTAT from "./RYSTAT";

export default class RYSDK {
    public static get Instance_csryw() {
        if (null == RYSDK._instance_csryw) {
            console.error("请先调用 RYSDK.init() 对RYSDK进行初始化！！！！！！");
        }
        return RYSDK._instance_csryw;
    }
    protected static _instance_csryw: RYSDK = null;

    public static init_csryw(code: string) {
        if (null != RYSDK._instance_csryw) {
            console.error("请不要重复初始化 RYSDK ！！！！！！");
            return;
        }
        console.log("初始化 RYSDK ！！！！！！")
        let ins = new RYSDK();
        ins._ad_csryw = new RYAD();
        ins._stat_csryw = new RYSTAT();
        RYSDK._instance_csryw = ins;
        if (null != window["rysdk"]) {
            window["rysdk"].ry_init();
            RYSDK._instance_csryw._rysdk_csryw = window["rysdk"];
            RYSDK.Instance_csryw.STAT_csryw.reportInit_csryw();
            RYSDK.Instance_csryw.STAT_csryw.reportLogin_csryw();
        }
    }

    private constructor() {

    }

    public get AD_csryw() {
        return this._ad_csryw;
    }
    private _ad_csryw: RYAD = null;
    public get STAT_csryw() {
        return this._stat_csryw;
    }
    private _stat_csryw: RYSTAT = null;

    public get rysdk_csryw() {
        return this._rysdk_csryw
    }
    protected _rysdk_csryw: any = null;
}