import Character from "../character/Character";

export class MonsterDead {
    static key = "MonsterDead";
}

export class KillMonster {
    static key = "KillMonster";
    
    character: Character;
    
    static create(character: Character) {
        var data = new KillMonster;
        data.character = character;
        return data;
    }
}

