import Gizmos from "../../tools/Gizmos";
import AudioManager from "../manager/AudioManager";
import CollisitionManager from "../manager/CollisitionManager";
import GamaManager from "../manager/GamaManager";
import { IActionConsumer } from "../unit/action/IActionConsumer";
import { BehaviorFlag } from "./Behavior";
import Character from "./Character";
import BallMove from "./movement/BallMove";
import DistanceMove from "./movement/DistanceMove";

const { ccclass, property, executionOrder } = cc._decorator;

@ccclass
export default class CharacterActionConsumer extends cc.Component implements IActionConsumer {
    public character: Character

    @property(sp.Skeleton)
    public skeleton: sp.Skeleton = null;

    protected onLoad(): void {
        this.character = this.getComponent(Character);
    }

    protected update(dt: number): void {
        var pause = GamaManager.inst.isPause;
        if (this.skeleton.paused != pause) {
            this.skeleton.paused = pause;
        }
    }

    public onConsumeAction(action: string, args: any[]): void {
        var func = this[action] as Function;
        if (func == null) {
            console.error("no find action, " + action);
            return;
        }
        func.apply(this, args);
    }

    protected playAnim(animName: string, loop: boolean = false): void {
        if (this.skeleton.animation == animName) {
            return;
        }
        this.skeleton.setAnimation(0, animName, loop)
    }

    protected changeFlag(move: boolean = true, rotate: boolean = true): void {
        if (move == false) {
            this.character.actionControlFlag &= ~BehaviorFlag.move;
        } else {
            this.character.actionControlFlag |= BehaviorFlag.move;
        }
        if (rotate == false) {
            this.character.actionControlFlag &= ~BehaviorFlag.rotate;
        } else {
            this.character.actionControlFlag |= BehaviorFlag.rotate;
        }
    }

    protected attack(hitBox: string, group: string): void {
        var collision = this.character.getHitBox(hitBox);
        if (GamaManager.inst.debugHitBox) {
            collision.draw();
        }
        group = this.character.group == "enemy" ? "hero" : "enemy";
        var nodes = CollisitionManager.inst.collisition(collision, group);
        for (var i = 0; i < nodes.length; i++) {
            GamaManager.inst.doDamage(this.character.node, nodes[i]);
        }
    }

    private shoot(bullet: string, launcher: string): void {
        var target = GamaManager.inst.mainCharacter.node;
        var direction = new cc.Vec2(target.x - this.character.node.x, target.y - this.character.node.y);
        if (target == this.character.node) {
            direction.set(cc.Vec2.RIGHT);
        }

        var position: cc.Vec2;
        if (launcher == null) {
            position = cc.v2(this.node.x, this.node.y);
        } else {
            var node = this.character.node.getChildByName(launcher);
            position = node.convertToWorldSpaceAR(cc.Vec2.ZERO);
        }

        direction.normalizeSelf();
        GamaManager.inst.createBullet(this.character.node, bullet, position, direction);
    }

    private shootByCount(bullet: string, launcher: string, count: number, angle: number): void {
        var target = GamaManager.inst.mainCharacter.node;
        
        var position: cc.Vec2;
        if (launcher == null) {
            position = cc.v2(this.node.x, this.node.y);
        } else {
            var node = this.character.node.getChildByName(launcher);
            position = node.convertToWorldSpaceAR(cc.Vec2.ZERO);
        }
        var direction = cc.v2(target.x - position.x, target.y - position.y);
        direction.normalizeSelf();

        var step = angle / count;
        direction.rotateSelf(-(angle - step) / 2);
        for (var i = 0; i < count; i++) {
            var dir = direction.rotate(i * step);
            GamaManager.inst.createBullet(this.character.node, bullet, position, dir);
        }
    }

    private explode(explodeHitBox: string, force: number = 1500): void {
        var collision = this.character.getHitBox(explodeHitBox);
        if (GamaManager.inst.debugHitBox) {
            collision.draw();
        }
        var group = this.character.group == "enemy" ? "hero" : "enemy";
        var nodes = CollisitionManager.inst.collisition(collision, group);
        for (var i = 0; i < nodes.length; i++) {
            var ballMove = nodes[i].getComponent(BallMove);
            if (ballMove) {
                var direction = cc.v2(nodes[i].x - this.character.node.x, nodes[i].y - this.character.node.y);
                ballMove.catapult(direction.normalizeSelf(), force);
            }
        }
    }

    private attackToTarget(hitBox: string, group: string): void {
        var collision = this.character.getHitBox(hitBox);
        if (GamaManager.inst.debugHitBox) {
            collision.draw();
        }

        var target = GamaManager.inst.mainCharacter.node;
        var direction = new cc.Vec2(target.x - this.character.node.x, target.y - this.character.node.y);
        if (target == this.character.node) {
            direction.set(cc.v2(1,1).normalize());
        }
        direction.normalizeSelf();
        var angle = cc.misc.radiansToDegrees(Math.atan2(direction.x, direction.y)); 
        group = this.character.group == "enemy" ? "hero" : "enemy";
        collision.node.parent.angle = -angle;
        if (this.character.directionX < 0) {
            collision.node.parent.angle *= -1;
        }
        var nodes = CollisitionManager.inst.collisition(collision, group);
        for (var i = 0; i < nodes.length; i++) {
            GamaManager.inst.doDamage(this.character.node, nodes[i]);
        }
    }

    private springToHero(): void {
        var target = GamaManager.inst.mainCharacter.node;
        var direction = new cc.Vec2(target.x - this.character.node.x, target.y - this.character.node.y);
        direction.normalizeSelf();
        direction.mulSelf(300);
        this.character.addMoveProiver(new DistanceMove(direction, 0.3));
    }

    private playSound(sound: string): void {
        AudioManager.inst.playSFX("Sound/" + sound);
    }
}