import BuffObj from "./BuffObj";

export default class BuffWitchCd extends BuffObj {
    cdTime: number = 0;
    isReady: boolean = false;
    cdCallback: Function = null;

    private _cdTimer: number;
    protected get cdTimer(): number {
        return this._cdTimer;
    }
    protected set cdTimer(value: number) {
        if (this._cdTimer == value) {
            return;
        }
        this._cdTimer = value;
        this.onCdTimerChanged();
    }

    public onAdd(count: number): void {
        if (count > 0) {
            this.isReady = true;
        }
    }

    protected getCd(): number {
        var cd = this.buffParam["cd"];
        return cd ?? 0;
    }

    protected onCdOver() {
        this.isReady = true;
    }

    private startCd(time: number, cdCallback: Function) {
        this.cdTimer = time;
        this.cdTimer <= 0 ? cdCallback && cdCallback.apply(this) : this.cdCallback = cdCallback;
    }

    protected resetCd(): void {
        this.isReady = false;
        this.cdTime = this.getCd();
        this.startCd(this.cdTime, this.onCdOver);
    }

    private onCdTimerChanged() {

    }

    public onTick(dt: number): void {
        if (this.cdTimer && this.cdTimer > 0) {
            this.cdTimer -= dt;
            this.cdTimer <= 0 && this.cdCallback && this.cdCallback.apply(this);
        }
    }
}