import TTAPI from "../../../Platform/tt/TTAPI";
import PhysicalPowerMgr from "../../Mgr/PhysicalPowerMgr";
import DialogUIBase from "./DialogUIBase";

/*
 * @Author: YeeChan
 * @Date: 2021-12-22 18:54:23
 * @Description: 无限体力弹窗
 */
const { ccclass, property } = cc._decorator;

@ccclass
export default class DialogAddPowerAll extends DialogUIBase {
    private label: cc.Label = null;

    protected initView_csryw(): void {
        super.initView_csryw();

        //放弃按钮
        this.onClick(this.childNode("butLeft", this.showNode), () => {
            this.closeDialog();
        }, this)

        let butRight = this.childNode("butRight", this.showNode);
        this.label = this.child(butRight, "label", cc.Label);
        this.onClick(butRight, () => {
            TTAPI.showRewardedVideoAd_csryw("无线体力", (finish) => {
                if (finish) {
                    let num = PhysicalPowerMgr.getPhyVideoAllNum();
                    num = num + 1;
                    if (num >= 3) {
                        num = 0;
                        PhysicalPowerMgr.setPhyVideoAllNum(num);

                        PhysicalPowerMgr.addNewPhysicalAll();//增加一个24小时无限体力
                        this.closeDialog(true);

                    } else {
                        PhysicalPowerMgr.setPhyVideoAllNum(num);
                        this.updateVideoNum();
                    }
                }
            }, () => { })
        }, this)

        this.updateVideoNum();
    }


    /**
     * 更新无限体力看视频的次数
     */
    private updateVideoNum() {
        this.label.string = PhysicalPowerMgr.getPhyVideoAllNum() + "/3";
    }


}
