import DialogMgr from "../../../FrameWork/Mgr/DialogMgr";
import GameMgr from "../../../FrameWork/Mgr/GameMgr";
import User from "../../../FrameWork/User/User";
import TTAPI from "../../../Platform/tt/TTAPI";
import ConfigManager from "../../ConfigManager";
import AssetManager from "../../common/AssetManager";
import FixComponent from "../../common/fixcomponent/FixComponent";
import FixManager from "../../common/fixcomponent/FixManager";
import { GlobalMessage } from "../../tools/GlobalMessage";
import Utils from "../../tools/Utils";
import BulletObj from "../bullet/BulletObj";
import Character from "../character/Character";
import Hero from "../character/Hero";
import { ActionTable } from "../designer/table/ActionTable";
import { BulletTable } from "../designer/table/BulletTable";
import { BossShow, DropMoney, GameGuideEnd, GameGuideStart, GameStart, HeroDead, HeroSuperEnd, HeroSuperStart, KillMonster, MonsterDead, Settlement, TryResurrection } from "../message/Messages";
import ChaProperty from "../structs/battle/ChaProperty";
import DamageInfo, { DamageResult } from "../structs/battle/DamageInfo";
import AudioManager from "./AudioManager";
import CollisitionManager from "./CollisitionManager";
import EffectManager from "./EffectManager";
import HeroManager from "./HeroManager";

const { ccclass, property } = cc._decorator;

export enum GameState {
    None,
    Guide,
    Run,
    Resurrection,
    Settlement,
}

@ccclass
export default class GamaManager extends FixComponent {
    private static _inst: GamaManager;

    public static get inst(): GamaManager {
        return this._inst;
    }

    public debug: boolean = true;
    public debugHitBox: boolean = true;

    @property(cc.Node)
    public debugNode: cc.Node = null;

    @property(cc.Vec2)
    public mapSize: cc.Vec2 = new cc.Vec2;

    public get minX(): number {
        return -this.mapSize.x / 2;
    }

    public get minY(): number {
        return -this.mapSize.y / 2;
    }

    public get maxX(): number {
        return this.mapSize.x / 2;
    }

    public get maxY(): number {
        return this.mapSize.y / 2;
    }

    @property(cc.Node)
    public characterLayer: cc.Node = null;

    @property(cc.Node)
    public bulletLayer: cc.Node = null;

    @property(cc.Node)
    public effectLayer: cc.Node = null;

    @property(cc.Node)
    public numberLayer: cc.Node = null;

    public mainCharacter: Character;

    public bossCharacters: Character[] = [];

    public state: GameState = GameState.None;

    public isPause: boolean = false;
    public win: boolean = false;

    public totalTime: number = 0;

    private _stage: number = 0;
    public get stage(): number {
        return this._stage;
    }
    public set stage(value: number) {
        if (this._stage == value) {
            return;
        }
        this._stage = value;
        this.onUpdateStage();
    }

    private damageInfos: DamageInfo[] = [];

    private isResurrected: boolean = false;

    private waitStartRecordTimer: number = -1;

    private clipIndexList: number[] = [];

    public currentMoney: number = 0;

    protected onLoad(): void {
        GamaManager._inst = this;
        FixManager.inst.pause = false;
    }

    protected start(): void {
        var offset = 100;
        var canvas = cc.director.getScene().getChildByName("Canvas").getComponent(cc.Canvas);
        this.mapSize.x = 750 - offset//canvas.node.width;
        this.mapSize.y = 1334 - offset//canvas.node.height;

        this.mainCharacter = this.createCharacter("hero", cc.v2(0, -200)).getComponent(Character);
        // this.mainCharacter.addBuff("AddAttack10");
        // this.mainCharacter.addBuff("heroSupperCirt");
        // this.mainCharacter.addBuff("AddCritDamageMulti");

        // this.createCharacter("caomei", cc.v2(0, 0));
        this.stage = 0;
        this.swicthBattleBgm();

        this.debugNode.active = true;//cc.sys.isBrowser || this.debug;

        TTAPI.startRecord_csryw(60 * 6);
    }

    protected onEnable(): void {
        super.onEnable();
        GlobalMessage.on(HeroDead, this.onHeroDead, this);
        GlobalMessage.on(KillMonster, this.onKillMonster, this);
        GlobalMessage.on(GameStart, this.onGameStart, this);
        GlobalMessage.on(HeroSuperStart, this.onHeroSuperChange, this);
        GlobalMessage.on(HeroSuperEnd, this.onHeroSuperChange, this);
    }

    protected onDisable(): void {
        super.onDisable();
        GlobalMessage.offAll(this);
    }

    private onGameStart(): void {
        this.state = GameState.Guide;
        this.createCharacter("xigua", cc.v2(0, 200));
        GlobalMessage.emit(new GameGuideStart);
        this.schedule(() => { GameMgr.getInstance_csryw().saveGameData_csryw(); }, 0.5, 99999);
    }

    public fixMovementPosition(position: cc.Vec2, fixPosition: cc.Vec2): boolean {
        var mapSize = this.mapSize;
        var width = mapSize.x / 2;
        var height = mapSize.y / 2;
        var hit = false;
        fixPosition.set(position);
        if (fixPosition.x < -width) {
            fixPosition.x = -width;
            hit = true;
        }
        if (fixPosition.x > width) {
            fixPosition.x = width;
            hit = true;
        }
        if (fixPosition.y < -height) {
            fixPosition.y = -height;
            hit = true;
        }
        if (fixPosition.y > height) {
            fixPosition.y = height;
            hit = true;
        }
        return hit;
    }

    private CreateFromPrefab(prefabPath: string, position: cc.Vec2, parent: cc.Node): cc.Node {
        var prefab = AssetManager.getPrefab(prefabPath);
        var node = cc.instantiate(prefab);
        node.parent = parent;
        node.setPosition(position);
        return node;
    }

    public createCharacter(name: string, pos: cc.Vec2): cc.Node {
        var info = ConfigManager.characterConfig[name]
        if (info == null) {
            console.error("no find character data: ", name);
            return null;
        }
        var node = this.CreateFromPrefab(info.prefab, pos, this.characterLayer);
        var character = node.getComponent(Character);
        if (character) {
            var property = new ChaProperty();
            property.hp = info.hp;
            property.attack = info.attack;
            property.range = info.range;
            property.catapultForce = info.catapultForce;
            property.moveSpeed = info.moveSpeed;
            property.cirt = info.cirt;
            character.InitProperty(property);
            for (var i = 0; i < info.skills.length; i++) {
                // var skillModel = DesingerTables.Skill.data[info.skills[i]];
                // skillModel && chaState.LearnSkill(skillModel, 1);
            }
            character.group = info.group;
            character.setActions(ActionTable[info.actions]);
            character.config = info;
            CollisitionManager.inst.addBody(character);

            if (this.stage != 2 && info.type == 2) {
                this.stage = 1;
                this.bossCharacters.push(character);
                GlobalMessage.emit(BossShow.create(character));
            } else if (info.type == 3) {
                this.stage = 2;
                this.bossCharacters.push(character);
                GlobalMessage.emit(BossShow.create(character));
            }
        } else {
            node.destroy();
            return null;
        }
        return node;
    }

    public pause(): void {
        this.isPause = true;
        FixManager.inst.pause = true;
    }

    public resume(): void {
        this.isPause = false;
        FixManager.inst.pause = false
    }

    public doDamage(attacker: cc.Node, target: cc.Node, tags: string[] = []): void {
        this.damageInfos.push(new DamageInfo(attacker, target, tags));
    }

    private checkCirtHappen(unit: Character, critMulti: number): boolean {
        var crit = unit.getProperty("cirt");
        if ((crit *= critMulti) <= .01) return false;
        if (crit >= 100) return true;
        return Utils.random(100) < crit * 100;
    }

    private createDamages(info: DamageInfo, from: Character, to: Character, isCrit: boolean): number {
        var attack = from.getProperty("attack");
        var tagMulti = 0;
        for (var i = 0; i < info.tags.length; i++) {
            tagMulti += from.getProperty(info.tags[i] + "DamageMulti");
        }
        var damage = attack * (tagMulti == 0 ? 1 : tagMulti)/* * config.attackMulti*/;
        if (isCrit) {
            var critDamageMulti = from.getProperty("critDamageMulti");
            var critDamageOffset = from.getProperty("critDamageOffset");
            damage *= critDamageMulti;
            damage += critDamageOffset;
        }
        damage = damage * (1 + Math.random() * 0.05);
        return Math.floor(damage);
    }

    private dealWithDamage(dInfo: DamageInfo): void {
        if (!dInfo.defender) return;
        var defenderCharacter = dInfo.defender.getComponent(Character);
        if (!defenderCharacter) return;
        if (defenderCharacter.isDead == true) 
            return;

        var attackerCharacter: Character = null;
        if (dInfo.attacker) {
            attackerCharacter = dInfo.attacker.getComponent(Character);
            if (attackerCharacter == defenderCharacter) {
                return;
            }

            var isCirt = this.checkCirtHappen(attackerCharacter, 1);
            var damage = this.createDamages(dInfo, attackerCharacter, defenderCharacter, isCirt);
            var result = new DamageResult(damage, isCirt, dInfo.direction);
            defenderCharacter.beHurt(result);
            attackerCharacter.hit(defenderCharacter);
        }
    }

    public createBullet(owner: cc.Node, id: string, position: cc.Vec2, direction: cc.Vec2): void {
        var model = BulletTable[id];
        if (model == null) {
            console.error("no find bullet, ", id);
        }

        var prefab = AssetManager.getPrefab(model.prefab);
        var node = cc.instantiate(prefab);
        node.parent = this.bulletLayer;
        node.active = true;
        node.setPosition(position);

        var bulletObj = node.getComponent(BulletObj);
        bulletObj.init(owner, direction, model);
    }

    public fixUpdate(): void {
        var dt = FixManager.fixedDeltaTime;
        if (this.state == GameState.Run) {
            this.totalTime += dt;
        }

        while (this.damageInfos.length) {
            var info = this.damageInfos.shift();
            this.dealWithDamage(info);
        }

        var old = this.waitStartRecordTimer;
        this.waitStartRecordTimer -= dt;
        if (old > 0 && this.waitStartRecordTimer <= 0) {
            this.clipIndexList = [];
            TTAPI.startRecord_csryw();
        }
    }

    private settlement(win: boolean, now?: boolean): void {
        TTAPI.stopRecord_csryw();
        this.scheduleOnce(() => {
            this.pause();
            this.win = win;
            this.state = GameState.Settlement;
            GlobalMessage.emit(new Settlement);
        }, now ? 0 : 3);
    }

    private tryResurrection(): void {
        TTAPI.stopRecord_csryw();
        this.scheduleOnce(() => {
            this.pause();
            this.state = GameState.Resurrection;
            GlobalMessage.emit(new TryResurrection);
        }, 3);
    }

    public resurrect(needResurrect: boolean): void {
        if (needResurrect) {
            this.isResurrected = true;
            this.resume();
            this.state = GameState.Run;
            (this.mainCharacter as Hero).resurrect();
            this.mainCharacter.node.setPosition(cc.Vec2.ZERO);
        } else {
            this.settlement(false, true);
        }
    }

    private onKillMonster(data: KillMonster): void {
        if (data.character.config.type == 2 && this.stage == 1) {
            this.stage = 0;
        }

        if (this.state == GameState.Guide) {
            this.state = GameState.Run;
            GlobalMessage.emit(new GameGuideEnd);
        }

        var index = this.bossCharacters.findIndex(item => item == data.character);
        if (index != -1) {
            this.bossCharacters.splice(index, 1);
        }

        if (data.character.config.type == 3) {
            this.settlement(true);
            TTAPI.reportAnalytics("heroWin", {
                time: Math.floor(this.totalTime / 60),
                level: HeroManager.inst.level,
            });
        }

        var dropMoney = data.character.config.dropMoney;
        if (dropMoney > 0) {
            this.addDropMoney(dropMoney, cc.v2(data.character.node.x, data.character.node.y));
        }

        var monsterId = data.character.config.prefab;
        User.addKillMonster(monsterId);
        var weaponConfigs = ConfigManager.weaponConfig;
        for (var i = 0; i < weaponConfigs.length; i++) {
            var config = weaponConfigs[i];
            if (config.type == 1 && config.monster == monsterId &&
                User.getKillMonsterCount(monsterId) >= config.monsterCount && !User.checkHasWeapon(config.id)) {
                User.addWeapon(config.id);
                DialogMgr.openToast("解锁：" + config.name + "武器", "view");
            }
        }
    }

    private addDropMoney(dropMoney: number, position: cc.Vec2 = cc.Vec2.ZERO.clone()): void {
        this.currentMoney += dropMoney;
        User.addMoney_csryw(dropMoney);
        GlobalMessage.emit(DropMoney.create(dropMoney, position));
    }

    private onHeroDead(): void {
        console.log("结束录屏");
        if (!this.isResurrected) {
            this.tryResurrection();
        } else {
            this.settlement(false);
            TTAPI.reportAnalytics("heroFail", {
                time: Math.floor(this.totalTime / 60),
                level: HeroManager.inst.level,
            });
        }
    }

    private onUpdateStage(): void {
        this.swicthBattleBgm();
    }

    private swicthBattleBgm(): void {
        var soundName = "battleBgm";
        switch (this.stage) {
            case 2:
                soundName = "boss战斗环境音2";
                break;
        }
        AudioManager.inst.playBGM("Sound/" + soundName);
    }

    private onHeroSuperChange(data: HeroSuperStart | HeroSuperEnd): void {
        // TTAPI.stopRecord_csryw();
        // var self = this;
        // if (data instanceof HeroSuperStart) {
        //     TTAPI.stopRecord_csryw(() => {
        //         self.tryStartRecord();
        //     });
        //     GlobalMessage.off(HeroSuperStart, this.onHeroSuperChange, this);
        //     GlobalMessage.off(HeroSuperEnd, this.onHeroSuperChange, this);
        // }
    }

    public tryStartRecord(): void {
        this.waitStartRecordTimer = 1;
    }

    private onTestAttack(): void {
        this.mainCharacter.doAction("Attack");
    }

    private onTestHurt(): void {
        this.mainCharacter.beHurt(new DamageResult(50, false, cc.Vec2.RIGHT.clone()));
    }

    private onTestRotate(): void {
        this.mainCharacter.setDirection(Math.sign(this.mainCharacter.directionX) * -1);
    }

    private onTestShoot(): void {
        this.mainCharacter.doAction("Shoot");
    }

    private onAddTime(): void {
        this.totalTime += 60;
    }

    private onInvincible(): void {
        // for (var i = 0; i < 50; i++) {
        //     EffectManager.inst.addEffect("CirtHurtPopup", this.node, cc.v2(Math.random() * 100, 0), {
        //         damage: i + 1,
        //     }, GamaManager.inst.numberLayer);
        // }
        (this.mainCharacter as Hero).invincible = !(this.mainCharacter as Hero).invincible;
    }

    private onBigSkill(): void {
        (this.mainCharacter as Hero).useBigSkill();
    }

    private _clickCount: number = 0;
    private onClickHead(): void {
        if (!cc.sys.isBrowser && !this.debug) {
            return;
        }
        this._clickCount++;
        if ((this._clickCount % 3) == 0) {
            this._clickCount = 0;
            this.debugNode.active = !this.debugNode.active;
        }
    }

    private onClickAddMoney(): void {
        this.addDropMoney(5000);
    }

    private onClickAddLevel(): void {
        HeroManager.inst.upgrade();
    }

    private onClickUnlockWeapon(): void {
        var weaponConfigs = ConfigManager.weaponConfig;
        weaponConfigs.forEach(item => {
            User.addWeapon(item.id);
        });
    }
}
