/*
 * @Descripttion: 循环滚动容器  只支持竖向滚动或横向滚动，
 * @version: 1.0.0
 * @Author: YeeChan
 * @Date: 2020-07-17 10:32:52
 */

import FMTouchMaskView, { SlideDirection, FMTouchEvent } from "./FMTouchMaskView";
import { FMListener, callFM_csryw, handleFM_csryw } from "../Interface/FMInterface";
import Utilit from "../Util/Utilit";
import FMItemLayout from "./FMItemLayout";

const { ccclass, property, requireComponent, disallowMultiple, menu } = cc._decorator;

@ccclass
// @requireComponent(cc.ScrollView)
@disallowMultiple() //防止多个相同类型（或子类型）的组件被添加到同一个节点
//@menu('FM组件/FMScrollViewLoop')
export default class FMScrollViewLoop extends FMTouchMaskView {

    //是否自动移动
    @property()
    private _isAutoMoveType: boolean = true;
    @property({ tooltip: "自动滚动", type: cc.Boolean })
    set isAutoMoveType(val: boolean) {
        this._isAutoMoveType = val;
    }
    get isAutoMoveType() { return this._isAutoMoveType; }


    //--------------------------------------
    //广告标签
    protected ad_tag_csryw: any;
    //更新的回调
    protected fmListenerUpdateData_csryw: FMListener;

    //layout list
    protected arrayAliveListLayout_csryw: FMItemLayout[] = []
    //自由的节点
    protected arrayFreeListLayout_csryw: FMItemLayout[] = []

    //一个屏幕中的layout数量
    private viewLayoutNum_csryw: number = 0;
    //超出的个数
    private viewLayoutGapNum_csryw: number = 3;
    //总共的个数
    private viewLayoutSum_csryw: number = 0;


    //初始的下标
    private startIndexItem_csryw: number = 0;
    //是否可以更新item相关
    private isCanUpdateItems_csryw: boolean = false;
    //点击了 触发停止
    private isTouchStop_csryw: boolean = false;
    //外部需要控制的是否可以update
    private _isCanScrollViewLoopUpdate_csryw: boolean = true;


    protected isAutoMoveWayLeftUp_csryw: boolean = true;//是否自动移动往左或上 还是右或下
    //自动移动后等待的时间
    protected autoMoveWaitTime_csryw: number = 60;
    //自动移动的一次的距离格子数量 0不启用 
    protected autoMoveWaitNum_csryw: number = 1;
    //移动的数据
    protected autoMoveSpeed_csryw: number = 3;
    //

    //当次移动的距离大小
    private _nextMoveLength_csryw: number = 0;
    //当下需要等待的时间
    private _waitTimeNum_csryw: number = 0;
    private _moveState_csryw: number = 0;//0正常移动 1等待



    protected _init_csryw(): boolean {
        if (super._init_csryw()) {
            if (this._slideDirection == SlideDirection.HORIZONTAL) {//水平
                this.viewLayoutNum_csryw = Math.ceil(this.node.width / (this.itemPrefabWidth_csryw));
            } else if (this._slideDirection == SlideDirection.VERTICAL) {//垂直
                this.viewLayoutNum_csryw = Math.ceil(this.node.width / (this.itemPrefabHeight_csryw));
            }
            //创建的个数 
            this.viewLayoutSum_csryw = this.viewLayoutNum_csryw + this.viewLayoutGapNum_csryw * 2

            let pos = 0;
            for (let index = 0; index < this.viewLayoutSum_csryw; index++) {
                let layoutScript = this.cloneLayout_csryw();
                let layout = layoutScript.node;
                if (this._slideDirection == SlideDirection.HORIZONTAL) {//水平
                    layout.x = pos;
                    layout.y = 0;
                    pos = pos + layout.width;
                } else if (this._slideDirection == SlideDirection.VERTICAL) {//垂直
                    layout.x = 0;
                    layout.y = pos;
                    pos = pos - layout.height
                }
                this.arrayAliveListLayout_csryw.push(layoutScript);
            }
            this.isCanUpdateItems_csryw = true;
            return true;
        }
        return false;
    }

    /**
     * 外部调用
     * @param _listener 
     */
    public setFMListenerUpdateItem_csryw(_listener: FMListener) {
        this.fmListenerUpdateData_csryw = _listener;
    }



    /**
     * 设置自动移动的数据
     * @param way 方向
     * @param moveWaitNum 移动的格子 0持续
     * @param waitTime 移动的格子后停顿的时间 s
     * @param speed 速度
     */
    public setAutoData_csryw(way: number, moveWaitNum?: number, waitTime?: number, speed?: number, bgColor?: string) {
        this.isAutoMoveWayLeftUp_csryw = way == 0 ? true : false;
        this.autoMoveWaitNum_csryw = moveWaitNum ? Math.abs(moveWaitNum) : 0;
        this.autoMoveWaitTime_csryw = waitTime ? Math.abs(waitTime) * 60 : 60;
        this.autoMoveSpeed_csryw = speed ? Math.abs(speed) : 3;
        if (bgColor) {
            this.node.color = Utilit.colorHex2Rgb_csryw(bgColor);
        }

    }


    /**
     * 是否点击了
     */
    public isTouchStopView_csryw() {
        return this.isTouchStop_csryw;
    }

    /**
     * 外部控制是否update
     */
    public setCanScrollViewLoopUpdate_csryw(_update: boolean) {
        this._isCanScrollViewLoopUpdate_csryw = _update;
    }

    /**
     * 设置广告的自定义标签
     * @param tag 
     */
    public setAdTag_csryw(tag: any) {
        this.ad_tag_csryw = tag;
        for (let index = 0; index < this.arrayAliveListLayout_csryw.length; index++) {
            const element = this.arrayAliveListLayout_csryw[index];
            element.setAdTag_csryw(this.ad_tag_csryw);
        }
        for (let index = 0; index < this.arrayFreeListLayout_csryw.length; index++) {
            const element = this.arrayFreeListLayout_csryw[index];
            element.setAdTag_csryw(this.ad_tag_csryw);
        }
    }




    //外部初始数据
    public initUpdateItems_csryw() {
        this._init_csryw();
        this.startIndexItem_csryw = 0;
        if (this._slideDirection == SlideDirection.HORIZONTAL) {//水平
            let pos = - this.itemPrefabWidth_csryw * 3;
            for (let index = 0; index < this.arrayAliveListLayout_csryw.length; index++) {
                const element = this.arrayAliveListLayout_csryw[index];
                element.setPointX_csryw(pos);
                element.setItemIndex_csryw(this.startIndexItem_csryw + index);
                pos = pos + this.itemPrefabWidth_csryw
            }
        } else { //垂直
            let pos = this.itemPrefabHeight_csryw * 3;
            for (let index = 0; index < this.arrayAliveListLayout_csryw.length; index++) {
                const element = this.arrayAliveListLayout_csryw[index];
                element.setPointY_csryw(pos);
                element.setItemIndex_csryw(this.startIndexItem_csryw + index);
                pos = pos - this.itemPrefabHeight_csryw
            }
        }
        this._updateNextLengthTime_csryw();
    }

    protected update(dt: number): void {
        if (this.isCanUpdateItems_csryw && this._isCanScrollViewLoopUpdate_csryw) {
            this.updateScrollingItems_csryw();
            if (this.isAutoMoveType == true) {//判断类型是自动移动类型
                this._updateAutoMove_csryw();
            }
        }
    }

    /**
     * 更新子节点的数据内容
     * @param index  第几个大的
     * @param childIndex 第几个小的
     */
    protected listenerUpdateInitItem_csryw(sum: number) {
        if (this.fmListenerUpdateData_csryw) {
            let data = callFM_csryw(this.fmListenerUpdateData_csryw, sum);
            return data;
        }
    }

    /**
     * 自动移动
     */
    public _updateAutoMove_csryw() {
        if (this.isTouchStopView_csryw()) {
            return;
        }
        if (this._moveState_csryw == 0) {
            if (this.isAutoMoveWayLeftUp_csryw) {
                if (this._slideDirection == SlideDirection.HORIZONTAL) {//水平
                    this.content_csryw.x = this.content_csryw.x - this.autoMoveSpeed_csryw
                } else {
                    this.content_csryw.y = this.content_csryw.y + this.autoMoveSpeed_csryw
                }
            } else {
                if (this._slideDirection == SlideDirection.HORIZONTAL) {//水平
                    this.content_csryw.x = this.content_csryw.x + this.autoMoveSpeed_csryw
                } else {
                    this.content_csryw.y = this.content_csryw.y - this.autoMoveSpeed_csryw
                }
            }
            if (this.autoMoveWaitNum_csryw > 0) {//0 持续移动
                this._nextMoveLength_csryw = this._nextMoveLength_csryw - this.autoMoveSpeed_csryw;
                if (this._nextMoveLength_csryw <= 0) {
                    this._moveState_csryw = 1;
                }
            }
        } else {
            this._waitTimeNum_csryw = this._waitTimeNum_csryw + 1;
            if (this._waitTimeNum_csryw >= this.autoMoveWaitTime_csryw) {
                this._waitTimeNum_csryw = 0;
                this._updateNextLengthTime_csryw();
            }
        }

    }

    /**
     * 消息
     * @param event 
     */
    protected _dispatchEvent_csryw(event: FMTouchEvent) {
        if (event == FMTouchEvent.Scrolling) {
            //this.updateScrollingItems();
        } else if (event == FMTouchEvent.TouchStart) {
            this.isTouchStop_csryw = true;
        } else if (event == FMTouchEvent.TouchEnded) {
            this._updateNextLengthTime_csryw();
            this.isTouchStop_csryw = false;
        }
    }

    /**
     * 更新下次移动需要的数据
     */
    protected _updateNextLengthTime_csryw() {
        if (this.autoMoveWaitNum_csryw > 0) {
            if (this._slideDirection == SlideDirection.HORIZONTAL) {//水平
                this._nextMoveLength_csryw = this.autoMoveWaitNum_csryw * this.itemPrefab.data.width
                if (this.isAutoMoveWayLeftUp_csryw) {
                    for (let index = 0; index < this.arrayAliveListLayout_csryw.length; index++) {
                        const element = this.arrayAliveListLayout_csryw[index];
                        let vcx = this.content_csryw.x + element.node.x;
                        if (vcx > -element.node.width && vcx < 0) {
                            this._nextMoveLength_csryw = this._nextMoveLength_csryw + vcx;
                            break;
                        }
                    }
                } else {
                    for (let index = this.arrayAliveListLayout_csryw.length - 1; index >= 0; index--) {
                        const element = this.arrayAliveListLayout_csryw[index];
                        let vcx = this.content_csryw.x + element.node.x;
                        if (vcx < this.node.width + element.node.width && vcx > this.node.width) {
                            vcx = vcx - this.node.width;
                            this._nextMoveLength_csryw = this._nextMoveLength_csryw - vcx;
                            break;
                        }
                    }
                }
            } else {//垂直
                this._nextMoveLength_csryw = this.autoMoveWaitNum_csryw * this.itemPrefab.data.height
                if (this.isAutoMoveWayLeftUp_csryw) {
                    for (let index = 0; index < this.arrayAliveListLayout_csryw.length; index++) {
                        const element = this.arrayAliveListLayout_csryw[index];
                        let vcy = this.content_csryw.y + element.node.y;
                        if (vcy < element.node.height && vcy > 0) {
                            this._nextMoveLength_csryw = this._nextMoveLength_csryw - vcy;
                            break;
                        }
                    }
                } else {
                    for (let index = this.arrayAliveListLayout_csryw.length - 1; index >= 0; index--) {
                        const element = this.arrayAliveListLayout_csryw[index];
                        let vcy = this.content_csryw.y + element.node.y - element.node.height;
                        if (vcy > -(this.node.height + element.node.height) && vcy < -this.node.height) {
                            vcy = vcy + this.node.height;
                            this._nextMoveLength_csryw = this._nextMoveLength_csryw + vcy;
                            break;
                        }
                    }
                }
            }
        }
        this._waitTimeNum_csryw = 0;
        this._moveState_csryw = 0;
    }




    //更新
    protected updateScrollingItems_csryw() {
        let arrayLive: FMItemLayout[] = []
        if (this._slideDirection == SlideDirection.HORIZONTAL) {//水平
            for (let index = 0; index < this.arrayAliveListLayout_csryw.length; index++) {
                const element = this.arrayAliveListLayout_csryw[index];
                if (this.content_csryw.x + element.getPointX_csryw() < - this.itemPrefabWidth_csryw * 3) {//左 超过三个标准 移除
                    this.addFreeItem_csryw(element);
                } else if (element.getPointX_csryw() + this.content_csryw.x > this.node.width + this.itemPrefabWidth_csryw * 3) { //右 超过三个标准 移除
                    this.addFreeItem_csryw(element);
                } else { //中间区域 激活
                    arrayLive.push(element);
                }
            }
            if (arrayLive[0].getPointX_csryw() + this.content_csryw.x > - this.itemPrefabWidth_csryw) {//起始的 低于一个标准 创建一个
                let element = this.getFreeItem_csryw();
                element.setPointX_csryw(arrayLive[0].getPointX_csryw() - this.itemPrefabWidth_csryw);
                element.setItemIndex_csryw(arrayLive[0].getItemIndex_csryw() - 1)
                arrayLive.splice(0, 0, element);
                element.node.active = true;
            }
            if (arrayLive[arrayLive.length - 1].getPointX_csryw() + this.content_csryw.x < + this.node.width + this.itemPrefabWidth_csryw) {//结尾的 低于一个标准 创建一个
                const element = this.getFreeItem_csryw()
                element.setPointX_csryw(arrayLive[arrayLive.length - 1].getPointX_csryw() + this.itemPrefabWidth_csryw);
                element.setItemIndex_csryw(arrayLive[arrayLive.length - 1].getItemIndex_csryw() + 1)
                arrayLive.push(element);
                element.node.active = true;
            }
        } else { //垂直
            for (let index = 0; index < this.arrayAliveListLayout_csryw.length; index++) {
                const element = this.arrayAliveListLayout_csryw[index];
                if (element.getPointY_csryw() + this.content_csryw.y > this.itemPrefabHeight_csryw * 3) {//上 超过三个标准 移除
                    this.addFreeItem_csryw(element);
                } else if (element.getPointY_csryw() < -(this.content_csryw.y + this.node.height + this.itemPrefabHeight_csryw * 3)) { //下 超过三个标准 移除
                    this.addFreeItem_csryw(element);
                } else { //中间区域 激活
                    arrayLive.push(element);
                }
            }
            if (arrayLive[0].getPointY_csryw() + this.content_csryw.y <= this.itemPrefabHeight_csryw) {//起始的 低于一个标准 创建一个
                let element = this.getFreeItem_csryw();
                element.setPointY_csryw(arrayLive[0].getPointY_csryw() + this.itemPrefabHeight_csryw);
                element.setItemIndex_csryw(arrayLive[0].getItemIndex_csryw() - 1)
                arrayLive.splice(0, 0, element);
                element.node.active = true;
            }
            if (arrayLive[arrayLive.length - 1].getPointY_csryw() >= -(this.content_csryw.y + this.node.height + this.itemPrefabHeight_csryw)) {//结尾的 低于一个标准 创建一个
                const element = this.getFreeItem_csryw()
                element.setPointY_csryw(arrayLive[arrayLive.length - 1].getPointY_csryw() - this.itemPrefabHeight_csryw);
                element.setItemIndex_csryw(arrayLive[arrayLive.length - 1].getItemIndex_csryw() + 1)
                arrayLive.push(element);
                element.node.active = true;
            }
        }
        this.arrayAliveListLayout_csryw = arrayLive;
    }
    /**
       * 创建一个
       */
    private cloneLayout_csryw(): FMItemLayout {
        let layout = cc.instantiate(this.itemPrefab);
        let script = layout.getComponent(FMItemLayout);
        script.setAdTag_csryw(this.ad_tag_csryw)
        script.setFMListenerUpdate_csryw(handleFM_csryw(this.listenerUpdateInitItem_csryw, this));
        this.content_csryw.addChild(layout);
        return script;
    }


    /**
     * 获取一个自由节点
     */
    private getFreeItem_csryw(): FMItemLayout {
        let element = this.arrayFreeListLayout_csryw.pop()
        if (!element) {
            element = this.cloneLayout_csryw();
        }
        return element;
    }

    private addFreeItem_csryw(item: FMItemLayout) {
        item.node.active = false;
        this.arrayFreeListLayout_csryw.push(item);
    }


}