import AppConfig from "../../FrameWork/Config/AppConfig";

export default class KSAPI {
    public static readonly App_ID = "ks716072342233021082";
    public static readonly KS = window["kwaigame"];
    public static readonly adUnitId = "2300003758_01";
    public static readonly adMoudleId = null;

    protected static isRegisterVideoEvent = false;
    protected static onRewardCallBack: Function = null;
    protected static onCloseCallBack: Function = null;

    private static record: any;
    private static recordStartTime:number = 0;
    private static recordStopTime:number = 0;
    private static recordPauseTime:number = 0;
    private static recordResumeTime:number = 0;
    public static recordVideoID:string = null;
    public static isCanRecord:boolean = true;

    public static location:string = null;
 
    static isPlaySuccess: boolean = false;
    //初始化快手
    public static init_csryw(){
        if(this.KS){
            this.KS.init({"appId":this.App_ID});
        }
    }

    //开始游戏
    public static readyGo_csryw(){
        if(this.KS){
            this.KS.readyGo();
        }
    }

    //登录
    public static login_csryw(onSuccess: Function, onFail: Function) { 
        if (this.KS) {
            this.KS.login({ 
                success: (result) => {
                    console.log("登录成功" + JSON.stringify(result));
                    let gameToken = result.gameToken;
                    let gameUserId = result.gameUserId;
                    onSuccess(result.gameToken);
                },
                fail: (result) => {
                    console.log("登录失败" + JSON.stringify(result));
                    onFail(result);
                },
            }); 
        }
    }

    //加载分包
    public static loadSubPackage_csryw(packageName:string,onSuccess:Function,onFail:Function,onProgress:Function){
        var subpackageSupport  = this.KS.isSupport(this.KS.Support.features.Subpackage);
        if(!subpackageSupport){            
            //旧版本APP不支持，加载子包路径中的main.js即可，该路径与开发时设置的子包路径一致，如子包为sence2，路径为sence2/main.js
            window["require"](packageName + "/main.js");  
            onSuccess();
            return;
        }

        let loadTask = this.KS.loadSubpackage({name:packageName,success:function(res){   
            onSuccess(res);    
        },fail:function (err) {  
            console.log(err);
            onFail(err);
        }});

        loadTask.onProgressUpdate(res=>{	
            console.log(res);
            onProgress(res);
        });
    }

    //获得激励视频奖励事件
    protected static onRewardVideo_csryw(result) { 
        console.log("激励视频奖励回调: " , JSON.stringify(result),KSAPI.isPlaySuccess);
        if (KSAPI.onRewardCallBack) {
            KSAPI.onRewardCallBack(KSAPI.isPlaySuccess)
        }
    }

    //关闭激励视频事件
    protected static onCloseVideo_csryw(result) {
        console.log("激励视频关闭回调: " , JSON.stringify(result),KSAPI.isPlaySuccess);
        if (KSAPI.onCloseCallBack) {
            KSAPI.onCloseCallBack(false)
        }
    }

    //注册激励视频事件
    protected static regRewardedVideoAdEvent_csryw(rewardedVideoAd) {

        rewardedVideoAd.onReward(KSAPI.onRewardVideo_csryw) 
        rewardedVideoAd.onClose(KSAPI.onCloseVideo_csryw)
        KSAPI.isRegisterVideoEvent = true;
    }

    //播放激励视频
    public static showRewardedVideoAd_csryw(onClosed:Function,onFailed:Function,onRewarded:Function) {
        if (this.KS) {
            KSAPI.onCloseCallBack = onClosed;
            KSAPI.onRewardCallBack = onRewarded;

            var rewardedVideoAd = this.KS.createRewardedVideoAd(
                {
                    adUnitId: KSAPI.adUnitId,
                }
            );
            KSAPI.isPlaySuccess = false
            if(rewardedVideoAd)
            {    
                if (!KSAPI.isRegisterVideoEvent) {
                    KSAPI.regRewardedVideoAdEvent_csryw(rewardedVideoAd);
                }

                rewardedVideoAd.show({
                    success: () => {
                        console.log("激励视频播放成功");
                        KSAPI.isPlaySuccess = true
                    },
                    fail: (result) => {
                        console.log("激励视频播放失败: " + JSON.stringify(result)); 
                        onFailed();
                    }
                });
            }
            else{
                console.log("激励视频播放失败!") 
                onFailed();
            }
        }else{
            onRewarded();
        }
           
    }

    //创建录屏对象
    private static createRecord_csryw() {

        // 创建录屏对象
        if(this.KS){
            this.record = this.KS.createMediaRecorder();
            if(this.record){
                let self = this
                this.record.onStart(function () {
                    console.log("录频开始成功")
                });
                this.record.onStop(function (data) {
                    // 每次成功录制都会生成一个 videoID
                    self.recordVideoID = data.videoID;
                    console.log(`录频停止成功 ${JSON.stringify(data)} `)
                });

                this.record.onError(function (error) {
                    console.log(`录频过程中发生错误 ${JSON.stringify(error)} `)
                });

                this.record.onPause(function () {  
                    console.log("暂停录制成功!");     
                });

                this.record.onResume(function () { 
                    console.log("恢复录制成功!");
                });
            }else{
                console.log("该版本不支持录屏!")
                this.isCanRecord = false;
            }
        }
    } 
    
    //开始录屏
    public static startRecord_csryw() {
        this.stopRecord_csryw();
        if(this.KS){
            KSAPI.recordStartTime = new Date().getTime();
            if(this.record == null){
                this.createRecord_csryw();
            }
            
            this.record && this.record.start();
        }
    }

    //停止录屏
    public static stopRecord_csryw() {
        if(this.KS && this.record){
            KSAPI.recordStopTime = new Date().getTime();
            this.record.stop();
        }
    }

    //暂停录屏
    public static pauseRecord_csryw() {
        if(this.KS && this.record){
            KSAPI.recordPauseTime = new Date().getTime();
            this.record.pause();
        }
    }

    //恢复录屏
    public static resumeRecord_csryw() {
        if(this.KS && this.record){
            KSAPI.recordResumeTime = new Date().getTime();
            this.record.resume();
        }
    }

    //分享录屏
    public static shareRecord_csryw(onCompleted: Function = null, onFailed: Function = null) {
        if(this.KS && this.record){
            this.record.publishVideo({
                video: this.recordVideoID,
                mouldId:this.adMoudleId,
                callback: (error) => {
                    if (error != null && error != undefined) {
                        console.log("分享录屏失败: " + JSON.stringify(error));
                        onFailed && onFailed();
                        return;
                    }
                    console.log("分享录屏成功");
                    onCompleted && onCompleted();
                }
            }); 
        }
    } 

    //后台隐藏
    public static onHide_csryw(onCompleted:Function){
        if(this.KS){
            this.KS.onHide(onCompleted);
        }
    }

    //后台显示
    public static onShow_csryw(onCompleted:Function){
        if(this.KS){
            this.KS.onShow(onCompleted);
        }
    }

    //获得地理位置
    public static getLocation_csryw(){
        if(this.KS){ 
            this.KS.getLocation({
                success: (result) => {
                    console.log("获取地理位置成功:" + JSON.stringify(result));
                    this.location = result.province + result.city;
                },
                fail: (error) => {
                    console.log("获取地理位置失败:" + JSON.stringify(error));
                }
            });
        }   
    }

    // ------------------------------------- 插屏广告 -------------------------------------
    public static showInterstitialAd(onAdClose : Function, onFail : Function)
    {
        if(this.KS){

            console.log("插屏插屏create....................")
            let interstitialAd : any = this.KS.createInterstitialAd({
                adUnitId : AppConfig.ks_InsAdUnitId_csryw,
            });
    
            // interstitialAd.load().then(() => {
            // console.log("插屏广告加载成功！");
            interstitialAd.show().then(() => {
                console.log("插屏广告展示成功！");
            }).catch((err) => {
                console.log("插屏广告显示失败：", JSON.stringify(err));
            });
            // }).catch((err) => {
            //     console.log("插屏广告加载失败：", JSON.stringify(err));
            // });
    
            interstitialAd.onClose(() => {
                console.log('插屏广告 关闭');
                interstitialAd.destroy();
                if (onAdClose) {
                    onAdClose();
                }
            })
        }
    }
}