import FMComponentExtend from "../../Base/FMComponentExtend";

/*
 * @Author: YeeChan
 * @Date: 2022-01-11 20:25:27
 * @Description: 提示框
 */
const { ccclass, property } = cc._decorator;

@ccclass
export default class DialogToast extends FMComponentExtend {
    protected EventEnumView_csryw: { [key: string]: string; } = {}

    /**显示文本 */
    private label: cc.Label = null;
    protected initView_csryw(): void {
        this.label = this.child(this.node, "label", cc.Label);
    }

    /**
     * 打开提示对话框
     * @param msg 
     */
    public openTip(msg: string) {
        this.__onInit_csryw();
        this.label.string = msg;

        let height = cc.winSize.height * 0.25;
        this.node.opacity = 0;
        this.node.x = 0;
        this.node.y = height
        let self = this;
        cc.tween(this.node)
            .to(0.5, { y: height + 150, opacity: 255 }, { easing: cc.easing.cubicOut })
            .delay(2)
            .to(0.5, { y: height + 250, opacity: 0 }, { easing: cc.easing.cubicOut })
            .call(() => {
                self.node.destroy();
            })
            .start()
    }

}
