import Character from "../../../character/Character";
import BuffWitchCd from "./BuffWitchCd";

export default class BuffImmune extends BuffWitchCd {
    private orginValue: number = 0;

    public onAdd(count: number): void {
        super.onAdd(count);
        this.addShield();
    }

    private addShield(): void {
        var character = this.owner.getComponent(Character);
        character.addImmune();
        this.orginValue = character.shieldImmune;
    }

    protected onCdOver(): void {
        super.onCdOver();
        this.addShield();
    }

    public onTick(dt: number): void {
        super.onTick(dt);

        var character = this.owner.getComponent(Character);
        if (this.orginValue > 0 && character.shieldImmune <= 0) {
            this.resetCd();
        } 
        this.orginValue = character.shieldImmune;
    }
}