export default class Utils  {
    static getRootModel(name: string, component?: string): any {
        component = component || name;
        var node = cc.find(name);
        if (node) return node.getComponent(component);
    }

    static parseFloatParams(value: string): number {
        var num = 0;
        if ("string" == typeof value) {
            num = parseFloat(value);
            if (isNaN(num)) return 0;
        }
        return num;
    }

    static parseIntParams(value: string): number {
        var num = 0;
        if ("string" == typeof value) {
            num = parseInt(value);
            if (isNaN(num)) return 0;
        }
        return num;
    }

    static isStringNull(value: string): boolean {
        return value == null || value == "";
    }

    static isNull(value: any) {
        return value == null || void 0 == value;
    }

    static getConfigValue<T>(value: T, def: T): T {
        return this.isNull(value) ? value : def;
    }

    static random(min: number, max?: number): number {
        switch (arguments.length) {
            case 1:
                return 0 == min ? 0 : parseInt((Math.random() * min + 1).toString(), 10);
            case 2:
                return parseInt((Math.random() * (max - min + 1) + min).toString(), 10);
            default:
                return 0;
        }
    }

    static mergeTable(e, t): void {
        if (!t) return e;
        for (var a in t) if (t.hasOwnProperty(a)) {
            var r = t[a];
            e[a] ? e[a] += r : e[a] = r;
        }
        return e;
    }

    static getRandomListByCount<T>(arr: T[], count: number): T[] {
        if (arr.length <= count) return arr;
        var result: T[] = [];
        for (var i = 0; i < count; i++) {
            var i = this.random(0, arr.length - 1);
            result.push(arr[i]);
            arr.splice(i, 1);
        }
        return result;
    }

    static randomPointInCircle(radius: number): cc.Vec2 {
        var angle = Math.random() * Math.PI * 2;
        var x = Math.cos(angle) * radius;
        var y = Math.sin(angle) * radius;
        return cc.v2(x, y);
    }

    
    static stringPadding(num: string, length: number) {
        for(var len = (num + "").length; len < length; len = num.length) {
            num = "0" + num;            
        }
        return num;
    }
}