import FixComponent from "../../../common/fixcomponent/FixComponent";
import Gizmos from "../../../tools/Gizmos";
import Circle from "../../../tools/objects/Circle";
import BoxCollision from "../../collision/BoxCollision";
import Collision from "../../collision/Collision";
import CollisitionManager from "../../manager/CollisitionManager";
import GamaManager from "../../manager/GamaManager";
import Character from "../Character";

const { ccclass, property } = cc._decorator;

@ccclass
export default class WhirlBlade extends FixComponent {
    @property([cc.Node])
    public effects: cc.Node[] = [];

    @property(BoxCollision)
    public boxCollision: BoxCollision = null;

    public character: Character;

    private characterInRange: cc.Node[] = [];

    private circleHitBox: Circle;

    protected onLoad(): void {
        this.character = this.node.getComponent(Character);

        this.circleHitBox = new Circle();
    }

    public fixUpdate(): void {
        this.effects.forEach(node => {
            node.active = this.character.moving;
        });

        if (this.character.moving) {
            this.boxCollision.node.convertToWorldSpaceAR(cc.Vec2.ZERO, this.circleHitBox.center);
            this.circleHitBox.radius = this.character.getProperty("range");

            if (GamaManager.inst.debugHitBox) {
                Gizmos.DrawCirle(this.circleHitBox.center, this.circleHitBox.radius);
            }

            var cha = CollisitionManager.inst.collisitionByShape(this.circleHitBox, "enemy");
            
            for (var i = this.characterInRange.length - 1; i >= 0; i--) {
                if (!cha.includes(this.characterInRange[i])) {
                    this.characterInRange.splice(i, 1);
                }
            }

            for(var i = 0; i < cha.length; i++) {
                if (this.node != cha[i] && !this.characterInRange.includes(cha[i])) {
                    this.characterInRange.push(cha[i])
                    GamaManager.inst.doDamage(this.character.node, cha[i]);
                }
            }
        } else {
            this.characterInRange.length = 0;
        }
    }
}