import FixComponent from "../../../common/fixcomponent/FixComponent";
import FixManager from "../../../common/fixcomponent/FixManager";
import InputSysten, { Input } from "../../input/InputSystem";
import GamaManager, { GameState } from "../../manager/GamaManager";
import Character from "../Character";
import Hero from "../Hero";
import BallMove from "../movement/BallMove";

const { ccclass, property } = cc._decorator;

@ccclass
export default class PlayerController extends FixComponent {
    private input: Input = new Input;
    private inputSystem: InputSysten;

    private character: Hero;
    private ballMove: BallMove;

    protected start(): void {
        this.character = this.node.getComponent(Hero);
        this.ballMove = this.node.getComponent(BallMove);
    }

    protected onLoad(): void {
        this.inputSystem = cc.director.getScene().getComponentInChildren(InputSysten);
    }

    public fixUpdate(): void {
        this.input.reset();
        this.inputSystem.accumulateInput(this.input, FixManager.inst.fixedDeltaTime);

        if (GamaManager.inst.state == GameState.None) {
            return;
        }

        if (this.input.slide) {
            var direction = GamaManager.inst.state == GameState.Guide ? cc.Vec2.UP.clone() : this.input.direction;
            this.character.catapult(direction);
        }
    } 
}