import { PropertyModifier } from "../../PropertyModifier";
import BuffConfig from "./BuffConfig";

export class AddBuffInfo {
    id: string;
    addStack: number;
}

export default class BuffObj {
    public config: BuffConfig;

    public owner: cc.Node;

    public duration: number;

    public timeElapsed: number;

    public ticked: number;

    public stack: number;

    public buffParam: { [id: string]: any };

    public propMod: PropertyModifier = new PropertyModifier;

    constructor(owner: cc.Node, config: BuffConfig, addStack: number) {
        this.owner = owner;
        this.config = config;
        this.duration = config.duration ?? 0;
        this.timeElapsed = 0;
        this.ticked = 0;
        this.stack = addStack;
        this.buffParam = config.param ?? [];
    }

    onAdd(modifyStack: number): void {
        
    }

    onTick(dt: number): void {

    }

    onRemoved(): void {
        
    }
}