/**
 * Timer工具
 */
export class TimerUtils {

    /**
     * 循环执行 有限次数
     * @param handler 回调
     * @param interval 间隔时间 秒
     * @param repeat 重复次数（实际运行repeat + 1次）
     * @param delay 延迟多少时间后开始执行 秒
     */
    public static loopNum_csryw(handler: Function, interval: number, repeat: number, delay: number) {
        return this.schedule_csryw(handler, interval, repeat, delay);
    }


    /**
     * 延迟执行一次。
     * @param handler 回调
     * @param delayTime 延迟时间 秒
     */
    public static once_csryw(handler: Function, delayTime: number = 0) {
        return this.scheduleOnce_csryw(handler, delayTime);
    }


    /**
     * 循环执行 一直循环
     * @param handler 回调
     * @param intervlTime 间隔时间 秒 值为 0，那么回调函数每一帧都会被调用
     * @param delay 延迟时间执行  秒
     */
    public static loop_csryw(handler: Function, intervlTime: number = 0, delay: number = 0) {
        return this.schedule_csryw(handler, intervlTime ? intervlTime : 0.02, cc.macro.REPEAT_FOREVER, delay);
    }


    /**
     * 移除计时器Timer
     * @param handler 回调
     */
    public static removeTimer_csryw(handler: Function) {
        this.unschedule_csryw(handler);
    }


    /**
     * 移除所有Timer
     */
    public static removeAllTimers_csryw() {
        cc.director.getScheduler().unscheduleAllForTarget(this)
    }


    /**
     * 单次调度
     * @param handler 
     * @param delay 
     */
    private static scheduleOnce_csryw(handler: Function, delay: number) {
        this.schedule_csryw(handler, 0, 0, delay);
        return handler;
    }


    /**
     * Timer开始调度
     * @param handler 回调
     * @param interval 间隔时间
     * @param repeat 重复次数（实际运行repeat + 1次）
     * @param delay 延迟多少时间后开始执行
     */
    private static schedule_csryw(handler: Function, interval: number, repeat: number, delay: number) {
        cc.director.getScheduler().schedule(handler, this, interval, repeat, delay);
        return handler;
    }


    /**
     * 移除调度
     * @param handler 
     */
    private static unschedule_csryw(handler: Function) {
        if (!handler) return;
        cc.director.getScheduler().unschedule(handler, this);
    }

}