import FixComponent from "../../../common/fixcomponent/FixComponent";
import FixManager from "../../../common/fixcomponent/FixManager";
import GamaManager, { GameState } from "../../manager/GamaManager";
import UltSkillConfig from "../../structs/character/UltSkillConfig";
import Hero from "../Hero";

const { ccclass, property, executionOrder } = cc._decorator;

@ccclass
export default class UltSkill extends FixComponent {
    hero: Hero;
    config: UltSkillConfig;
    buff: string[];

    timer: number = 0;
    isRunning: boolean = false;

    init(hero: Hero, config: UltSkillConfig): void {
        this.hero = hero;
        this.config = config;
        this.buff = (config.buff ?? "").split("|");
    }

    castSkill(): void {
        this.isRunning = true;
        this.timer = this.config.duration;
        this.onSkillStart();
    }

    protected onSkillStart(): void {
        for (var i = 0; i < this.buff.length; i++) {
            this.hero.removeBuff(this.buff[i]);
            this.hero.addBuff(this.buff[i]);
        }   
    }

    protected onSkillEnd(): void {
        for (var i = 0; i < this.buff.length; i++) {
            this.hero.removeBuff(this.buff[i]);
        }   
    }

    public fixUpdate(): void {
        if (GamaManager.inst.state != GameState.Run || !this.isRunning) {
            return;
        }

        var dt = FixManager.fixedDeltaTime;
        var old = this.timer;
        this.timer -= dt;
        if (old > 0 && this.timer <= 0) {
            this.isRunning = false;
            this.onSkillEnd();
        }
    }
}