const {ccclass, property} = cc._decorator;

@ccclass
export default class ObjPool extends cc.Component {
    @property(cc.Boolean)
    useCache: boolean = true;

    _pool_list: { [id: string]: cc.NodePool } = {};
    _obj_count: number = 0;

    protected loadPrefab(name: string, callBack: (err: any, prefab: cc.Prefab) => void): void {

    }

    public getObj(name: string, callBack: Function, forceCache?: boolean) {
        if (this.useCache || forceCache) {
            this._pool_list[name] || (this._pool_list[name] = new cc.NodePool());
            var item = this._pool_list[name].get();
            if (item != null) {
                callBack && callBack(item);
            } else {
                this.loadPrefab(name, function (err, prefab) {
                    if (err) {
                        console.error(err);
                    } else {
                        var node = cc.instantiate(prefab);
                        callBack && callBack(node);
                    }
                }.bind(this));
            }
        } else {
            this.loadPrefab(name, function (err, prefab) {
                if (err) console.error(err); else {
                    var node = cc.instantiate(prefab);
                    callBack && callBack(node);
                }
            }.bind(this));
        }
        this._obj_count++;
    }

    public removeObj(name: string, node: cc.Node, forceCache?: boolean) {
        if (node) {
            if (this.useCache, forceCache, this._pool_list[name]) {
                this._pool_list[name].put(node);
            } else {
                node.destroy();
            }
            this._obj_count--;
        }
    }

    public isEmpty(): boolean {
        return 0 == this._obj_count;
    }

    public clearPool() {
        for (var key in this._pool_list) {
            if (this._pool_list.hasOwnProperty(key)) {
                this._pool_list[key].clear();
            }
        }
        this._pool_list = {};
    }

    protected onDestroy() {
        this.clearPool();
    }
}