export class CharacterWeights {
    id: string;
    weights: number;
}

export class EndlessInfo {
    time: number = 0; //秒
    interval: number = 1;
    count: number = 1;
    total: number = 0;
    monsters: string[] = [];
    weights: CharacterWeights[] = [];

    constructor(time: number, interval: number, count: number, total: number, monsters:string[], weights: CharacterWeights[]) {
        this.time = time;
        this.interval = interval;
        this.count = count;
        this.total = total;
        this.monsters = monsters;
        this.weights = weights;
    }
}