import UnitBindPoint from "./UnitBindPoint";

const { ccclass, property } = cc._decorator;

@ccclass
export default class UnitBindManager extends cc.Component {
    public getBindPointByKey(key: string): UnitBindPoint{
        var bindPoints = this.getComponentsInChildren(UnitBindPoint);
        for (var i = 0; i < bindPoints.length; i++){
            if (bindPoints[i].key == key){
                return bindPoints[i];
            }
        }
        return null;
    }

    public addBindGameObject(bindPointKey: string, go: string, key: string, loop: boolean): void {
        var bp = this.getBindPointByKey(bindPointKey);
        if (bp == null) return;
        bp.addBindGameObject(go, key, loop);
    }

    public removeBindGameObject(bindPointKey: string, key: string,){
        var bp = this.getBindPointByKey(bindPointKey);
        if (bp == null) return;
        bp.removeBindGameObject(key);
    }

    public removeAllBindGameObject(key: string): void {
        var bindPoints = this.getComponentsInChildren(UnitBindPoint);
        for (var i = 0; i < bindPoints.length; i++){
            bindPoints[i].removeBindGameObject(key);
        }
    }
}