/****************************************************
* cc.Component 的扩展 主要增加初始方法 initView_csryw  __onInit_csryw  和 事件
*****************************************************/
export default abstract class FMComponentExtend extends cc.Component {
    //标记
    protected __isInit_csryw: boolean = false;
    //回调
    protected _listenerCallView_csryw: FMListener;
    //各个节点自定义事件
    protected abstract EventEnumView_csryw: { [key: string]: string; } = {}
    //初始方法
    protected abstract initView_csryw(): void;


    protected onLoad() {
        this.__onInit_csryw();
    }


    /**
     * 获取子节点上的类型
     * @param childName 
     * @param node 
     * @returns 
     */
    public childNode(childName: string, node?: cc.Node): cc.Node {
        let cnode = node ? node : this.node;
        let childNode = cnode.getChildByName(childName);
        if (!childNode) {
            console.error("没有找到节点 ", childName, cnode.name)
        }
        return childNode;
    }

    /**
     * 获取子节点上的类型
     * @param node 
     * @param childName 
     * @param type 
     * @returns 
     */
    public child<T extends cc.Component>(node: cc.Node, childName: string, type: { prototype: T }): T {
        let childNode = node.getChildByName(childName);
        if (childNode) {
            return childNode.getComponent(type);
        } else {
            console.error("没有找到节点 ", childName, node.name)
        }
        return undefined;
    }

    /**
     * 绑定点击事件
     * @param node 
     * @param callback 
     * @param target 
     * @returns 
     */
    public onClick<T extends Function>(node: cc.Node, callback: T, target: any): T {
        return node.on("click", callback, target);
    }


    /**
     * 外部如果需要里面获取onload中执行的属性，需要在方法执行前调用
     */
    protected __onInit_csryw() {
        if (!this.__isInit_csryw) {
            this.__isInit_csryw = true;
            this.initView_csryw();
        }
    }

    /**
     * 设置监听
     * @param listener 
     */
    public onListenerEventView_csryw(listener: FMListener) {
        this._listenerCallView_csryw = listener;
    }

    /**
     * 发送事件 (接收结构为(Event,Node,Data) )
     * @param event 事件名 @EventEnumView_csryw
     * @param data 数据结构
     */
    protected emitListenerEvent_csryw(event: string, data?: any, data2?: any) {
        //console.log("发送 " + event)
        if (this._listenerCallView_csryw) {
            callFM_csryw(this._listenerCallView_csryw, event, this, data, data2);
        }
    }

    /**
     * 判断view 是否激活
     */
    public isActiveView_csryw(): boolean {
        if (cc.isValid(this, true) && this.node.activeInHierarchy) { //没有被销毁 && 在场景中是激活的
            return true;
        }
        return false;
    }

    //当该组件被销毁时调用
    protected onDestroy(): void {

    }

}