import { GlobalMessage } from "../../tools/GlobalMessage";
import { BlackMaskHide, BlackMaskShow } from "../message/Messages";

const {ccclass, property} = cc._decorator;

@ccclass
export default class BlackMask extends cc.Component {
    protected onLoad(): void {
        this.node.active = false;
        GlobalMessage.on(BlackMaskShow, this.onShow, this);
        GlobalMessage.on(BlackMaskHide, this.onHide, this);
    }

    private onShow(): void {
        this.node.active = true;
    }

    private onHide(): void {
        this.node.active = false;
    }

    protected onDestroy(): void {
        GlobalMessage.offAll(this);
    }
}
