/**
 * 事件绑定接口
 */
export class FMListener {
    callback: Function; //对象回调方法
    target: any; //对象本身
}

/**
 * 添加一个绑定接口
 * @param callback //对象回调方法
 * @param target //对象本身
 * @returns  FMListener
 */
export function handleFM_csryw(callback: Function, target: any): FMListener {
    return { "target": target, "callback": callback }
}

/**
 * call一个绑定接口
 * @param FMListener 
 * @param ...argArray 
 * @returns any
 */
export function callFM_csryw(inter: FMListener, ...argArray: any[]): any {
    if (inter && inter.callback) {
        return inter.callback.call(inter.target, ...argArray)
    }
}
//导出
(<any>window).FMListener = FMListener;
(<any>window).handleFM_csryw = handleFM_csryw;
(<any>window).callFM_csryw = callFM_csryw;