import AssetManager from "../../common/AssetManager";
import SightEffect from "../../common/SightEffect";
import FixComponent from "../../common/fixcomponent/FixComponent";
import FixManager from "../../common/fixcomponent/FixManager";

const { ccclass, property } = cc._decorator;

@ccclass
export default class UnitBindPoint extends FixComponent {
    @property(cc.String)
    public key: string = "";

    @property(cc.Vec2)
    public offset: cc.Vec2 = new cc.Vec2;

    private bindGameObject: { [id: string]: BindGameObjectInfo } = {};

    public fixUpdate(): void {
        var toRemove: string[] = [];
        for(var key in this.bindGameObject){
            var goInfo = this.bindGameObject[key];
            if (goInfo.gameObject == null){
                toRemove.push(key);
                continue;
            }
            if (goInfo.forever == false){
                goInfo.duration -= FixManager.fixedDeltaTime;
                if (goInfo.duration <= 0){
                    goInfo.gameObject.destroy();
                    toRemove.push(key);
                }
            }
        }
        for (var i = 0; i < toRemove.length; i++){
            delete this.bindGameObject[toRemove[i]];
        }
    }

    public addBindGameObject(goPath: string, key: string, loop: boolean){
        if (key != "" && this.bindGameObject[key] != null) return;    //已经存在，加不成
        
        var prefab = AssetManager.getPrefab(goPath);
        if (prefab == null) {
            return;
        }
        var effectGO = cc.instantiate(prefab);
        effectGO.setPosition(this.offset);
        effectGO.parent = this.node;
        effectGO.active = true;

        var se = effectGO.getComponent(SightEffect);
        if (!se){
            effectGO.destroy();
            return;
        } 
        var duration = se.duration * (loop == false ? 1 : -1);
        var bindGameObjectInfo = new BindGameObjectInfo( effectGO, duration );
        if (key != ""){
            this.bindGameObject[key] = bindGameObjectInfo;
        }else{
            this.bindGameObject[cc.director.getTotalFrames() * Math.random() * 10 + "_" + Math.random()] = bindGameObjectInfo;
        }
    }


    public removeBindGameObject(key: string) {
        var info = this.bindGameObject[key];
        if (info == null) 
            return;
        if (info.gameObject){
            info.gameObject.destroy();
        }
        delete this.bindGameObject[key];
    }
}

export class BindGameObjectInfo{
    public  gameObject: cc.Node;

    public duration: number;

    public forever: boolean;

    public constructor(gameObject: cc.Node, duration: number){
        this.gameObject = gameObject;
        this.duration = Math.abs(duration);
        this.forever = duration <= 0;
    }
}