import Character from "../../../character/Character";
import BuffObj from "./BuffObj";

export default class BuffHeal extends BuffObj {
    onAdd(modifyStack: number): void {
        var value = parseFloat(this.buffParam[0]);
        var character = this.owner.getComponent(Character);
        var value = character.getProperty("maxHp") * value;
        character.heal(value);
    }
}