import FixComponent from "../../common/fixcomponent/FixComponent";
import FixManager from "../../common/fixcomponent/FixManager";
import Gizmos from "../../tools/Gizmos";
import AABB from "../../tools/objects/AABB";
import Character from "../character/Character";
import CollisitionManager from "../manager/CollisitionManager";
import GamaManager from "../manager/GamaManager";
import BulletModel from "../structs/battle/BulletModel";
const { ccclass, property } = cc._decorator;

@ccclass
export default class BulletObj extends FixComponent {
    public owner: cc.Node;
    
    public direction: cc.Vec2;

    public hitTime: number;

    public model: BulletModel;

    public hitRecord: { [id: string]: boolean } = {};

    init(owner: cc.Node, direction: cc.Vec2, model: BulletModel): void {
        this.owner = owner;
        this.direction = direction;
        this.model = model;
        this.hitTime = model.hitTime;
        this.node.rotation = Math.atan2(direction.x, direction.y) * 180 / Math.PI;
    }

    public fixUpdate(): void {
        var dt = FixManager.fixedDeltaTime;

        var position = this.direction.clone();
        position.mulSelf(this.model.speed * dt);
        position.x += this.node.x;
        position.y += this.node.y;

        this.node.setPosition(position);

        if (position.x < GamaManager.inst.minX || position.y < GamaManager.inst.minY 
                || position.x > GamaManager.inst.maxX || position.y > GamaManager.inst.maxY) {
            this.node.destroy();
        } else {
            if (GamaManager.inst.debugHitBox) {
                Gizmos.DrawCirle(cc.v2(this.node.x, this.node.y), this.node.width / 2);
            }
            var shape = new AABB();
            shape.center.set(this.node.position as any);
            shape.halfExtents.x = this.node.width / 2;
            shape.halfExtents.y = shape.halfExtents.x;
            var character = this.owner.getComponent(Character);
            var group = "hero";
            if (character.group == "hero") {
                group = "enemy"
            }
            var result = CollisitionManager.inst.collisitionByShape(shape, group);
            for (var i = 0; i < result.length; i++) {
                var node = result[i];
                var id = node["_id"];
                if (this.hitRecord[id] == null) {
                    this.hitRecord[id] = true;
                    this.hit(node);
                }
            }
        }
    }

    private hit(node: cc.Node): void {
        if (this.hitTime != -1) {
            this.hitTime -= 1;
            if (this.hitTime <= 0) {
                this.node.destroy();
            }
        }
        GamaManager.inst.doDamage(this.owner, node, this.model.tags);
    }
}