import ConfigManager from "../../ConfigManager";
import FixComponent from "../../common/fixcomponent/FixComponent";
import FixManager from "../../common/fixcomponent/FixManager";
import { GlobalMessage } from "../../tools/GlobalMessage";
import WeigthsRandom from "../../tools/WeigthsRandom";
import { EndlessTable } from "../designer/table/EndlessTable";
import { MonsterDead } from "../message/Messages";
import { EndlessInfo } from "../structs/battle/EndlessStage";
import AudioManager from "./AudioManager";
import GamaManager, { GameState } from "./GamaManager";

const { ccclass, property } = cc._decorator;

@ccclass
export default class MonsterSpawn extends FixComponent {
    private _nextTime: number = -1;
    private _currentStageIndex: number = -1;
    private _monsterWarningTime: number = -1;
    private _monsterWarning: boolean = false;
    private _weigthsRandom: WeigthsRandom = new WeigthsRandom;

    public count: number = 0;
    public isOver: boolean = false;
    public endlessTable: EndlessInfo[] = [];

    public get currentStage(): EndlessInfo {
        return this.endlessTable[this._currentStageIndex];
    }

    protected onLoad(): void {
        this.endlessTable = ConfigManager.endlessConfig;
    }

    protected onEnable(): void {
        super.onEnable();
        GlobalMessage.on(MonsterDead, this.onMonsterDead, this);
    }

    protected onDisable(): void {
        super.onDisable();
        GlobalMessage.offAll(this);
    }

    private checkCurrentStage(time: number): void {
        var stage = this.currentStage;
        if (stage != null && time < stage.time) {
            return;
        }

        if (this._currentStageIndex == this.endlessTable.length - 1) {
            return;
        }

        var index = this._currentStageIndex == -1 ? 0 : this._currentStageIndex;
        for (; index < this.endlessTable.length; index++) {
            var stage = this.endlessTable[index];
            if (stage.time > time) {
                break;
            }
        }

        this._currentStageIndex = index;
        var weights = this.currentStage.weights;
        var monsterWeights: { key: string, weight: number }[] = [];
        for (var i = 0; i < weights.length; i++) {
            var item = weights[i];
            monsterWeights.push({ key: item.id, weight: item.weights });
        }
        this._weigthsRandom.weights = monsterWeights;

        if (this.currentStage.monsters.length > 0 
            && this.currentStage.monsters.find(id => ConfigManager.characterConfig[id].type > 1) != null) {
            this._monsterWarningTime = this.currentStage.time - 3;
        }
    }

    private createMonster(id: string): void {
        var mapSize = GamaManager.inst.mapSize;
        var width = mapSize.x / 2;
        var height = mapSize.y / 2;
        var pos = new cc.Vec2(((Math.random() - 0.5) * 2) * width, ((Math.random() - 0.5) * 2) * height);
        GamaManager.inst.createCharacter(id, pos);
    }

    private tryGenerateMonsters(time: number, currentCount: number): void {
        var index = this._currentStageIndex;
        var state = this.currentStage;
        this.checkCurrentStage(time);

        if (state != null && (state != this.currentStage || time > this.currentStage.time)) {
            if (index == this.endlessTable.length - 1) {
                this.isOver = true;
            }
            for (var i = 0; i < state.monsters.length; i++) {
                this.createMonster(state.monsters[i]);
                this.count++;
            }
        }

        var flag = false;
        if (this._nextTime <= 0) {
            flag = true;
            this._nextTime = this.currentStage.interval;
        }

        if (flag || this.count == 0) {
            var needCount = this.currentStage.count;
            if ((currentCount + needCount) > this.currentStage.total) {
                needCount = this.currentStage.total - currentCount;
            }
            for (var i = 0; i < needCount; i++) {
                var id = this._weigthsRandom.Random();
                this.createMonster(id);
                this.count++;
            }
        }
    }

    public fixUpdate(): void {
        if (GamaManager.inst.state != GameState.Run || this.isOver) {
            return;
        }

        var dt = FixManager.fixedDeltaTime;
        this._nextTime -= dt;
        this.tryGenerateMonsters(GamaManager.inst.totalTime, this.count);

        if (this._monsterWarningTime != -1 && this._monsterWarningTime <= GamaManager.inst.totalTime) {
            if (this._monsterWarning == false) {
                this._monsterWarning = true
                this._monsterWarningTime = GamaManager.inst.totalTime + 3;
                AudioManager.inst.pauseBGM();
                AudioManager.inst.playSFX("Sound/精英和boss出现前开场音效3");
            } else {
                this._monsterWarningTime = -1;
                this._monsterWarning = false;
                AudioManager.inst.resumBGM();
            }
        }
    }

    private onMonsterDead(): void {
        this.count--;
        this.count = Math.max(this.count, 0);
    }
}