import DialogMgr from "../../../FrameWork/Mgr/DialogMgr";
import VibrateMgr from "../../../FrameWork/Mgr/VibrateMgr";
import Common from "../../../FrameWork/Util/Common";
import { GlobalMessage } from "../../tools/GlobalMessage";
import Utils from "../../tools/Utils";
import Character from "../character/Character";
import Hero from "../character/Hero";
import GamaManager from "../manager/GamaManager";
import HeroManager from "../manager/HeroManager";
import { StartSelectTco, Settlement, HeroHurt, KillMonster, GameStart, BatterShow, TryResurrection, BossShow, GameGuideStart, GameGuideEnd, DropMoney } from "../message/Messages";
import BloodBar from "./BloodBar";

const {ccclass, property} = cc._decorator;

@ccclass
export default class GameView extends cc.Component {
    @property(cc.Node)
    public maskAnimNode: cc.Node = null;

    @property(cc.Node)
    public superFlagNode: cc.Node = null;

    @property(cc.Node)
    public moneyWeight: cc.Node = null;

    @property(cc.Node)
    public head: cc.Node = null;

    @property(cc.Sprite)
    public heroHpBar: cc.Sprite = null;
    
    @property(cc.Label)
    public heroHpLabel: cc.Label = null;

    @property(cc.Sprite)
    public heroEnergyBar: cc.Sprite = null;

    @property(cc.Sprite)
    public heroExpBar: cc.Sprite = null;

    @property(cc.Label)
    public heroLevelLabel: cc.Label = null;

    @property([cc.Label])
    public timeLabels: cc.Label[]= [];

    @property(cc.Sprite)
    public heroHurt: cc.Sprite = null;

    @property(cc.Prefab)
    public expItemPrefab: cc.Prefab = null;

    @property(cc.Node)
    public expParent: cc.Node = null;

    @property(cc.Node)
    public batterNode: cc.Node = null;

    @property(cc.Sprite)
    private bossNameIcon: cc.Sprite = null;

    @property(BloodBar)
    private bossHpBar: BloodBar = null;

    @property(cc.Node)
    private guide: cc.Node = null;

    @property(cc.Node)
    private crisisTip: cc.Node = null;

    @property(cc.Prefab)
    private moneyItem: cc.Prefab = null;

    @property(cc.Label)
    private moneyLabel: cc.Label = null;

    private hero: Hero;

    private tagetBoss: Character;

    private expPool: cc.NodePool = new cc.NodePool;

    private moneyPool: cc.NodePool = new cc.NodePool;

    protected start(): void {
        this.guide.active = false;
    }

    protected onEnable(): void {
        GlobalMessage.on(Settlement, this.onGameSettlement, this);
        GlobalMessage.on(StartSelectTco, this.onStartSelectTco, this);
        GlobalMessage.on(HeroHurt, this.onHeroHurt, this);
        GlobalMessage.on(KillMonster, this.onKillMonster, this);
        GlobalMessage.on(BatterShow, this.onBatterShow, this);
        GlobalMessage.on(TryResurrection, this.onTryResurrection, this);
        GlobalMessage.on(BossShow, this.onBossShow, this);
        GlobalMessage.on(GameGuideStart, this.onGameGuide, this);
        GlobalMessage.on(GameGuideEnd, this.onGameGuide, this);
        GlobalMessage.on(DropMoney, this.onDropMoeny, this);
    }

    protected onDisable(): void {
        GlobalMessage.offAll(this);
        this.expPool.clear();
    }

    private updateHeroHp(): void {
        this.heroHpBar.fillRange = this.hero.hpPercentage;
        this.heroHpLabel.string = this.hero.hp + "/" + this.hero.getProperty("maxHp");
        this.heroLevelLabel.string = HeroManager.inst.level + "";
        this.heroExpBar.fillRange = HeroManager.inst.getExpProgress();
        this.heroEnergyBar.fillRange = this.hero.energy / this.hero.maxEnergy;
        this.superFlagNode.active = this.hero.isSuper;

        this.crisisTip.active = (this.hero.hp / this.hero.getProperty("maxHp")) <= 0.3;
    }

    private updateTime(): void {
        var time = GamaManager.inst.totalTime;
        var sec = Math.floor(time % 60);
        var min = Math.floor(time / 60);
        this.timeLabels[0].string = Utils.stringPadding(min + '', 2);
        this.timeLabels[1].string = Utils.stringPadding(sec + '', 2);
    }

    private updateBossHpBar(): void {
        var boss = this.hero.attackBoss;
        if (boss == null) {
            boss = GamaManager.inst.bossCharacters[0];
        }

        if (boss != this.tagetBoss) {
            this.tagetBoss = boss;
            if (boss != null) {
                this.bossHpBar.node.active = true;
                this.bossHpBar.setBar(boss.getProperty("maxHp"), 500);
                if (boss.config.nameIcon != null && boss.config.nameIcon != "") {
                    this.bossNameIcon.node.active = true;
                    Common.loadSpriteFrame("subResGame", "Texture/Name/" + boss.config.nameIcon, (frame) => {
                        this.bossNameIcon.spriteFrame = frame;
                    });
                } else {
                    this.bossNameIcon.node.active = false;
                }
            }
        }

        if (this.bossHpBar.node.active && this.tagetBoss) {
            this.bossHpBar.ChangeValue(this.tagetBoss.hp);
        } else {
            this.bossHpBar.node.active = false;
        }
    }

    protected update(dt: number): void {
        if (this.hero == null || this.hero.node == null) {
            this.hero = GamaManager.inst.mainCharacter as Hero;
        }

        if (this.hero && this.hero.node != null) {
            this.updateHeroHp();
        }

        this.updateBossHpBar();

        this.updateTime();
    }

    private onClickSetting(): void {
        // cc.director.loadScene("battleScene");
        GamaManager.inst.pause();
        DialogMgr.openSettingDialog({ callback: (_, back: boolean) => {
            if (!back) {
                GamaManager.inst.resume();
            } else {
                cc.director.loadScene("FMMainScene");
            }
        }, target: this }, this.node, "view");
    }

    private onGameSettlement(): void {
        DialogMgr.openSettlement(this.node);
    }

    private onStartSelectTco(data: StartSelectTco): void {
        GamaManager.inst.pause();
        DialogMgr.openTco(this.node, data.options, () => {
            GamaManager.inst.resume();
        });
    }

    private onHeroHurt(): void {
        if (this.heroHurt == null) {
            return;
        }
        VibrateMgr.vibrateShort_csryw();
        this.heroHurt.node.opacity = 0;
        cc.tween(this.heroHurt.node).to(0.2, { opacity: 255 }).to(0.2, { opacity: 0 }).start();
    }

    private onMaskOpened(): void {
        GlobalMessage.emit(new GameStart);
    }

    private onBatterShow(): void {
        this.batterNode.active = true;
        this.batterNode.getComponentInChildren(cc.Label).string = HeroManager.inst.batterCount + "";
        this.batterNode.getComponent(cc.Animation).play("Batter");
    }

    private onTryResurrection(): void {
        DialogMgr.openResurrection(this.node);
    }

    private onBossShow(data: BossShow): void {
    }

    private onGameGuide(data: GameGuideStart | GameGuideEnd): void {
        if (data instanceof GameGuideStart) {
            this.guide.active = true;
        } else {
            this.guide.active = false;
        }
    }

    private onKillMonster(data: KillMonster): void {
        return;
        var character = data.character;
        var position = character.node.convertToWorldSpaceAR(cc.Vec2.ZERO);
        position = this.node.convertToNodeSpace(position);

        var node: cc.Node = null;
        if (this.expPool.size() > 0) {
            node = this.expPool.get();
        } else {
            node = node = cc.instantiate(this.expItemPrefab);
        }
        node.parent = this.expParent;
        node.active = true;
        node.setPosition(position);

        this.head.convertToWorldSpaceAR(cc.Vec2.ZERO, position);
        this.node.convertToNodeSpaceAR(position, position);

        var self = this;
        cc.tween(node).to(0.7, { x: position.x, y: position.y }).call(() => {
            self.expPool.put(node);
        }).start();
    }

    private onDropMoeny(data: DropMoney): void {
        var position = data.position.clone();
        position = this.node.convertToNodeSpaceAR(position);
        position.x += this.node.width / 2;
        position.y += this.node.height / 2;

        var node: cc.Node = null;
        if (this.moneyPool.size() > 0) {
            node = this.moneyPool.get();
        } else {
            node = node = cc.instantiate(this.moneyItem);
        }
        node.parent = this.expParent;
        node.active = true;
        node.scale = 1;
        node.setPosition(position);

        this.moneyWeight.convertToWorldSpaceAR(cc.Vec2.ZERO, position);
        this.expParent.convertToNodeSpaceAR(position, position);

        var self = this;
        cc.tween(node).to(0.7, { x: position.x, y: position.y, scale: 0.8 }).call(() => {
            self.moneyPool.put(node);
        }).start();

        this.moneyLabel.string = GamaManager.inst.currentMoney + '';
    }
}
