import Character from "../../character/Character";

export default class DamageInfo {
    public attacker: cc.Node;
    
    public defender: cc.Node;

    public tags: string[];

    public direction: cc.Vec2;

    constructor(attacker: cc.Node, defender: cc.Node, tags: string[] = []) {
        this.attacker = attacker;
        this.defender = defender;
        this.tags = tags;
        this.direction = cc.v2(attacker.x - defender.x, attacker.y - defender.y);
        this.direction.normalizeSelf();
    }

    public isHeal(): boolean {
        return false;
    }

    public DamageValue(): number {
        var attackerCharacter = this.attacker.getComponent(Character);
        return attackerCharacter.attack;
    }

}

export class DamageResult {
    public isCirt: boolean;
    public damage: number;
    public direction: cc.Vec2;

    constructor(damage: number, isCirt: boolean, direction: cc.Vec2) {
        this.isCirt = isCirt;
        this.damage = damage;
        this.direction = direction;
    }
}
