import EventMgr from "../Event/EventMgr";
import { ryw_Event } from "../Event/EventEnum";
import PhysicalPowerMgr from "../Mgr/PhysicalPowerMgr";
import GameMgr from "../Mgr/GameMgr";

//游戏数据,为保持版本兼容，建议不要删除和修改字段名
export class UserGameData {
    public physicalData: any = {};
    public levelNum: number = 1;//当前关卡
    public moneyNum: number = 0;//金币数量
    public gameTime: number = 0;
    public weapons: string[] = ["1"];
    public useWeapon: string = "1";
    public showSharedTime: number = 0;
    public killMonster: { [id: string]: number } = {};
}

export default class User {
    public static code_csryw: string = "";
    public static openId_csryw: string = "";
    public static token_csryw: string = null;
    public static nickName_csryw: string = "";
    public static gender_csryw: number = 0;

    public static get isLogin_csryw() {
        return (User.code_csryw != "") || (User.token_csryw != "");
    }

    private static readonly _gameData_csryw: UserGameData = new UserGameData();

    public static getSaveData_csryw(): string {
        //体力数据
        User._gameData_csryw.physicalData = PhysicalPowerMgr.getData();
        return JSON.stringify(User._gameData_csryw);
    }


    /**
     * 测试用途
     */
    public static testInitUser_csryw() {
        User._gameData_csryw.levelNum = 1;
        User._gameData_csryw.moneyNum = 1000;
        User._gameData_csryw.gameTime = 0;
        User._gameData_csryw.useWeapon = "1";
        User._gameData_csryw.weapons = ["1"];
    }


    public static initiUser_csryw(data) {
        console.log("*****************************  User initUser  **************************************  ");
        console.log(data);
        if (data && 0 != data) {
            User._gameData_csryw.levelNum = User.verify(data.levelNum, 0);
            User._gameData_csryw.moneyNum = User.verify(data.moneyNum, 0);
            PhysicalPowerMgr.initData(User.verify(data.physicalData, {}))
            //增加自己的数据
            User._gameData_csryw.gameTime = User.verify(data.gameTime, 0);
            User._gameData_csryw.weapons = data.weapons ?? ["1"];
            User._gameData_csryw.useWeapon = data.useWeapon ?? "1";
            User._gameData_csryw.showSharedTime = User._gameData_csryw.gameTime;
            User._gameData_csryw.killMonster = data.killMonster ?? {};
        }
        else {
            //todo：处理没有获取到玩家数据的情况
        }
    }

    public static setLeveNum_csryw(levelNum: number) {
        User._gameData_csryw.levelNum = levelNum;
    }

    public static getLeveNum_csryw(): number {
        return User._gameData_csryw.levelNum;
    }

    public static addMoney_csryw(add: number) {
        add = Math.ceil(add)
        var last = User._gameData_csryw.moneyNum
        User._gameData_csryw.moneyNum += add;
        EventMgr.emitEvent_csryw(ryw_Event.ryw_Game_OnUserMoneyChange_csryw,
            {
                curr: User._gameData_csryw.moneyNum,
                last: last
            })
    }
    public static subMoney_csryw(sub: number) {
        sub = Math.ceil(sub)
        var last = User._gameData_csryw.moneyNum
        User._gameData_csryw.moneyNum -= sub;
        if (User._gameData_csryw.moneyNum < 0) {
            User._gameData_csryw.moneyNum = 0;
        }
        EventMgr.emitEvent_csryw(ryw_Event.ryw_Game_OnUserMoneyChange_csryw,
            {
                curr: User._gameData_csryw.moneyNum,
                last: last
            })
    }
    public static getMoney_csryw() {
        return User._gameData_csryw.moneyNum;
    }

    public static addGameTime(): void {
        User._gameData_csryw.gameTime++;
        GameMgr.getInstance_csryw().saveGameData_csryw();
    }

    public static getGameTime(): number {
        return User._gameData_csryw.gameTime;
    }

    public static addWeapon(id: string): void {
        if (this.checkHasWeapon(id)) {
            return;
        }
        User._gameData_csryw.weapons.push(id);
        User._gameData_csryw.useWeapon = id;
        EventMgr.emitEvent_csryw(ryw_Event.weaponRefresh);
        GameMgr.getInstance_csryw().saveGameData_csryw();
    }

    public static checkHasWeapon(id: string): boolean {
        var index = User._gameData_csryw.weapons.indexOf(id);
        return index != -1;
    }

    public static useWeapon(id: string): void {
        if (!this.checkHasWeapon(id)) {
            return;
        }
        User._gameData_csryw.useWeapon = id;
        EventMgr.emitEvent_csryw(ryw_Event.weaponRefresh);
        GameMgr.getInstance_csryw().saveGameData_csryw();
    }

    public static get useWeaponId(): string {
        return User._gameData_csryw.useWeapon;
    }
    public static get showShareTime(): number {
        return User._gameData_csryw.showSharedTime;
    }

    public static updateNoSharedTime(): void {
        User._gameData_csryw.showSharedTime = this._gameData_csryw.gameTime;
        GameMgr.getInstance_csryw().saveGameData_csryw();
    }

    public static addKillMonster(monsterId: string): void {
        var count = this.getKillMonsterCount(monsterId);
        User._gameData_csryw.killMonster[monsterId] = count + 1;
    }

    public static getKillMonsterCount(monsterId: string): number {
        return User._gameData_csryw.killMonster[monsterId] ?? 0;
    }

    /**
     * 数据校验
     * @param data 校验的数据
     * @param defaultValue 默认值
     */
    public static verify(data: any, defaultValue: any) {
        if (data !== undefined) {
            return data;
        }
        return defaultValue;
    }

}

