import QQMiniGameAPI from "../../Platform/qq/QQMiniGameAPI";
import WXAPI from "../../Platform/weixin/WXAPI";
import AppSwitchConfig from "../Config/AppSwitchConfig";
import { ryw_Event } from "../Event/EventEnum";
import EventMgr from "../Event/EventMgr";
import HttpUnit from "../NetWork/HttpUnit";
import AppPlatform from "../Util/AppPlatform";

export default class WudianMgr {
    private static ipBlockFlag_csryw: number = -1;
    public static ryw_IpBlockFlag_csryw(): number {
        return WudianMgr.ipBlockFlag_csryw;
    }
    /**
     * 此方法调用很慢，所以要在游戏初始化的时候调用一次此方法
     * 
     * @static
     * @memberof WudianMgr
     */
    public static UpdateIpBlockState_csryw(completed?: Function) {
        HttpUnit.GetIpBlock_csryw(
            function (res) {
                console.log("调用IpBlock接口成功,结果为:", res);
                WudianMgr.ipBlockFlag_csryw = res.code;
                EventMgr.emitEvent_csryw(ryw_Event.ryw_App_OnUpdateIpBlockState_csryw, { ipBlockFlag: WudianMgr.ipBlockFlag_csryw });
                completed && completed();
            },
            null
        )
    }
    /**
     * IP是否被屏蔽
     * 
     * @static
     * @returns {boolean} 
     * @memberof WudianMgr
     */
    public static ryw_GetIpBlocked_csryw(): boolean {
        return WudianMgr.ipBlockFlag_csryw == 0;
    }
    // /**
    //  * 得到用户进入的场景值
    //  * 
    //  * @static
    //  * @returns {number} 
    //  * @memberof WudianMgr
    //  */
    // public static GetEntryScene_csryw(): boolean {
    //     return WXAPI.getLaunchOptionsSync_csryw().scene == 1006;
    // }
    /**
     * 误点开关是否打开，包括了总开关和分时开关
     * 
     * @static
     * @returns {boolean} 
     * @memberof WudianMgr
     */
    public static ryw_IsSwitchOpen_csryw(): boolean {
        let mainSwitch_csryw = AppSwitchConfig.getInstance_csryw().getAppSwitchData_csryw().wudian_csryw == 1;
        let isOpenTime_csryw = AppSwitchConfig.getInstance_csryw().getAppSwitchData_csryw().wudianTimeAvaliable_csryw;
        console.log("误点 Flag_csryw 状态: ", "总开关:", mainSwitch_csryw, "打开时间", isOpenTime_csryw);
        return mainSwitch_csryw && isOpenTime_csryw;
    }


    /**
     * 完全封装好的误点Flag  去掉了IP屏蔽
     * 
     * @readonly
     * @static
     * @type {boolean}
     * @memberof WudianMgr
     */
    public static get wudianFlag_noIpBlock_csryw(): boolean {
        let mainSwitch_csryw = AppSwitchConfig.getInstance_csryw().getAppSwitchData_csryw().wudian_csryw == 1;
        let isOpenTime_csryw = AppSwitchConfig.getInstance_csryw().getAppSwitchData_csryw().wudianTimeAvaliable_csryw;
        //let ipnotBlock = this.ipBlockFlag_csryw == 0;
        let launchScene_csryw = AppPlatform.getLaunchOptionsSync_csryw().scene;

        if (AppPlatform.is_TT_GAME_csryw()) {
            mainSwitch_csryw = true;
            isOpenTime_csryw = true;
        }

        let noEnterBySearch_csryw: boolean = true;
        let wudianSceneList_csryw = AppSwitchConfig.getInstance_csryw().getAppSwitchData_csryw().wudianSceneList_csryw;
        for (let i = 0; i < wudianSceneList_csryw.length; ++i) {
            var wudianSceneValue = wudianSceneList_csryw[i];
            if (launchScene_csryw == wudianSceneValue) {
                noEnterBySearch_csryw = false;
            }
        }
        /* 测试功能，等删 */
        // ipnotBlock = true;
        console.log("误点 Flag_csryw 状态: ", "总开关:", mainSwitch_csryw, "场景进入", noEnterBySearch_csryw, "打开时间",
            isOpenTime_csryw);

        return mainSwitch_csryw && noEnterBySearch_csryw && isOpenTime_csryw;
    }


    /**
     * 完全封装好的误点Flag
     * 
     * @readonly
     * @static
     * @type {boolean}
     * @memberof WudianMgr
     */
    public static get wudianFlag_csryw(): boolean {
        let mainSwitch_csryw = AppSwitchConfig.getInstance_csryw().getAppSwitchData_csryw().wudian_csryw == 1;
        let isOpenTime_csryw = AppSwitchConfig.getInstance_csryw().getAppSwitchData_csryw().wudianTimeAvaliable_csryw;
        let ipnotBlock_csryw = this.ipBlockFlag_csryw == 0;
        let launchScene_csryw = AppPlatform.getLaunchOptionsSync_csryw().scene;

        if (AppPlatform.is_TT_GAME_csryw()) {
            mainSwitch_csryw = true;
            isOpenTime_csryw = true;
        }

        let noEnterBySearch_csryw: boolean = true;
        let wudianSceneList_csryw = AppSwitchConfig.getInstance_csryw().getAppSwitchData_csryw().wudianSceneList_csryw;
        for (let i = 0; i < wudianSceneList_csryw.length; ++i) {
            let wudianSceneValue = wudianSceneList_csryw[i];
            if (launchScene_csryw == wudianSceneValue) {
                noEnterBySearch_csryw = false;
            }
        }
        /* 测试功能，等删 */
        // ipnotBlock = true;
        console.log("误点 Flag_csryw 状态: ", "总开关:", mainSwitch_csryw, "场景进入", noEnterBySearch_csryw, "IP未被屏蔽", ipnotBlock_csryw, "打开时间",
            isOpenTime_csryw);

        return mainSwitch_csryw && noEnterBySearch_csryw && ipnotBlock_csryw && isOpenTime_csryw;
    }
    /**
     * 没有涉及到定时开关的wudianFlag,自行整合按照时间开关的效果
     * 
     * @static
     * @returns {boolean} 
     * @memberof WudianMgr
     */
    public static get NoTimeWudianFlag_csryw(): boolean {
        let mainSwitch_csryw = AppSwitchConfig.getInstance_csryw().getAppSwitchData_csryw().wudian_csryw == 1;
        let launchScene_csryw = AppPlatform.getLaunchOptionsSync_csryw().scene;

        let noEnterBySearch_csryw: boolean = true;
        let wudianSceneList_csryw = AppSwitchConfig.getInstance_csryw().getAppSwitchData_csryw().wudianSceneList_csryw;
        for (let i = 0; i < wudianSceneList_csryw.length; ++i) {
            let wudianSceneValue_csryw = wudianSceneList_csryw[i];
            if (launchScene_csryw == wudianSceneValue_csryw) {
                noEnterBySearch_csryw = false;
            }
        }
        let ipnotBlock_csryw = WudianMgr.ipBlockFlag_csryw == 0;
        /* 测试功能，等删 */
        // ipnotBlock = true;
        console.log("误点 Flag_csryw 状态: ", "总开关:", mainSwitch_csryw, "场景进入", noEnterBySearch_csryw, "IP未被屏蔽");
        return mainSwitch_csryw && noEnterBySearch_csryw && ipnotBlock_csryw;
    }


    public static get isEnterBySerach_csryw(): boolean {

        let launchScene = null;
        if (AppPlatform.is_WECHAT_GAME_csryw()) {
            launchScene = WXAPI.getLaunchOptionsSync_csryw().scene;
        }
        else if (AppPlatform.is_QQ_PLAY_csryw()) {
            launchScene = QQMiniGameAPI.getLaunchOptionsSync_csryw().scene;
        }

        let noEnterBySearch: boolean = true;
        var wudianSceneList = [1011, 1012, 1013, 1047]
        for (var i = 0; i < wudianSceneList.length; ++i) {
            var wudianSceneValue = wudianSceneList[i];
            if (launchScene == wudianSceneValue) {
                noEnterBySearch = false;
            }
        }

        console.log("场景进入", noEnterBySearch);
        return noEnterBySearch;
    }
}
