import DialogMgr from "../../../FrameWork/Mgr/DialogMgr";
import User from "../../../FrameWork/User/User";
import TTAPI from "../../../Platform/tt/TTAPI";
import AudioManager from "../manager/AudioManager";
import GamaManager from "../manager/GamaManager";

const {ccclass, property} = cc._decorator;

@ccclass
export default class SettlementView extends cc.Component {
    @property(cc.Node)
    public winNode: cc.Node = null;
    
    @property(cc.Node)
    public failNode: cc.Node = null;

    @property(cc.Label)
    public moneyLabel: cc.Label = null;

    protected start(): void {
        this.winNode.active = GamaManager.inst.win;
        this.failNode.active = !GamaManager.inst.win;
        this.moneyLabel.string = GamaManager.inst.currentMoney + '';
        AudioManager.inst.pauseBGM();
        AudioManager.inst.playSFX("Sound/" + (GamaManager.inst.win ? "gameWin" : "gameFail"));
    }

    private onClickVideo(): void {
        var self = this;
        TTAPI.showRewardedVideoAd_csryw("双倍奖励", (finish) => {
            if (finish) {
                AudioManager.inst.playSFX("Sound/结算界面获得金币");
                User.addMoney_csryw(GamaManager.inst.currentMoney);
                cc.director.loadScene("FMMainScene", () => {
                    DialogMgr.openToast("双倍金币领取成功");
                });
            }
        }, () => {

        })
    }

    private onClickShared(): void {
        TTAPI.shareRecord_csryw();
        TTAPI.reportAnalytics("sharedVideo");
    }

    private onClickBack(): void {
        cc.director.loadScene("FMMainScene");
        AudioManager.inst.playSFX("Sound/结算界面获得金币");
    }
}
