const {ccclass, property, requireComponent, executionOrder} = cc._decorator;

@ccclass
@requireComponent(cc.Graphics)
@executionOrder(-99999)
export default class Gizmos extends cc.Component {
    private _graphics: cc.Graphics = null;

    public static isDebug: boolean = true;

    private static _instance: Gizmos = null;

    onLoad(): void {
        Gizmos._instance = this;
        this._graphics = this.getComponent(cc.Graphics);
    }

    public static DrawRay1(p1: cc.Vec2, p2: cc.Vec2, color: cc.Color = cc.Color.RED) {
        if (!this.isDebug) return;
        this._instance._graphics.moveTo(p1.x, p1.y);
        this._instance._graphics.lineTo(p2.x, p2.y);
        let orginColor = this._instance._graphics.strokeColor;
        this._instance._graphics.strokeColor = color;
        this._instance._graphics.stroke();
        this._instance._graphics.strokeColor = orginColor;
    }

    private static _tempVec: cc.Vec2 = new cc.Vec2;
    public static DrawRay2(origin: cc.Vec2, direction: cc.Vec2, distance: number, color: cc.Color = cc.Color.RED) {
        if (!this.isDebug) return;
        let tempVec = Gizmos._tempVec;
        tempVec.set(direction);
        tempVec.mulSelf(distance);
        tempVec.addSelf(origin);
        this.DrawRay1(origin, tempVec, color);

    }

    public static DrawCirle(origin: cc.Vec2, r: number, color: cc.Color = cc.Color.RED): void {
        if (!this.isDebug) return;
        let graphics = this._instance._graphics;
        graphics.circle(origin.x, origin.y, r);
        let orginColor = this._instance._graphics.strokeColor;
        this._instance._graphics.strokeColor = color;
        this._instance._graphics.stroke();
        this._instance._graphics.strokeColor = orginColor;
    }

    public static DrawRect(x: number, y: number, w: number, h: number, color: cc.Color = cc.Color.RED): void {
        if (!this.isDebug) return;
        let graphics = this._instance._graphics;
        graphics.rect(x, y, w, h);
        let orginColor = this._instance._graphics.strokeColor;
        this._instance._graphics.strokeColor = color;
        this._instance._graphics.stroke();
        this._instance._graphics.strokeColor = orginColor;
    }

    update(): void {
        this._graphics.clear();
    }
}