import FixManager from "../../../common/fixcomponent/FixManager";
import BoxCollision from "../../collision/BoxCollision";
import CollisitionManager from "../../manager/CollisitionManager";
import GamaManager from "../../manager/GamaManager";
import AIBase from "./AIBase";
const { ccclass, property } = cc._decorator;

@ccclass
export default class SpringAI extends AIBase {
    @property(BoxCollision)
    public boxCollision: BoxCollision;

    private characterInRange: cc.Node[] = [];

    protected tryCastSKill(): boolean {
        var attack = super.tryCastSKill();
        return attack;
    }

    public fixUpdate(): void {
        super.fixUpdate();
        
        var dt = FixManager.fixedDeltaTime;
        if (this.character.unitAction.currentAction.key == "Attack") {
            if (GamaManager.inst.debugHitBox) {
                this.boxCollision.draw();
            }

            var cha = CollisitionManager.inst.collisition(this.boxCollision, "hero");
            
            for (var i = this.characterInRange.length - 1; i >= 0; i--) {
                if (!cha.includes(this.characterInRange[i])) {
                    this.characterInRange.splice(i, 1);
                }
            }

            for(var i = 0; i < cha.length; i++) {
                if (this.node != cha[i] && !this.characterInRange.includes(cha[i])) {
                    this.characterInRange.push(cha[i])
                    GamaManager.inst.doDamage(this.character.node, cha[i]);
                }
            }
        } else {
            this.characterInRange.length = 0;
        }
    }
}