import User from "../../../FrameWork/User/User";
import TTAPI from "../../../Platform/tt/TTAPI";
import GamaManager from "../manager/GamaManager";

const {ccclass, property} = cc._decorator;

@ccclass
export default class ResurrectionView extends cc.Component {
    @property(cc.Node)
    public resurrectBtn: cc.Node;

    @property(cc.Node)
    public sharedBtn: cc.Node;

    protected onLoad(): void {
        var needShared = (User.getGameTime() - User.showShareTime) > 2;
        this.resurrectBtn.active = !needShared;
        this.sharedBtn.active = needShared;
        if (needShared) {
            TTAPI.reportAnalytics("sharedVideoShow");
            User.updateNoSharedTime();
        }
    }

    onClickResurrection(): void {
        var self = this;
        TTAPI.showRewardedVideoAd_csryw("复活", (finish) => {
            if (finish) {
                GamaManager.inst.resurrect(true);
                self.node.destroy();
            }
        }, () => {

        });
    }

    onClickNo(): void {
        GamaManager.inst.resurrect(false);
        this.node.destroy();
    }

    onClickShared(): void {
        var self = this;
        
        TTAPI.ShowLoading();
        TTAPI.stopRecord_csryw(() => {
            TTAPI.HideLoading();
            GamaManager.inst.tryStartRecord();
            TTAPI.shareRecord_csryw(() => {
                TTAPI.reportAnalytics("sharedVideo");
                GamaManager.inst.resurrect(true);
                self.node.destroy();
            });
        });
    }
}