    public static _cachedNativeAd_csryw: any = null;
    public static _cachedAdItem_csryw: any = null;
    public static _cachedimgUrl_csryw: string = null;
    public static _tryLoadCount_csryw: number = 5;



    public static LoadCahcedNativeAd_csryw(): void {
        return;
        if (OPPOAPI._cachedNativeAd_csryw) {
            OPPOAPI._cachedNativeAd_csryw.destroy();
            OPPOAPI._cachedNativeAd_csryw = null;
        }
        OPPOAPI._cachedAdItem_csryw = null;
        OPPOAPI._cachedNativeAd_csryw = window["qg"].createNativeAd({
            posId: OPPOAPI.NativeAdId_csryw
        })
        OPPOAPI._cachedNativeAd_csryw.load();
        ++OPPOAPI._tryLoadCount_csryw;
        console.log("缓存 原生广告 开始加载");

        let self = this;

        OPPOAPI._cachedNativeAd_csryw.onLoad((res) => {
            console.log("缓存 原生广告 加载成功：", res);
            var adlist = res.adList;
            for (var i = 0; i < adlist.length; ++i) {
                var ad = adlist[i];
                console.log("缓存 原生广告 数据：", i);
                for (var key in ad) {
                    console.log(key, ad[key]);
                }
            }
            OPPOAPI._cachedAdItem_csryw = adlist[Math.floor(Math.random() * adlist.length)];
            if (null != OPPOAPI._cachedAdItem_csryw) {
                for (var i = 0; i < OPPOAPI._cachedAdItem_csryw.imgUrlList.length; ++i) {
                    console.log("缓存 原生广告 imgUrlList : ", i + " ", OPPOAPI._cachedAdItem_csryw.imgUrlList[i])
                }
                let imgUrlList = OPPOAPI._cachedAdItem_csryw.imgUrlList;
                for (let i = 0; i < imgUrlList.length; ++i) {
                    let imgUrl = imgUrlList[i];
                    OPPOAPI._cachedimgUrl_csryw = imgUrl;
                    if (null != OPPOAPI._cachedimgUrl_csryw && "" != OPPOAPI._cachedimgUrl_csryw) {
                        break;
                    }
                }
                if (null != OPPOAPI._cachedimgUrl_csryw && "" != OPPOAPI._cachedimgUrl_csryw) {
                    console.log("缓存 原生广告  加载图片", OPPOAPI._cachedimgUrl_csryw);
                }
                else {
                    console.log("缓存 原生广告 加载失败 imgulr is : ", OPPOAPI._cachedimgUrl_csryw);
                    OPPOAPI._cachedNativeAd_csryw.destroy();
                    OPPOAPI._cachedNativeAd_csryw = null;
                    setTimeout(() => {
                        OPPOAPI.LoadCahcedNativeAd_csryw();
                    }, 2500);
                }
            }
        })
        OPPOAPI._cachedNativeAd_csryw.onError((res) => {
            console.log("缓存 原生广告 加载失败：", res);
            for (var key in res) {
                console.log(key, res[key]);
            }
            OPPOAPI._cachedNativeAd_csryw.destroy();
            OPPOAPI._cachedNativeAd_csryw = null;
            setTimeout(() => {
                OPPOAPI.LoadCahcedNativeAd_csryw();
            }, 5000);
        })
    }

    /**
     * 判断是否创建桌面图标
     * @param onSuccess 
     * @param onFail 
     */
    public static hasShortcutInstalled(onSuccess: Function, onFail: Function) {
        window["qg"].hasShortcutInstalled({
            success: function (res) {
                if (res == false) {
                    console.log("桌面图标不存在！！！！");
                    if (onSuccess) {
                        onSuccess(false);
                    }
                }
                else {
                    console.log("桌面图标已存在！！！！");
                    if (onSuccess) {
                        onSuccess(true);
                    }
                }
            },
            fail: function (err) {
                if (onFail) {
                    onFail();
                }
                console.log("判断桌面图标是否存在失败！！！", err);
                for (var key in err) {
                    console.log(key, err);
                }
            },
            complete: function () {

            }
        })
    }

