export default class CharacterInfo {
    name: string = "";
    group: string = "";
    nameIcon: string = "";
    prefab: string = "";
    hp: number = 0;
    attack: number = 0;
    cirt: number = 0;
    range: number = 0;
    moveSpeed: number = 0;
    catapultForce: number = 0;
    bodyRadius: number = 0;
    actions: string;
    skills: string[];
    hit: string = "";
    type: number = 0;
    hurtSound: string = "";
    deadEffect: string = "";
    deadSound: string = "";
    dropMoney: number = 0;

    constructor (name: string, group: string, prefab: string,
        hp: number, attack: number, cirt: number, range: number, moveSpeed: number, 
        catapultForce: number, bodyRadius: number, anim: string, skills: string[]) {
            this.name = name;
            this.group = group;
            this.prefab = prefab;
            this.hp = hp;
            this.attack = attack;
            this.cirt = cirt;
            this.range = range;
            this.moveSpeed = moveSpeed;
            this.catapultForce = catapultForce;
            this.bodyRadius = bodyRadius;
            this.actions = anim;
            this.skills = skills;
    }
}