export class PropertyModifier {
    private m_ModifierMap: { [id: string]: PropertyProvider[] } = {};

    public addProvider(provider: PropertyProvider): void {
        var name = provider.property;
        var list = this.m_ModifierMap[name];
        if (list == null) {
            list = [];
            this.m_ModifierMap[name] = list;
        }
        list.push(provider);
    }

    public removeProvider(provider: PropertyProvider): void {
        var name = provider.property;
        var list = this.m_ModifierMap[name];
        if (list == null) {
            return;
        }
        var index = list.indexOf(provider);
        if (index != -1) {
            list.splice(index, 1);
        }
    }

    public updateProperty(name: string, source: number): number {
        var list = this.m_ModifierMap[name];
        if (list == null) {
            return 0;
        }

        let constantValue = 0, percentageValue = 0;
        list.forEach(provider => {
            constantValue += provider.constant;
            percentageValue += provider.percentage;
        });

        return (source + constantValue) * (1 + percentageValue);
    }
}

export class PropertyProvider {
    public property: string;

    public constant: number;

    public percentage: number

    constructor(name: string, value: number, percentage: number) {
        this.property = name;
        this.constant = value;
        this.percentage = percentage;
    }
}