import TTAPI from "../../../../Platform/tt/TTAPI";
import AudioManager from "../../../../scripts/battle/manager/AudioManager";
import DialogMgr from "../../../Mgr/DialogMgr";
import PhysicalPowerMgr from "../../../Mgr/PhysicalPowerMgr";
import User from "../../../User/User";

const { ccclass, property } = cc._decorator;

@ccclass
export default class FMMainScene extends cc.Component {
    public touch: cc.Node = null;
    public butStart: cc.Node = null;

    protected onLoad() {
        this.touch = this.node.getChildByName("touch");
        this.touch.on(cc.Node.EventType.TOUCH_START, this.onTouchStart, this);

        let butSet = this.node.getChildByName("butSet");
        butSet.on("click", () => {
            DialogMgr.openSettingDialog(handleFM_csryw(() => { }, this))
        }, this)

        this.butStart = this.node.getChildByName("butStart");
        this.butStart.on("click", () => {
            User.addGameTime();
            TTAPI.reportAnalytics("gameStart");
            this.butStart.getComponent(cc.Button).interactable = false;
            AudioManager.inst.playSFX("Sound/切西瓜");
            this.butStart.getComponentInChildren(sp.Skeleton).setAnimation(0, "play", false);
            this.scheduleOnce(() => {
                this.getComponent(cc.Animation).play("CloseMask");
            }, 0.3);
            return;
            if (PhysicalPowerMgr.isCanUserPhysical()) {
                console.log("扣除体力 进游戏")
                cc.director.loadScene("battleScene");
            } else {
                PhysicalPowerMgr.openAddPowerDialog(handleFM_csryw((finish) => {
                    if (finish) {
                        console.log("体力添加成功 继续下一个操作")
                    } else {
                        console.log("体力添加失败，取消相关操作")
                    }
                }, this))
            }
        }, this)

        let butToast = this.node.getChildByName("butToast");
        butToast.on("click", () => {
            DialogMgr.openToast("这是提示语")
        }, this)


        let butWeapon = this.node.getChildByName("butWeapon");
        butWeapon.on("click", () => {
            DialogMgr.openWeapon(this.node);
        }, this);
    }

    protected start(): void {
        AudioManager.inst.playBGM("Sound/" + "battleBgm");
    }

    private onTouchStart(): void {

    }

    private onCloseComplete(): void {
        cc.director.loadScene("battleScene");
    }
}
