export default class AABB {
    type: number = cc.geomUtils.enums.SHAPE_AABB;
    center: cc.Vec2 = new cc.Vec2;
    halfExtents: cc.Vec2 = new cc.Vec2;

    get minX(): number {
        return this.center.x - this.halfExtents.x;
    }

    get minY(): number {
        return this.center.y - this.halfExtents.y;
    }

    get maxX(): number {
        return this.center.x + this.halfExtents.x;
    }

    get maxY(): number {
        return this.center.y + this.halfExtents.y;
    }
}