import FixComponent from "../../common/fixcomponent/FixComponent";
import FixManager from "../../common/fixcomponent/FixManager";
import Gizmos from "../../tools/Gizmos";
import AABB from "../../tools/objects/AABB";
import Collision from "../collision/Collision";
import CollisitionManager, { IBodyCollisition } from "../manager/CollisitionManager";
import GamaManager from "../manager/GamaManager";
import ChaProperty from "../structs/battle/ChaProperty";
import UnitMovement from "../unit/UnitMovement";
import ActionInfo from "../unit/action/ActionInfo";
import UnitAction from "../unit/action/UnitAction";
import { BehaviorFlag } from "./Behavior";
import CharacterActionConsumer from "./CharacterActionConsumer";
import IMovePreorder from "./movement/IMoveProvider";
import BuffObj from "../structs/battle/buff/BuffObj";
import BuffConfig from "../structs/battle/buff/BuffConfig";
import ConfigManager from "../../ConfigManager";
import { BuffTable } from "../designer/table/BuffTable";
import EffectManager from "../manager/EffectManager";
import CharacterInfo from "../structs/character/CharacterInfo";
import { PropertyModifier, PropertyProvider } from "../structs/PropertyModifier";
import DamagePopup from "../../tools/DamagePopup";
import { DamageResult } from "../structs/battle/DamageInfo";
import AudioManager from "../manager/AudioManager";

const { ccclass, property, executionOrder } = cc._decorator;

@ccclass
export default class Character extends FixComponent implements IBodyCollisition {
    public config: CharacterInfo;

    public group: string;

    public movement: UnitMovement;
    public unitAction: UnitAction;
    public actionConsumer: CharacterActionConsumer;

    public controlFlag: BehaviorFlag = BehaviorFlag.all;
    public actionControlFlag: BehaviorFlag = BehaviorFlag.all;

    public directionX: number = 1;
    public velocity: cc.Vec2 = new cc.Vec2;
    public moving: boolean = false;
    public moveProviders: IMovePreorder[] = [];

    private waitActions: string[] = [];

    private body: cc.Node;
    private hitBox: cc.Node;

    public isDead: boolean;

    public hp: number = 0;

    public maxHp: number = 0;

    public attack: number = 0;

    public cirt: number = 0;

    public moveSpeed: number = 0;

    public catapultForce: number = 0;

    public bodyRadius: number = 0;

    public range: number = 0;

    public energy: number = 0;

    public critDamageMulti: number = 0;

    public critDamageOffset: number = 0;

    public shield: number = 0;

    public shieldImmune: number = 0;

    private _realPropertyList = {};
    private _propertyModifier: PropertyModifier = new PropertyModifier;

    public get hpPercentage(): number {
        return Math.max(0, this.hp / this.getProperty("maxHp"));
    }

    public buffs: BuffObj[] = [];

    public lastHurtDirection: cc.Vec2 = new cc.Vec2;

    protected onLoad(): void {
        this.movement = this.getComponent(UnitMovement);
        this.unitAction = this.getComponent(UnitAction);
        this.actionConsumer = this.getComponent(CharacterActionConsumer);

        if (this.unitAction && this.actionConsumer) {
            this.unitAction.setActionConsumer(this.actionConsumer);
        }

        this.body = this.node.getChildByName("Body");
        this.hitBox = this.node.getChildByName("HitBox");
    }

    protected onEnable(): void {
        super.onEnable();
    }

    protected onDisable(): void {
        super.onDisable();
    }

    public checkAllow(flag: BehaviorFlag): boolean {
        var curFlag = this.controlFlag & this.actionControlFlag;
        return (curFlag & flag) == flag;
    }

    public fixUpdate(): void {
        var dt = FixManager.fixedDeltaTime;
        if (!this.isDead) {
            var toRemove: BuffObj[] = [];
            for (let i = 0; i < this.buffs.length; i++) {
                if (this.buffs[i].config.permanent == false) {
                    this.buffs[i].duration -= dt;
                }
                this.buffs[i].timeElapsed += dt;

                this.buffs[i].onTick(dt);
                this.buffs[i].ticked += 1;

                // if (this.buffs[i].config.tickTime > 0){
                //     //float取模不精准，所以用x1000后的整数来
                //     if (Math.round(this.buffs[i].timeElapsed * 1000) % Math.round(this.buffs[i].config.tickTime * 1000) == 0){
                //         this.buffs[i].onTick();
                //         this.buffs[i].ticked += 1;
                //     }
                // }

                //只要duration <= 0，不管是否是permanent都移除掉
                if (this.buffs[i].config.permanent == false && this.buffs[i].duration <= 0 || this.buffs[i].stack <= 0){
                    this.buffs[i].onRemoved();
                    toRemove.push(this.buffs[i]);
                }
            }
            if (toRemove.length > 0){
                for (var i = 0; i < toRemove.length; i++){
                    var index = this.buffs.indexOf(toRemove[i]);
                    this.buffs.splice(index, 1);
                }
            }
            toRemove = null;

            var canMove = this.checkAllow(BehaviorFlag.move);
            if (canMove) {
                for (let i = 0; i < this.moveProviders.length;) {
                    var provider = this.moveProviders[i];
                    if (provider.finished) {
                        this.moveProviders.splice(i, 1);
                    } else {
                        i++
                        this.velocity.addSelf(provider.updateMovement(dt));
                    }
                }
                this.movement.move(this.velocity);
                this.moving = this.velocity.lengthSqr() > 1e-5;
                this.velocity.set(cc.Vec2.ZERO);
            }
    
            if (this.node.scaleX != this.directionX) {
                this.node.scaleX = this.directionX == 0 ? 1 : this.directionX;
            }
    
            var canDoAction = this.checkAllow(BehaviorFlag.doAction);
            if (canDoAction) {
                var tryRun = canMove && this.moving;
                if (tryRun == false) {
                    this.waitActions.push("Idle");
                }else{
                    this.waitActions.push("Move");
                }
                for (var i = 0; i < this.waitActions.length; i++){
                    this.unitAction.playAction(this.waitActions[i]);
                }
                this.waitActions.length = 0;
            }
        } else {
            this.moving = false;
        }

        if (GamaManager.inst.debugHitBox) {
            var shape = this.getShape();
            Gizmos.DrawRect(shape.center.x - shape.halfExtents.x, shape.center.y - shape.halfExtents.y, shape.halfExtents.x * 2, shape.halfExtents.y * 2);
        }
    }

    public InitProperty(property: ChaProperty): void {
        this.maxHp = property.hp;
        this.attack = property.attack;
        this.cirt = property.cirt;
        this.moveSpeed = property.moveSpeed;
        this.catapultForce = property.catapultForce;
        this.bodyRadius = property.bodyRadius;
        this.range = property.range;
        this.critDamageMulti = 2;
        this.critDamageOffset = 0;

        for (var key in this._realPropertyList) {
            this.updateReadProperty(key);
        }

        this.hp = this.getProperty("maxHp");
    }

    public setActions(actions: ActionInfo[]): void {
        if (this.unitAction == null) {
            return;
        }

        this.unitAction.setAction(actions);
    }

    public doAction(name: string): void {
        if (!this.checkAllow(BehaviorFlag.doAction)) {
            return;
        }
        this.waitActions.push(name);
    }

    public moveByDirection(direction: cc.Vec2): void {
        this.velocity.set(direction);
        this.velocity.mulSelf(this.moveSpeed);
    }

    public addMoveProiver(proiver: IMovePreorder): void {
        if (this.moveProviders.includes(proiver)) {
            return;
        }

        this.moveProviders.push(proiver);
    }

    public setDirection(sign: number): void {
        this.directionX = Math.sign(sign);
    }

    public getHitBox(name: string): Collision {
        if (this.hitBox == null) {
            return;
        }
        var node = this.hitBox.getChildByName(name);
        return node && (node.getComponent(Collision) ?? node.getComponentInChildren(Collision));
    }

    public beHurt(result: DamageResult): void {
        if (this.isDead == true) {
            return;
        }

        if (this.shieldImmune > 0) {
            this.shieldImmune = 0
            this.node.emit("IMMUNE_BREA_BROKEN");
            return;
        }

        var damage = result.damage;
        if (this.shield > 0) {
            this.shield -= damage;
            damage = 0;
            if (this.shield < 0) {
                this.shield = 0;
                damage = -this.shield;
                this.node.emit("SHIELD_BREA_BROKEN");
            }
        }

        this.hp -= damage;
        if (this.hp <= 0) {
            this.hp = 0;
            this.dead();
        }

        this.lastHurtDirection.set(result.direction);
        if (this.config.hit != null && this.config.hit != "") {
            EffectManager.inst.addEffect(this.config.hit, this.node, cc.v2(this.node.x, this.node.y), result.direction);
        }
        EffectManager.inst.addEffect(result.isCirt ? "CirtHurtPopup" : "NormalHurtPopup", this.node, cc.v2(this.node.x, this.node.y), {
            damage: damage,
        }, GamaManager.inst.numberLayer);
    }

    public hit(target: Character): void {
        
    }

    public heal(value: number): void {
        this.hp += value;
        this.hp = Math.min(this.hp, this.getProperty("maxHp"));
    }

    public addShield(value: number): void {
        this.shield += value;
        this.node.emit("ADD_SHIELD");
    }

    public addImmune(): void {
        this.shieldImmune = 1;
        this.node.emit("ADD_IMMUNE");
    }

    public dead(): void {
        this.isDead = true;
        this.deadRemove();
    }

    protected deadRemove(): void {
        this.unitAction.playAction("Dead");
        CollisitionManager.inst.removeBody(this);
        if (this.config.deadEffect != null && this.config.deadEffect != "") {
            this.node.children[0].active = false;
            EffectManager.inst.addEffect("Effect_Dead_" + this.config.prefab, this.node, cc.v2(this.node.x, this.node.y));
        }
        if (this.config.deadSound != null && this.config.deadSound != "") {
            AudioManager.inst.playSFX("Sound/" + this.config.deadSound);
        }
        this.scheduleOnce(() => this.node.destroy(), 2);
    }

    public getBuffById(id: string): BuffObj {
        for (var i = 0; i < this.buffs.length; i++) {
            var buff = this.buffs[i];
            if (buff.config.id == id) {
                return buff;
            }
        }
        return null;
    }

    public addBuff(id: string, addStack: number = 1): void {
        var buff = this.getBuffById(id);
        var config: BuffConfig = ConfigManager.getBuffConfig(id);
        if (config == null) {
            console.error("no find buff ", id);
            return;
        }

        var modStack = Math.min(addStack, config.maxStack);
        var toRemove = false;
        if (buff) {
            buff.duration = (buff.config.durationSetTo == true) ? buff.duration : (buff.duration + buff.duration);

            var afterAdd = buff.stack + modStack;
            modStack = afterAdd >= buff.config.maxStack ? 
                (buff.config.maxStack - buff.stack) : 
                (afterAdd <= 0 ? (0 - buff.stack) : modStack);

            buff.stack += modStack;
            toRemove = buff.stack <= 0;
        } else {
            var buffClass = BuffTable[config.class];
            if (buffClass) {
                buff = new buffClass(this.node, config, addStack);
            } else {
                buff = new BuffObj(this.node, config, addStack);
            }
            this.buffs.push(buff);
            this.buffs.sort((a, b)=>{
                return a.config.priority - b.config.priority;
            });
        }
        if (toRemove == false) {
            buff.onAdd(modStack);
        }
    }

    public removeBuff(id: string): void {
        for (var i = 0; i < this.buffs.length; i++) {
            var buff = this.buffs[i];
            if (buff.config.id == id) {
                buff.onRemoved();
                this.buffs.splice(i, 1);
                return;
            }
        }
    }

    public getProperty(name: string): number {
        var value = this._realPropertyList[name];
        if (value) {
            return this._realPropertyList[name]
        }
        return this[name] ?? 0;
    }


    protected updateReadProperty(name: string): void {
        this._realPropertyList[name] = this._propertyModifier.updateProperty(name, this[name] ?? 0);
        if (name == "maxHp") {
            this._realPropertyList[name] = Math.floor(this._realPropertyList[name]);
        }
    }

    public addBuffProperty(name: string, ...provider: PropertyProvider[]): void {
        for (var i = 0; i < provider.length; i++) {
            this._propertyModifier.addProvider(provider[i]);
        }
        this.updateReadProperty(name);
    }

    public removeBuffProperty(name: string, ...provider: PropertyProvider[]): void {
        for (var i = 0; i < provider.length; i++) {
            this._propertyModifier.removeProvider(provider[i]);
        }
        this.updateReadProperty(name);
    }

    public getShape() {
        var aabb = new AABB();
        this.body.convertToWorldSpaceAR(cc.Vec2.ZERO, aabb.center);
        aabb.halfExtents.x = this.body.width / 2;
        aabb.halfExtents.y = this.body.height / 2;
        return aabb;
    }
}