import IMovePreorder from "./IMoveProvider";

export default class DistanceMove implements IMovePreorder {
    public velocity: cc.Vec2;

    private totalTime: number;

    public duration: number;

    get finished(): boolean {
        return this.duration <= 1e-5;
    }

    public constructor(velocity: cc.Vec2, duration: number) {
        this.velocity = velocity;
        this.duration = duration;
        this.totalTime = duration;
    }

    public updateMovement(time: number): cc.Vec2 {
        if (time >= this.duration) {
            this.duration = 0;
        } else {
            this.duration -= time;
        }
        if (this.totalTime <= 0) {
            return this.velocity
        } else {
            var value = this.duration / this.totalTime;
            value = Math.max(0, 1 - value);
            return this.velocity.div(this.totalTime);
        }
    } 
}