import TTAPI from "../../../Platform/tt/TTAPI";
import GameMgr from "../../Mgr/GameMgr";
import PhysicalPowerMgr from "../../Mgr/PhysicalPowerMgr";
import DialogUIBase from "./DialogUIBase";

/*
 * @Author: YeeChan
 * @Date: 2021-12-22 18:54:23
 * @Description: 体力不足弹窗
 */
const { ccclass, property } = cc._decorator;

@ccclass
export default class DialogAddPowerUI extends DialogUIBase {
    protected initView_csryw(): void {
        super.initView_csryw();

        this.onClick(this.childNode("butLeft", this.showNode), () => {
            this.closeDialog();
        }, this)
        this.onClick(this.childNode("butRight", this.showNode), () => {
            TTAPI.showRewardedVideoAd_csryw("补充体力", (finish) => {
                if (finish) {
                    PhysicalPowerMgr.setPhysicalNum(3);
                    GameMgr.getInstance_csryw().saveGameData_csryw();
                    this.closeDialog(true);
                }
            }, () => { })
        }, this)
    }


}
