export default class WeigthsRandom {
    weights: { key: string, weight: number }[] = [];

    Delete(key: string) {
        var index = this.weights.findIndex(item => item.key == key);
        if (index != -1) {
            this.weights.splice(index, 1);
        }
    }

    Random(): string {
        let sum = 0;
        for (var key in this.weights) {
            sum += this.weights[key].weight;
        }

        let num = Math.random() * sum;
        for (var i = 0; i < this.weights.length; i++) {
            if (num < this.weights[i].weight) {
                return this.weights[i].key;
            } else {
                num -= this.weights[i].weight
            }
        }
        return this.weights[0].key;
    }
}