import RYSDK from "./RYSDK";
import { TimerUtils } from "../Mgr/TimerUtils";

export enum RYSTAT_Event {
    RYSTAT_E_INIT_csryw = "init",
    RYSTAT_E_LOGIN_csryw = "login",
    RYSTAT_E_ENTRY_SOURCE_csryw = "entrysource",
    RYSTAT_E_CLICK_AD_csryw = "clickad",
    RYSTAT_E_CLICK_ALLOW_csryw = "clickad",
}

export default class RYSTAT {
    public static readonly domain_csryw

    public SendEvent_csryw(event: string, data?: object | string, s?: Function, f?: Function) {
        if (null != RYSDK.Instance_csryw.rysdk_csryw && null != RYSDK.Instance_csryw.rysdk_csryw.ry_SendEvent) {
            RYSDK.Instance_csryw.rysdk_csryw.ry_SendEvent(event, data, null, s, f);
        }
    }

    protected reportInitFail_csryw() {
        if (null != RYSDK.Instance_csryw.rysdk_csryw && null != RYSDK.Instance_csryw.rysdk_csryw.ry_ReportFail) {
            RYSDK.Instance_csryw.rysdk_csryw.ry_ReportFail();
        }
    }

    public reportInit_csryw() {
        let tryCounter = 0;
        let s = () => { console.log("init 上报成功!!"); }
        let f = () => {
            console.log("init 上报失败!!");
            if (tryCounter >= 10) {
                console.log("init 上报重试次数超过 10次，放弃上报");
                this.reportInitFail_csryw();
            }
            else {
                ++tryCounter;
                TimerUtils.once_csryw(() => {
                    this.SendEvent_csryw(RYSTAT_Event.RYSTAT_E_INIT_csryw, null, s, f);
                    console.log("init 第 " + tryCounter + " 次重新上报");
                }, 5)
                // Laya.timer.once(5000, this, () =>  {
                //     this.SendEvent(RYSTAT_Event.RYSTAT_E_INIT,null,s,f);
                //     console.log("init 第 " + tryCounter +" 次重新上报");
                // })
            }
        }
        this.SendEvent_csryw(RYSTAT_Event.RYSTAT_E_INIT_csryw, null, s, f);
    }

    public reportLogin_csryw() {
        this.SendEvent_csryw(RYSTAT_Event.RYSTAT_E_LOGIN_csryw);
    }

    public reportEntrySource_csryw() {
        this.SendEvent_csryw(RYSTAT_Event.RYSTAT_E_ENTRY_SOURCE_csryw);
    }

    public reportClickAd_csryw(adv_id: string) {
        this.SendEvent_csryw(RYSTAT_Event.RYSTAT_E_CLICK_AD_csryw,
            {
                adv_id: adv_id,
                timelog: Date.now(),
                type: 0
            });
    }

    public reportClickAdAllow_csryw(adv_id: string) {
        this.SendEvent_csryw(RYSTAT_Event.RYSTAT_E_CLICK_ALLOW_csryw,
            {
                adv_id: adv_id,
                timelog: Date.now(),
                type: 1
            });
    }
}