/** 框架预制体*/
export enum EnumSubFrameRes {
    DialogAddPower = "DialogAddPower",
    DialogAddPowerAll = "DialogAddPowerAll",
    DialogSetting = "DialogSetting",
    DialogToast = "DialogToast",
    PowerNode = "PowerNode",
}

type subPack = { bundle?: cc.AssetManager.Bundle, subName: string, prefab: { [key: string]: cc.Prefab } };

export class ResSubMgr {
    //frame subres 资源
    private static subResFrame: subPack = {
        prefab: {}, subName: "subResFrame"
    }

    private static subResGame: subPack = {
        prefab: {}, subName: "subResGame"
    }

    /**
     * 加载一个分包完毕 做一些处理
     * @param subName 
     */
    public static loadSubpackageFinish_csryw(subName: string, listener: FMListener) {
        var subRes = this[subName] as subPack;
        if (subRes == null) {
            if (listener) {
                callFM_csryw(listener);
            }
        } else {
            subRes.bundle = cc.assetManager.getBundle(subName);
            //加载分包中的资源
            subRes.bundle.loadDir("Prefab", cc.Prefab, (error: Error, assets: Array<cc.Prefab>) => {
                for (let index = 0; index < assets.length; index++) {
                    const element = assets[index];
                    console.log(element.name);
                    subRes.prefab[element.name] = element;
                }
                if (listener) {
                    callFM_csryw(listener);
                }
            })
        }
    }

    /**
     * 获取框架分包中的资源
     * @param name 
     * @returns 
     */
    public static getPrefabBySubFrame(name: EnumSubFrameRes) {
        if (ResSubMgr.subResFrame.prefab[name] != null) {
            return ResSubMgr.subResFrame.prefab[name];
        }
        console.error("没有找到 subResFrame <", name, "> 中的预制体")
    }

    public static getPrefab(bundle: string, name: string) {
        var subRes = ResSubMgr[bundle] as subPack;
        if (subRes.prefab[name] != null) {
            return subRes.prefab[name];
        }
        // console.error("没有找到 " + bundle + " <", name, "> 中的预制体")
    }
}
