import TcoView from "../../scripts/battle/view/TcoView";
import DialogToast from "../SubResFrame/UI/DialogToast";
import DialogUIBase from "../SubResFrame/UI/DialogUIBase";
import { EnumSubFrameRes, ResSubMgr } from "./ResSubMgr";

export default class DialogMgr {
    /**弹窗zindex */
    private static readonly DIALOG_INDEX = 10;
    /**Toast zindex */
    private static readonly TOAST_INDEX = 11;

    /**
     * 显示增加单体力弹窗
     */
    public static openAddPowerDialog(listener: FMListener, parentNode?: cc.Node) {
        parentNode = parentNode || cc.director.getScene().getChildByName('Canvas');
        let prefab = ResSubMgr.getPrefabBySubFrame(EnumSubFrameRes.DialogAddPower);
        let node = cc.instantiate(prefab);
        parentNode.addChild(node, DialogMgr.DIALOG_INDEX);
        //打开弹窗
        node.getComponent(DialogUIBase).openDialog(listener);
    }

    /**
     * 显示增加无线体力弹窗
     */
    public static openAddPowerAllDialog(listener: FMListener, parentNode?: cc.Node) {
        parentNode = parentNode || cc.director.getScene().getChildByName('Canvas');
        let prefab = ResSubMgr.getPrefabBySubFrame(EnumSubFrameRes.DialogAddPowerAll);
        let node = cc.instantiate(prefab);
        parentNode.addChild(node, DialogMgr.DIALOG_INDEX);
        //打开弹窗
        node.getComponent(DialogUIBase).openDialog(listener);
    }



    /**
     * 显示设置弹窗
     */

    public static openSettingDialog(listener: FMListener, parentNode?: cc.Node, group: string = "default") {
        parentNode = parentNode || cc.director.getScene().getChildByName('Canvas');
        let prefab = ResSubMgr.getPrefabBySubFrame(EnumSubFrameRes.DialogSetting);
        let node = cc.instantiate(prefab);
        parentNode.addChild(node, DialogMgr.DIALOG_INDEX);
        //打开弹窗
        node.getComponent(DialogUIBase).openDialog(listener);
        node.group = group;
    }


    /**
     * 打开toast 提示
     * @param msg 提示语
     */
    public static openToast(msg: string, group?: string) {
        let parentNode = cc.director.getScene().getChildByName('Canvas');
        let prefab = ResSubMgr.getPrefabBySubFrame(EnumSubFrameRes.DialogToast);
        let node = cc.instantiate(prefab);
        parentNode.addChild(node, DialogMgr.TOAST_INDEX);
        //打开弹窗
        node.getComponent(DialogToast).openTip(msg);
        group && (node.group = group);
    }


    public static openSettlement(parent: cc.Node) {
        let prefab = ResSubMgr.getPrefab("subResGame", "SettlementView");
        let node = cc.instantiate(prefab);
        node.parent = parent;
    }

    public static openTco(parent: cc.Node, options: string[], callback?: Function) {
        let prefab = ResSubMgr.getPrefab("subResGame", "TcoView");
        let node = cc.instantiate(prefab);
        node.parent = parent;
        node.getComponent(TcoView).init(options, callback);
    }

    public static openResurrection(parent: cc.Node) {
        let prefab = ResSubMgr.getPrefab("subResGame", "ResurrectionView");
        let node = cc.instantiate(prefab);
        node.parent = parent;
    }

    public static openWeapon(parent: cc.Node) {
        let prefab = ResSubMgr.getPrefab("subResGame", "WeaponView");
        let node = cc.instantiate(prefab);
        node.parent = parent;
    }
}