import FixManager from "./FixManager";

const { ccclass, property } = cc._decorator;

@ccclass
export default class FixComponent extends cc.Component {
    protected onEnable(): void {
        FixManager.inst.addFixComponent(this);
    }

    protected onDisable(): void {
        FixManager.inst.removeFixComponent(this);
    }

    public fixUpdate(): void {
        
    }
}