import FixComponent from "../../../common/fixcomponent/FixComponent";
import FixManager from "../../../common/fixcomponent/FixManager";
import Gizmos from "../../../tools/Gizmos";
import Circle from "../../../tools/objects/Circle";
import CollisitionManager from "../../manager/CollisitionManager";
import EffectManager from "../../manager/EffectManager";
import GamaManager from "../../manager/GamaManager";
import Character from "../Character";
const { ccclass, property } = cc._decorator;

@ccclass
export default class Boom extends FixComponent {
    public character: Character;

    private cdTimer: number = 0;

    @property
    public boomEffect: string = "";

    protected onLoad(): void {
        this.character = this.node.getComponent(Character);
    }

    public fixUpdate(): void {
        var dt = FixManager.fixedDeltaTime;
        this.cdTimer -= dt;

        var movement = this.character.movement;
        var range = this.character.getProperty("boomRange");
        if (this.cdTimer <= 0 && movement.hit && range > 20) {
            EffectManager.inst.addEffect(this.boomEffect, this.character.node, movement.hitPoint.clone());

            var shape = new Circle();
            shape.center.set(movement.hitPoint);
            shape.radius = range;

            if (GamaManager.inst.debugHitBox) {
                Gizmos.DrawCirle(shape.center, shape.radius);
            }

            var damageMulti =  this.character.getProperty("boomDamageMulti") ?? 1;
            var result = CollisitionManager.inst.collisitionByShape(shape, "enemy");
            result.forEach(item => {
                GamaManager.inst.doDamage(this.character.node, item, ["boom"]);
            });
            this.cdTimer = 0.1;
        }
    }
}