import BattleEffect from "../battle/effect/BattleEffect";
import Utils from "./Utils";

//  - https://docs.cocos.com/creator/manual/en/scripting/life-cycle-callbacks.html
const {ccclass, property} = cc._decorator;

@ccclass
export default class DamagePopup extends BattleEffect {
    @property(cc.Label)
    public label: cc.Label = null;
    @property(cc.Vec2)
    public randomPos: cc.Vec2 = new cc.Vec2;

    init(data: { damage: number }): void {
        this.label.string = data.damage.toString();
        this.label.node.x += Utils.random(-this.randomPos.x, this.randomPos.x);
        this.label.node.y += Utils.random(-this.randomPos.y, this.randomPos.y);

        this.node.zIndex = 999;
    }
}
