@ccclass('TweenUtils')
export class TweenUtils {

    // TweenUtils.bezierTo(this.testNode,10,v2(-2,-2),v2(2,0),v3(3.5,4,0),null).start()
    start () {
        // [3]
    }

    // update (deltaTime: number) {
    //     // [4]
    // }
    /**
     *  二阶贝塞尔曲线 运动
     * @param target
     * @param {number} duration
     * @param {} c1 起点坐标
     * @param {} c2 控制点
     * @param {Vec3} to 终点坐标
     * @param opts
     * @returns {any}
     */
    public static bezierTo(target: any, duration: number, c1: cc.Vec2, c2: cc.Vec2, to: cc.Vec3, opts: any) {
        opts = opts || Object.create(null);
        /**
         * @desc 二阶贝塞尔
         * @param {number} t 当前百分比
         * @param {} p1 起点坐标
         * @param {} cp 控制点
         * @param {} p2 终点坐标
         * @returns {any}
         */
        let twoBezier = (t:number, p1: cc.Vec2, cp: cc.Vec2, p2: cc.Vec3) => {
            let x = (1 - t) * (1 - t) * p1.x + 2 * t * (1 - t) * cp.x + t * t * p2.x;
            let y = (1 - t) * (1 - t) * p1.y + 2 * t * (1 - t) * cp.y + t * t * p2.y;
            // console.log("twoBezier ==  ",x,y); 
            return cc.v3(x, y, 0);
        };
        opts.onUpdate = (arg: cc.Vec3, ratio: number) => {
            target.position = twoBezier(ratio, c1, c2, to);
            // console.log("onUpdate ==  ",target.position); 
        };
        return cc.tween(target).to(duration, {}, opts);
    }
}

