import AppConfig from "../Config/AppConfig";
import HttpUnit, { requestData } from "../NetWork/HttpUnit";
import RYAD from "../RYSDK/RYAD";
import RYSDK from "../RYSDK/RYSDK";
import { LogUtils } from "../Util/LogUtils";

//任游 运营平台 
export default class RYPlatformMgr {
    public static readonly urlRYWAdv_csryw: string = "https://javasttts.renyouwangluo.cn/api/data/product/2result"//从ry_sdk中迁移过来的 wx

    /**
     * 点击广告 上报
     * @param adv_id 
     */
    public static sendClickAd_csryw(adv_id: string) {
        if (AppConfig.UseRYSDK_csryw) {
            LogUtils.info_csryw("运营上报：点击广告" + adv_id);
            RYSDK.Instance_csryw.STAT_csryw.reportClickAd_csryw(adv_id)
        };
    }

    /**
     * 点击广告成功 上报
     * @param adv_id 
     */
    public static sendClickAdAllow_csryw(adv_id: string) {
        if (AppConfig.UseRYSDK_csryw) {
            LogUtils.info_csryw("运营上报：点击广告成功 " + adv_id);
            RYSDK.Instance_csryw.STAT_csryw.reportClickAdAllow_csryw(adv_id)
        };
    }

    /**
     * 获取广告数据
     * @param id 
     * @param complete 
     * @param useLocalRandom 
     */
    public static getAD_csryw(id: string, complete: Function, useLocalRandom?: boolean): boolean {

        if (AppConfig.UseRYSDK_csryw) {
            RYSDK.Instance_csryw.AD_csryw.getAD_csryw(id, complete, useLocalRandom);
            return true;
        }
        return false;
    }


    public static getADList_csryw(id: string, complete: Function, useLocalRandom?: boolean) {
        this.getRYWADVData_csryw(id, (data) => {
            if (data.result && data.result.list) {
                let datas_csryw = data.result.list;

                if (true == useLocalRandom) {
                    RYAD.sortDatas_csryw(datas_csryw);
                }
                complete(datas_csryw)
            } else {
                LogUtils.networkError_csryw("获取到广告数据  为NULL")
                complete(null)
            }
        }, () => {
            LogUtils.networkError_csryw("获取到广告数据  为NULL")
            complete(null)
        })
    }


    /**
  * 获取常规服务器的广告接口
  * @param locationid 
  * @param onSuccess 
  * @param onFail 
  */
    protected static getRYWADVData_csryw(locationid, onSuccess: Function, onFail: Function) {
        let timelog = Date.now();
        var req = new requestData();
        req.url_csryw = this.urlRYWAdv_csryw;
        //req.data_csryw.gameAppid = AppConfig.AppID_csryw;
        req.data_csryw.timelog = timelog;
        req.data_csryw.key = locationid;
        req.onSuccess_csryw = onSuccess;
        req.onFail_csryw = onFail;

        var completeFunc_csryw = (res) => {
            LogUtils.networkLog_csryw(res, "http Success")
            if (req.onSuccess_csryw) {
                req.onSuccess_csryw(res);
            }
            req.onSuccess_csryw = null;
            req = null;
        };

        let fail = req.onFail_csryw
        var errorFunc_csryw = (res) => {
            LogUtils.networkLog_csryw(res, "http fail")
            if (fail) {
                fail(res);
            }
            req && (req.onFail_csryw = null);
            fail = null;
            req = null;
        };

        let headers = {};
        headers["Content-Type"] = "application/x-www-form-urlencoded";
        headers["au"] = "renyou";

        let sendMsg_csryw = null
        let para_csryw = "";
        for (const key of Object.keys(req.data_csryw)) {
            var value = req.data_csryw[key];
            para_csryw += key + "=" + value + "&";
        }
        sendMsg_csryw = para_csryw;
        HttpUnit.sendHttpUrl_csryw(req, sendMsg_csryw, completeFunc_csryw, errorFunc_csryw, headers);
    }
}

