export default class BulletModel {
    id: string;
    prefab: string;
    speed: number;
    hitTime: number;
    tags: string[];
    constructor(id: string, prefab: string, speed: number, hitTime: number = 1, tags: string[] = []) {
        this.id = id;
        this.prefab = prefab;
        this.speed = speed;
        this.hitTime = hitTime;
        this.tags = tags
    }
}