import AssetManager from "../../common/AssetManager";
import BattleEffect from "../effect/BattleEffect";
import ObjPool from "../../tools/ObjPool";
import GamaManager from "./GamaManager";

const {ccclass, property} = cc._decorator;

@ccclass
export default class EffectManager extends ObjPool {
    private static _inst: EffectManager;

    public static get inst(): EffectManager {
        return this._inst;
    }

    protected onLoad(): void {
        EffectManager._inst = this;
    }

    protected onDestroy(): void {
    }

    protected loadPrefab(name: string, callBack: (err: any, prefab: cc.Prefab) => void): void {
        var prefab = AssetManager.getPrefab(name);
        callBack && callBack(null, prefab);
    }

    public addEffect(name: string, owner: cc.Node, position: cc.Vec2, data?: any, parent?: cc.Node): void {
        var self = this;
        this.getObj(name, (prefab: cc.Prefab) => {
            if (prefab == null) {
                // console.log("no find effect: ", name);
                return;
            }
            var node = cc.instantiate(prefab);
            node.parent = parent ?? GamaManager.inst.effectLayer;
            node.setPosition(position);
            var effect = node.getComponent(BattleEffect);
            effect.prefab = name;
            effect.owner = owner;
            effect.init(data);
        });
    }
}