import LoadingView from "./View/Logo/LoadingView";
import AppPlatform from "./Util/AppPlatform";
import AppConfig from "./Config/AppConfig";
import AppSwitchConfig from "./Config/AppSwitchConfig";
import { LogUtils } from "./Util/LogUtils";
import BundleMgr from "./Mgr/BundleMgr";
import GameMgr from "./Mgr/GameMgr";
import User from "./User/User";
import RYSDK from "./RYSDK/RYSDK";
import WudianMgr from "./Mgr/WudianMgr";
import { ResSubMgr } from "./Mgr/ResSubMgr";
import KSAPI from "../Platform/ks/KSAPI";
import ConfigManager from "../scripts/ConfigManager";

const { ccclass, property } = cc._decorator;

/**
 * 主页场景
 */

@ccclass
export default class Main extends cc.Component {

    public static isBack_csryw: boolean = false; //是否从后台回到前台
    //加载页面
    @property({ tooltip: "加载页面预支", type: cc.Node })
    private loadingPrefab: cc.Node = null;

    //-----------------
    private loadView_csryw: LoadingView;

    //加载分包的进度最终位置
    private readonly loadSubpackageTotal_csryw: number = 0.8;
    //网络加载的最终位置
    private readonly loadHttpNetworkTotal_csryw: number = 1;

    //当前加载的分包下标
    private subpackageIndex_csryw: number = 0;
    //公共的包大小
    private subpackageSum_csryw: number = 0;
    //下一步要到达的进度最大值（0-1）
    private needProcessNum_csryw: number = 0;

    //加载的状态 0未开始 1加载正常进度 2加载慢慢速度 3结束，进度条快速完成  4等待加载场景
    private loadingState_csryw: number = 0;
    //速度
    private loadingSpeed_csryw: number = 0;

    onLoad() {

        if (AppPlatform.is_TT_GAME_csryw()) {
            AppConfig.AppID_csryw = AppConfig.TT_APP_ID_csryw;
            AppConfig.state_csryw = AppConfig.TT_state_csryw;
            AppConfig.gameid_csryw = AppConfig.TT_gameid_csryw;
            AppConfig.ResServer_csryw = AppConfig.TT_ResServer_csryw;
            AppConfig.Versions_csryw = AppConfig.TT_Versions_csryw;
            AppConfig.UseRYSDK_csryw = false;

            AppConfig.LoopAdLocationID_csryw = AppConfig.TT_LoopAdLocationID_csryw;
            AppConfig.BannerAdLocationID_csryw = AppConfig.TT_BannerAdLocationID_csryw;
            AppConfig.InsertAdLocationID_csryw = AppConfig.TT_InsertAdLocationID_csryw;
            AppConfig.AniAdLocationID_csryw = AppConfig.TT_AniAdLocationID_csryw;
            AppConfig.HistoryLocationID_csryw = AppConfig.TT_HistoryLocationID_csryw;
            AppConfig.MoreGameLocationID_csryw = AppConfig.TT_MoreGameLocationID_csryw;
        } else if (AppPlatform.is_KS_csryw()) {//快手
            console.log(" --- mian kwaigame ===== ");            
            KSAPI.init_csryw();
            AppConfig.AppID_csryw = KSAPI.App_ID;
            AppConfig.ResServer_csryw = AppConfig.KS_ResServer_csryw;
            AppConfig.Versions_csryw = AppConfig.KS_Versions_csryw;
            AppConfig.UseRYSDK_csryw = false;
        }
        else if (AppPlatform.is_WECHAT_GAME_csryw()) {
            AppConfig.AppID_csryw = AppConfig.WX_APP_ID_csryw;
            AppConfig.state_csryw = AppConfig.WX_state_csryw;
            AppConfig.gameid_csryw = AppConfig.WX_gameid_csryw;
            AppConfig.ResServer_csryw = AppConfig.WX_ResServer_csryw;
            AppConfig.Versions_csryw = AppConfig.WX_Versions_csryw;
            AppConfig.UseRYSDK_csryw = true;
            if (AppConfig.closeUseRYSDK_csryw) {//强制关闭的情况
                AppConfig.UseRYSDK_csryw = false;
            }

            AppConfig.LoopAdLocationID_csryw = AppConfig.WX_LoopAdLocationID_csryw;
            AppConfig.BannerAdLocationID_csryw = AppConfig.WX_BannerAdLocationID_csryw;
            AppConfig.InsertAdLocationID_csryw = AppConfig.WX_InsertAdLocationID_csryw;
            AppConfig.AniAdLocationID_csryw = AppConfig.WX_AniAdLocationID_csryw;
            AppConfig.HistoryLocationID_csryw = AppConfig.WX_HistoryLocationID_csryw;
            AppConfig.MoreGameLocationID_csryw = AppConfig.WX_MoreGameLocationID_csryw;
        } else if (AppPlatform.is_QQ_PLAY_csryw()) {
            AppConfig.AppID_csryw = AppConfig.QQ_APP_ID_csryw;
            AppConfig.state_csryw = AppConfig.QQ_state_csryw;
            AppConfig.gameid_csryw = AppConfig.QQ_gameid_csryw;
            AppConfig.ResServer_csryw = AppConfig.QQ_ResServer_csryw;
            AppConfig.Versions_csryw = AppConfig.QQ_Versions_csryw;
            AppConfig.UseRYSDK_csryw = false;

            AppConfig.LoopAdLocationID_csryw = AppConfig.QQ_LoopAdLocationID_csryw;
            AppConfig.BannerAdLocationID_csryw = AppConfig.QQ_BannerAdLocationID_csryw;
            AppConfig.InsertAdLocationID_csryw = AppConfig.QQ_InsertAdLocationID_csryw;
            AppConfig.AniAdLocationID_csryw = AppConfig.QQ_AniAdLocationID_csryw;
            AppConfig.HistoryLocationID_csryw = AppConfig.QQ_HistoryLocationID_csryw;
            AppConfig.MoreGameLocationID_csryw = AppConfig.QQ_MoreGameLocationID_csryw;
        } else if (AppPlatform.is_OPPO_GAME_csryw()) {
            AppConfig.AppID_csryw = AppConfig.OPPO_APP_ID_csryw;
            AppConfig.state_csryw = AppConfig.OPPO_state_csryw;
            AppConfig.gameid_csryw = AppConfig.OPPO_gameid_csryw;
            AppConfig.ResServer_csryw = AppConfig.OPPO_ResServer_csryw;
            AppConfig.Versions_csryw = AppConfig.OPPO_Versions_csryw;
            AppConfig.UseRYSDK_csryw = false;

            AppConfig.LoopAdLocationID_csryw = AppConfig.OPPO_LoopAdLocationID_csryw;
            AppConfig.BannerAdLocationID_csryw = AppConfig.OPPO_BannerAdLocationID_csryw;
            AppConfig.InsertAdLocationID_csryw = AppConfig.OPPO_InsertAdLocationID_csryw;
            AppConfig.AniAdLocationID_csryw = AppConfig.OPPO_AniAdLocationID_csryw;
            AppConfig.HistoryLocationID_csryw = AppConfig.OPPO_HistoryLocationID_csryw;
            AppConfig.MoreGameLocationID_csryw = AppConfig.OPPO_MoreGameLocationID_csryw;

        } else if (AppPlatform.is_VIVO_GAME_csryw()) {
            AppConfig.AppID_csryw = AppConfig.VIVO_APP_ID_csryw;
            AppConfig.state_csryw = AppConfig.VIVO_state_csryw;
            AppConfig.gameid_csryw = AppConfig.VIVO_gameid_csryw;
            AppConfig.ResServer_csryw = AppConfig.VIVO_ResServer_csryw;
            AppConfig.Versions_csryw = AppConfig.VIVO_Versions_csryw;
            AppConfig.UseRYSDK_csryw = false;

            AppConfig.LoopAdLocationID_csryw = AppConfig.VIVO_LoopAdLocationID_csryw;
            AppConfig.BannerAdLocationID_csryw = AppConfig.VIVO_BannerAdLocationID_csryw;
            AppConfig.InsertAdLocationID_csryw = AppConfig.VIVO_InsertAdLocationID_csryw;
            AppConfig.AniAdLocationID_csryw = AppConfig.VIVO_AniAdLocationID_csryw;
            AppConfig.HistoryLocationID_csryw = AppConfig.VIVO_HistoryLocationID_csryw;
            AppConfig.MoreGameLocationID_csryw = AppConfig.VIVO_MoreGameLocationID_csryw;
        } else if (AppPlatform.is_Android_csryw() || AppPlatform.is_Iphone_csryw()) {//android
            AppConfig.AppID_csryw = AppConfig.APK_APP_ID_csryw;
            AppConfig.state_csryw = AppConfig.APK_state_csryw;
            AppConfig.gameid_csryw = AppConfig.APK_gameid_csryw;
            AppConfig.ResServer_csryw = AppConfig.APK_ResServer_csryw;
            AppConfig.Versions_csryw = AppConfig.APK_Versions_csryw;
            AppConfig.UseRYSDK_csryw = false;

        } else {//其他开发 用微信的
            AppConfig.AppID_csryw = AppConfig.WX_APP_ID_csryw;
            AppConfig.state_csryw = AppConfig.WX_state_csryw;
            AppConfig.gameid_csryw = AppConfig.WX_gameid_csryw;
            AppConfig.ResServer_csryw = AppConfig.WX_ResServer_csryw;
            AppConfig.Versions_csryw = AppConfig.WX_Versions_csryw;
            AppConfig.UseRYSDK_csryw = false;//开发模式不支持

            AppConfig.LoopAdLocationID_csryw = AppConfig.WX_LoopAdLocationID_csryw;
            AppConfig.BannerAdLocationID_csryw = AppConfig.WX_BannerAdLocationID_csryw;
            AppConfig.InsertAdLocationID_csryw = AppConfig.WX_InsertAdLocationID_csryw;
            AppConfig.AniAdLocationID_csryw = AppConfig.WX_AniAdLocationID_csryw;
            AppConfig.HistoryLocationID_csryw = AppConfig.WX_HistoryLocationID_csryw;
            AppConfig.MoreGameLocationID_csryw = AppConfig.WX_MoreGameLocationID_csryw;
        }

        this.loadView_csryw = this.loadingPrefab.getComponent(LoadingView);

        this.subpackageSum_csryw = AppConfig.subResArray_csryw.length;
        this.loadView_csryw.setProcess_csryw(0);

        //加载配置AppSwitchConfig
        AppSwitchConfig.getInstance_csryw().loadUrlConfig_csryw(handleFM_csryw(() => {
            LogUtils.log_csryw("加载 AppSwitchConfig 完成")
            this.loadSubpackage_csryw();
        }, this), handleFM_csryw(() => {
            console.log("加载失败。。。")
        }, this))

        AppPlatform.checkUpdate_csryw();
    }

    start() {
    }

    /**
     * 加载分包
     */
    private loadSubpackage_csryw() {
        var self = this;
        //有分包没有加载完成
        if (this.subpackageIndex_csryw < this.subpackageSum_csryw) {
            let subpackageName = AppConfig.subResArray_csryw[this.subpackageIndex_csryw];
            let proSum = ((this.subpackageIndex_csryw + 1) / this.subpackageSum_csryw) * self.loadSubpackageTotal_csryw;
            this.setStartLoadingPerNum_csryw(proSum)
            BundleMgr.loadBundleByName_csryw(subpackageName, handleFM_csryw((err, bundle) => {
                if (err) {
                    console.error(err);
                } else {
                    self.subpackageIndex_csryw = self.subpackageIndex_csryw + 1;
                    ResSubMgr.loadSubpackageFinish_csryw(subpackageName, handleFM_csryw(() => {
                        self.loadSubpackage_csryw();
                    }, this))
                }
            }, this));
            // }
        } else {//加载完成了
            this.onLoadResComplate_csryw();//预加载完成
        }
    }

    /**
     * 设置下一个加载步骤可以到的位置
     * @param preNum 
     */
    private setStartLoadingPerNum_csryw(preNum: number) {
        this.setLoadingState_csryw(1, preNum);
    }


    /**
     * 设置加载的进度状态
     * @param state 
     */
    private setLoadingState_csryw(state: number, preNum: number) {
        this.loadingState_csryw = state;
        this.needProcessNum_csryw = preNum;
        switch (state) {
            case 0:
                break;
            case 1:
                this.loadingSpeed_csryw = 1 / 80;
                break;
            case 2:
                this.loadingSpeed_csryw = 1 / 200;
                break;
            case 3://下载完成了
                {
                    let precess = this.loadView_csryw.getProcess_csryw();
                    let psum = 1 - precess;
                    if (psum > 0) {
                        this.loadingSpeed_csryw = psum / 20;
                    } else {
                        this.loadingSpeed_csryw = 1 / 2;
                    }

                }
                break;
            default:
                break;
        }
    }


    /**
     * 假的进度
     * @param dt 
     */
    protected update(dt: number): void {
        if (this.loadView_csryw) {
            let precess = this.loadView_csryw.getProcess_csryw();
            let nextPrecess = precess + this.loadingSpeed_csryw;

            switch (this.loadingState_csryw) {
                case 0:
                    break;
                case 1://正常速度
                    if (nextPrecess >= this.needProcessNum_csryw * 0.7) {
                        //nextPrecess = this.needProcessNum * 0.7;
                        if (nextPrecess >= this.needProcessNum_csryw) {
                            nextPrecess = this.needProcessNum_csryw;
                        }
                        //进入慢速度
                        this.setLoadingState_csryw(2, this.needProcessNum_csryw);
                    }
                    break;
                case 2://慢速度
                    if (nextPrecess >= this.needProcessNum_csryw) {
                        nextPrecess = this.needProcessNum_csryw;
                    }
                    break;
                case 3://急速进度
                    if (nextPrecess >= 1) {
                        nextPrecess = 1;
                        this.loadingState_csryw = 4;

                    }
                    break;
                default:
                    break;
            }

            if (precess != nextPrecess) {
                this.loadView_csryw.setProcess_csryw(nextPrecess);
            }
        }
    }

    /**
     * 设置下载全部完成，可以进游戏了
     */
    private setDownloadOver_csryw() {
        this.initGame_csryw();
        this.setLoadingState_csryw(3, 1);
    }



    /**
     * 分包等相关资源全部加载完成了
     */
    private onLoadResComplate_csryw() {
        var self = this;
        this.setStartLoadingPerNum_csryw(this.loadHttpNetworkTotal_csryw);
        //预加载用到的场景
        GameMgr.getInstance_csryw().preloadScene_csryw();
        //存档
        let data = cc.sys.localStorage.getItem("data" + AppConfig.AppID_csryw);
        if (data) {
            User.initiUser_csryw(JSON.parse(data));
        } else {
            User.initiUser_csryw(null);
        }

        ConfigManager.loadConfigs(() => {
            self.setDownloadOver_csryw();
        });
    }


    //初始游戏
    public initGame_csryw() {
        if (AppPlatform.is_KS_csryw()) {
            AppPlatform.loginPlatform_csryw(function (code) {
                console.log('登陆成功，进行初始化');
                GameMgr.getInstance_csryw().onLoadToWorldScene_csryw();
            }, null)
        }
        else if (AppPlatform.is_WECHAT_GAME_csryw()) {
            console.log("小游戏设置转发按钮");
            window["wx"].showShareMenu({
                withShareTicket: false,
                success: () => {

                },
                fail: () => {

                },
                complete: () => {

                }
            });
            window["wx"].onShareAppMessage(function () {
                return {
                    title: "",
                    imageUrl: ""
                }
            });

            window["wx"].onShow(function () {
                console.log('微信 显示在前台');
                AppPlatform.isBackGameWX = true;
            });


            //初始化放到后面
            let self = this;
            AppPlatform.loginPlatform_csryw(function (code) {
                //初始 ry sdk
                console.log('登陆成功，进行初始化');
                GameMgr.getInstance_csryw().onLoadToWorldScene_csryw();
                if (AppConfig.UseRYSDK_csryw) { RYSDK.init_csryw(code); }
            }, null)
        } else if (AppPlatform.is_TT_GAME_csryw()) {
            AppPlatform.loginPlatform_csryw(function (code) {
                console.log('登陆成功，进行初始化');
                GameMgr.getInstance_csryw().onLoadToWorldScene_csryw();
            }, null)
        }
        else {
            GameMgr.getInstance_csryw().onLoadToWorldScene_csryw();
        }
    }

}
