

var event = new cc.EventTarget;
export class GlobalMessage {
    private static getMessageKey(type): string {
        var className = type["key"];
        if (typeof type == "object") {
            className = type.constructor["key"];
        }
        return "E_" + className;
    }

    public static on<T>(type: { prototype: T }, callBack: (message: T) => void, caller: any) {
        event.on(this.getMessageKey(type), callBack, caller);
    }

    public static emit<T>(message: T): void {
        event.emit(this.getMessageKey(message), message);
    }

    public static off<T>(type: { prototype: T }, callBack: (message: T) => void, caller: any) {
        event.off(this.getMessageKey(type), callBack, this);
    }

    public static offAll(caller: any): void {
        event.targetOff(caller);
    }
}