import AudioManager from "../../../scripts/battle/manager/AudioManager";
import SoundMgr from "../../Mgr/SoundMgr";
import VibrateMgr from "../../Mgr/VibrateMgr";
import DialogUIBase from "./DialogUIBase";

const { ccclass, property } = cc._decorator;

@ccclass
export default class DialogSetting extends DialogUIBase {
    protected EventEnumView_csryw: { [key: string]: string; } = {};

    butSound: cc.Node;
    btnBgm: cc.Node;
    butVibrat: cc.Node;

    protected initView_csryw(): void {
        super.initView_csryw()

        var bg = this.childNode("bg");
        bg.on("click", () => {
            this.closeDialog();
        }, this);

        this.btnBgm = this.childNode("butBgm", this.showNode);
        this.butSound = this.childNode("butSound", this.showNode);
        this.butVibrat = this.childNode("butVibrat", this.showNode);

        //声音
        this.onClick(this.butSound, () => {
            AudioManager.inst.sfxEnable = !AudioManager.inst.sfxEnable;
            this.updateShowSound();
        }, this)

        this.onClick(this.btnBgm, () => {
            AudioManager.inst.bgmEnable = !AudioManager.inst.bgmEnable;
            AudioManager.inst.bgmEnable ? AudioManager.inst.resumBGM() : AudioManager.inst.pauseBGM();
            this.updateShowSound();
        }, this)

        //震动
        this.onClick(this.butVibrat, () => {
            VibrateMgr.isEnable_csryw = !VibrateMgr.isEnable_csryw;
            this.updateShowVibrat();
        }, this)

        this.onClick(this.childNode("butClose", this.showNode), () => {
            this.closeDialog(true);
        }, this)
        this.onClick(this.childNode("butClose2", this.showNode), () => {
            this.closeDialog(false);
        }, this)

        
        this.updateShowSound();
        this.updateShowVibrat();
    }

    private updateBtn(btn: cc.Node, active: boolean) {
        this.childNode("open", btn).active = active;
        this.childNode("close", btn).active = !active;
    }

    /**
     * 更新显示声音
     */
    private updateShowSound() {
        this.updateBtn(this.btnBgm, AudioManager.inst.bgmEnable);
        this.updateBtn(this.butSound, AudioManager.inst.sfxEnable);
    }

    /**
     * 更新显示震动
     */
    private updateShowVibrat() {
        this.childNode("open", this.butVibrat).active = VibrateMgr.isEnable_csryw;
        this.childNode("close", this.butVibrat).active = !VibrateMgr.isEnable_csryw;
    }

}