import ConfigManager from "../../ConfigManager";
import FixComponent from "../../common/fixcomponent/FixComponent";
import ActionEffectConfig from "../structs/battle/ActionEffectConfig";
const { ccclass, property } = cc._decorator;

@ccclass
export default class ActionManager extends FixComponent {
    private static _inst: ActionManager;

    public static get inst(): ActionManager {
        return this._inst;
    }

    protected onLoad(): void {
        ActionManager._inst = this;
    }

    private tryExecute(onwer: cc.Node, direction: cc.Vec2, key: string): boolean {
        var config: ActionEffectConfig = ConfigManager.getActionEffectConfig(key);
        switch(config.actionType) {
            case "summon": return this.executeSummon(onwer, direction, config);
        }
        return false;
    }

    private executeSummon(onwer: cc.Node, direction: cc.Vec2, config: ActionEffectConfig): boolean {

        return true;
    }
}