/*
 * @Author: YeeChan
 * @Date: 2020-12-17 20:07:37
 * @Description: 信息展示 清除存档
 */

import AppConfig from "../Config/AppConfig";
import AppSwitchConfig from "../Config/AppSwitchConfig";
import { ryw_Event } from "../Event/EventEnum";
import EventMgr from "../Event/EventMgr";
import HttpUnit from "../NetWork/HttpUnit";
import AppPlatform from "../Util/AppPlatform";
import { DateUtils } from "../Util/DateUtils";
import Utilit from "../Util/Utilit";
import WudianMgr from "./WudianMgr";

export default class DebugInfoMgr {
    //debug 根节点
    private static debugNode_csryw: cc.Node = null;
    //展示页面
    private static debugPanel_csryw: cc.Node = null;

    private static scrollview_csryw: cc.ScrollView = null;
    private static content_csryw: cc.Node = null;

    //debug 消息集合 
    private static debugInfoList_csryw: Array<string> = new Array();
    private static debugFrameInfoList_csryw: Array<string> = new Array();
    private static debugLabelList_csryw: Array<cc.Label> = new Array();
    private static debugUmengInfoList_csryw: { [key: string]: number } = {};

    private static showInfoNode_csryw: cc.Node = null;
    private static showInfoLabel_csryw: cc.Label = null;

    //是否启动了debug模式
    private static isOpenDebug_csryw: boolean = false;
    //是否正常点击
    private static isOpenStartClick_csryw: boolean = false;
    //次数
    private static touchClickSum_csryw: number = 0;
    //间隔时间
    private static clickTime_csryw: number = 0;

    //ip屏蔽接口是否调用成功
    private static isNetIpBlockState_csryw: boolean = false;

    /**
     * 启动debug
     */
    public static setDebug_csryw() {

        if (AppSwitchConfig.getInstance_csryw().getAppSwitchData_csryw().debuginfo_csryw == 1) {
            this.isOpenDebug_csryw = true;

            //监听ip屏蔽
            EventMgr.onEvent_csryw(ryw_Event.ryw_App_OnUpdateIpBlockState_csryw, () => {
                this.isNetIpBlockState_csryw = true;
                if (this.debugPanel_csryw.active) {
                    this.updateFrameInfo_csryw();
                    this.updateItem_csryw();
                }
            }, this);

            //平台登录的状态
            EventMgr.onEvent_csryw(ryw_Event.ryw_PlatformLoginState_csryw, (tab) => {
                let state_csryw = tab.state
                let info_csryw = tab.info
                this.addDebugInfo_csryw(info_csryw);
            }, this);
            //登录的状态
            EventMgr.onEvent_csryw(ryw_Event.ryw_NetLoginState_csryw, (tab) => {
                let state_csryw = tab.state
                let info_csryw = tab.info
                this.addDebugInfo_csryw(info_csryw);
            }, this);
            //获取用户数据
            EventMgr.onEvent_csryw(ryw_Event.ryw_NetUserDataState_csryw, (tab) => {
                let state_csryw = tab.state
                let info_csryw = tab.info
                this.addDebugInfo_csryw(info_csryw);
            }, this);
            //友盟打点
            EventMgr.onEvent_csryw(ryw_Event.ryw_Umeng_csryw, (tab) => {
                let event = tab.event
                if (this.debugUmengInfoList_csryw[event]) {
                    this.debugUmengInfoList_csryw[event] = this.debugUmengInfoList_csryw[event] + 1;
                } else {
                    this.debugUmengInfoList_csryw[event] = 1;
                }
                this.updateItem_csryw();
            }, this);

            let sizeView = cc.view.getVisibleSize();

            let size = cc.view.getFrameSize();
            // console.log(size)
            // console.log(sizeView)
            console.log("启动了debug展示")

            //根节点
            this.debugNode_csryw = new cc.Node();
            cc.game.addPersistRootNode(this.debugNode_csryw);
            this.debugNode_csryw.width = sizeView.width;
            this.debugNode_csryw.height = sizeView.height;
            this.debugNode_csryw.anchorX = 0;
            this.debugNode_csryw.anchorY = 0;
            this.debugNode_csryw.x = 0;
            this.debugNode_csryw.y = 0;

            let butSize_csryw = cc.size(180, 120);
            let butNode_csryw = this.createNode_csryw(this.debugNode_csryw, cc.v2(0, sizeView.height - butSize_csryw.height), cc.Color.WHITE);
            butNode_csryw.setContentSize(butSize_csryw);
            // butNode.addComponent(cc.Sprite).spriteFrame = this.createSpriteFrame_csryw(butSize);
            // this.createLabel_csryw(butNode, cc.v2(0, 0), "打开", cc.Color.RED);

            butNode_csryw.on(cc.Node.EventType.TOUCH_START, (touch: cc.Event, event) => { }, this)
            butNode_csryw["_touchListener"].onTouchBegan = (touch, event) => {
                let pos_csryw = butNode_csryw.convertToNodeSpaceAR(touch.getLocation());
                if (pos_csryw.x < 0 || pos_csryw.y < 0 || pos_csryw.x > butSize_csryw.width || pos_csryw.y > butSize_csryw.height) {//超出按钮范围
                    return;
                }
                if (!this.isOpenStartClick_csryw) {
                    let time_csryw = DateUtils.getNowTime_csryw();
                    //console.log(this.touchClickSum_csryw)
                    if (time_csryw - this.clickTime_csryw <= 500) {
                        this.touchClickSum_csryw = this.touchClickSum_csryw + 1;
                        if (this.touchClickSum_csryw > 10) {
                            this.isOpenStartClick_csryw = true;
                        }
                    } else {
                        this.touchClickSum_csryw = 0;
                    }
                    this.clickTime_csryw = time_csryw
                } else {
                    if (!this.debugPanel_csryw.active) {
                        this.updateFrameInfo_csryw();
                        this.updateItem_csryw();
                        this.debugPanel_csryw.active = true;
                    }
                }

                return true;
            };
            butNode_csryw["_touchListener"].setSwallowTouches(false);


            let showSize_csryw = cc.size(sizeView.width * 0.98, sizeView.height * 0.7);
            //背景色
            this.debugPanel_csryw = this.createNode_csryw(this.debugNode_csryw, cc.v2((sizeView.width - showSize_csryw.width) / 2, (sizeView.height - showSize_csryw.height) / 2), cc.Color.WHITE, showSize_csryw);
            this.debugPanel_csryw.active = false;
            this.debugPanel_csryw.addComponent(cc.Button);

            //滚动容器
            let snode_csryw = this.createNode_csryw(this.debugPanel_csryw, cc.v2(0, 0), cc.Color.GRAY, showSize_csryw);
            this.scrollview_csryw = snode_csryw.addComponent(cc.ScrollView);
            this.scrollview_csryw.horizontal = false;
            this.scrollview_csryw.vertical = true;
            this.scrollview_csryw.inertia = true;
            this.scrollview_csryw.brake = 0.75;
            //遮罩
            let viewMask_csryw = this.createNode_csryw(snode_csryw, cc.v2(0, 0), cc.Color.WHITE);
            viewMask_csryw.setContentSize(showSize_csryw);
            viewMask_csryw.addComponent(cc.Mask)
            viewMask_csryw.anchorY = 1;
            viewMask_csryw.y = showSize_csryw.height;
            //content
            this.content_csryw = this.createNode_csryw(viewMask_csryw, cc.v2(0, 0), cc.Color.GRAY, showSize_csryw);
            this.content_csryw.anchorY = 1;
            this.scrollview_csryw.content = this.content_csryw;

            //关闭按钮
            this.createButton_csryw(this.debugPanel_csryw, cc.v2(0, showSize_csryw.height), "关闭", cc.color(255, 180, 180), cc.Color.RED, cc.size(100, 70), () => {
                this.debugPanel_csryw.active = false;
            });
            //清除缓存按钮
            this.createButton_csryw(this.debugPanel_csryw, cc.v2(showSize_csryw.width - 170, showSize_csryw.height), "清理缓存", cc.color(255, 180, 180), cc.Color.RED, cc.size(170, 70), () => {
                HttpUnit.saveGameData_csryw("{}", (res) => {
                    if (res.code == 1) { console.log("存档成功") }
                    else { console.log("存档失败") }
                }, (res) => { console.log("存档失败") })
                this.showToast_csryw("清理用户数据，并提交！请重启")
            });

            //提示框
            let cshowToastSize_csryw = cc.size(showSize_csryw.width * 0.9, 100);
            this.showInfoNode_csryw = this.createNode_csryw(this.debugNode_csryw, cc.v2((sizeView.width) / 2, (sizeView.height) / 2), cc.color(175, 100, 170), cshowToastSize_csryw);
            this.showInfoNode_csryw.anchorX = 0.5;
            this.showInfoNode_csryw.anchorY = 0.5;
            this.showInfoLabel_csryw = this.createLabel_csryw(this.showInfoNode_csryw, cc.v2(0, 0), "", cc.Color.RED).getComponent(cc.Label);
            this.showInfoLabel_csryw.node.anchorX = 0.5;
            this.showInfoLabel_csryw.node.anchorY = 0.5;
            this.showInfoNode_csryw.active = false;


        }
    }

    /**
     * 增加一条需要展示的debug信息
     * @param info 需要展示的信息
     */
    public static addDebugInfo_csryw(info: string) {
        if (this.isOpenDebug_csryw) {
            this.debugInfoList_csryw.push(info);
        }
    }


    /**
     * 添加框架的信息
     */
    private static updateFrameInfo_csryw() {
        this.debugFrameInfoList_csryw = [];
        let addVersion_csryw = () => {
            let cur_csryw = Utilit.checkVersions_csryw();
            let versionSwitch_csryw = "[]"
            if (AppPlatform.is_TT_GAME_csryw())//抖音，字节跳动
            {
                versionSwitch_csryw = "ttcfg:<" + AppSwitchConfig.getInstance_csryw().getAppSwitchData_csryw().ttcfg_csryw.ttversions_csryw + ">";
            }
            else if (AppPlatform.is_OPPO_GAME_csryw())//oppo小游戏
            {
                versionSwitch_csryw = "oppocfg:<" + AppSwitchConfig.getInstance_csryw().getAppSwitchData_csryw().oppocfg_csryw.oppoversions_csryw + ">";
            }
            else if (AppPlatform.is_QQ_PLAY_csryw())//qq小游戏
            {
                versionSwitch_csryw = "qqcfg:<" + AppSwitchConfig.getInstance_csryw().getAppSwitchData_csryw().qqcfg_csryw.qqversions_csryw + ">";
            }
            else if (AppPlatform.is_WECHAT_GAME_csryw()) //VIVO 小游戏
            {
                versionSwitch_csryw = "vivocfg:<" + AppSwitchConfig.getInstance_csryw().getAppSwitchData_csryw().vivocfg_csryw.vivoversions_csryw + ">";
            }
            else//微信小游戏
            {
                versionSwitch_csryw = "version:<" + AppSwitchConfig.getInstance_csryw().getAppSwitchData_csryw().version_csryw + ">";
            }
            let str_csryw = "游戏名字:[" + AppConfig.GameName_csryw + "],gameid:[" + AppConfig.gameid_csryw + "]\n";
            str_csryw = str_csryw + AppConfig.ResServer_csryw + "\n"
            str_csryw = str_csryw + "本地版本号:[" + AppConfig.Versions_csryw + "]\n远端版本号:[" + versionSwitch_csryw + "]\n版本比较结果:" + (cur_csryw ? "true" : "false");
            this.debugFrameInfoList_csryw.push(str_csryw);
        }

        let cwudian_csryw = () => {
            let mainSwitch_csryw = AppSwitchConfig.getInstance_csryw().getAppSwitchData_csryw().wudian_csryw == 1;
            let isOpenTime_csryw = AppSwitchConfig.getInstance_csryw().getAppSwitchData_csryw().wudianTimeAvaliable_csryw;
            let ipnotBlock_csryw = WudianMgr.ryw_GetIpBlocked_csryw();
            let launchScene_csryw_csryw = AppPlatform.getLaunchOptionsSync_csryw().scene;

            if (AppPlatform.is_TT_GAME_csryw()) {
                mainSwitch_csryw = true;
                isOpenTime_csryw = true;
            }

            let noEnterBySearch_csryw: boolean = true;
            var wudianSceneList_csryw = AppSwitchConfig.getInstance_csryw().getAppSwitchData_csryw().wudianSceneList_csryw;
            for (let index = 0; index < wudianSceneList_csryw.length; index++) {
                var wudianSceneValue_csryw = wudianSceneList_csryw[index];
                if (launchScene_csryw_csryw == wudianSceneValue_csryw) {
                    noEnterBySearch_csryw = false;
                }
            }
            let cwd_csryw = mainSwitch_csryw && noEnterBySearch_csryw && ipnotBlock_csryw && isOpenTime_csryw;

            let str = "ip屏蔽接口调用:" + (this.isNetIpBlockState_csryw ? "成功" : "失败") + "\n"
                + "误点最终结果：[" + (cwd_csryw ? "有" : "没有") + "]\n"
                + "当前场景值:[" + launchScene_csryw_csryw + "]\n"
                + "ip没有屏蔽:[" + (ipnotBlock_csryw ? "是" : "不是") + "]\n"
                + "场景没有屏蔽:[" + (noEnterBySearch_csryw ? "是" : "不是") + "]\n"
                + "误点开关:[" + (mainSwitch_csryw ? "打开" : "关闭") + "],\n"
                + "时间开关:[" + (isOpenTime_csryw ? "打开" : "关闭") + "]"

            this.debugFrameInfoList_csryw.push(str);
        }

        addVersion_csryw();
        cwudian_csryw();
    }



    /**
     * 设置提示语
     * @param info 
     */
    private static showToast_csryw(info: string) {
        cc.Tween.stopAllByTarget(this.showInfoNode_csryw);
        this.showInfoLabel_csryw.string = info;
        this.showInfoNode_csryw.active = true;
        this.showInfoNode_csryw.opacity = 0;
        this.showInfoNode_csryw.scale = 0.75;
        cc.tween(this.showInfoNode_csryw)
            .to(0.25, { opacity: 255, scale: 1 }, { easing: "backOut" })
            .delay(2)
            .call(() => {
                this.showInfoNode_csryw.active = false;
            })
            .start();
    }

    /**
     * 更新item
     */
    private static updateItem_csryw() {
        let infoList = [];
        //先加框架的
        for (let index = 0; index < this.debugFrameInfoList_csryw.length; index++) {
            const element_csryw = this.debugFrameInfoList_csryw[index];
            infoList.push(element_csryw);
        }

        let umengInfo = "友盟统计开始：\n"
        for (const key in this.debugUmengInfoList_csryw) {
            if (Object.prototype.hasOwnProperty.call(this.debugUmengInfoList_csryw, key)) {
                const element = this.debugUmengInfoList_csryw[key];
                umengInfo = "事件：" + umengInfo + key + ",次数: " + element + "\n"
            }
        }
        umengInfo = umengInfo + "友盟统计结束!"
        infoList.push(umengInfo);

        //再加自定义的
        for (let index = 0; index < this.debugInfoList_csryw.length; index++) {
            const element_csryw = this.debugInfoList_csryw[index];
            infoList.push(element_csryw);
        }


        let sizeInfo = infoList.length;
        let sizeLabel = this.debugLabelList_csryw.length;
        let num = sizeInfo - sizeLabel;
        //增加新的
        if (num > 0) {
            for (let index = 0; index < num; index++) {
                let label = this.createLabel_csryw(this.content_csryw, cc.v2(0, 0), "", cc.Color.BLACK).getComponent(cc.Label);
                label.node.width = this.content_csryw.width;
                label.overflow = cc.Label.Overflow.RESIZE_HEIGHT;
                label.horizontalAlign = cc.Label.HorizontalAlign.LEFT;
                label.node.anchorY = 1;
                this.debugLabelList_csryw.push(label);
            }
        }
        //关闭
        for (let index = 0; index < this.debugLabelList_csryw.length; index++) {
            const element_csryw = this.debugLabelList_csryw[index];
            element_csryw.node.active = false;
        }
        //更新
        let maxHeight_csryw: number = 0;
        let temp_csryw: boolean = false;
        for (let index = 0; index < infoList.length; index++) {
            const element_csryw = infoList[index];
            let label = this.debugLabelList_csryw[index];
            label.string = element_csryw;
            label.node.y = -maxHeight_csryw;
            label.node.active = true;
            label["_forceUpdateRenderData"]();
            maxHeight_csryw = maxHeight_csryw + label.node.height + 5
            if (temp_csryw) {
                temp_csryw = false;
                label.node.color = cc.color(0, 0, 0);
            } else {
                temp_csryw = true;
                label.node.color = cc.color(122, 22, 22);
            }
        }
        this.content_csryw.height = maxHeight_csryw;
    }

    /**
     * 创建一个 label
     * @param info 
     * @param color 
     */
    private static createLabel_csryw(parent: cc.Node, pos: cc.Vec2, info: string, color: cc.Color) {
        let node = this.createNode_csryw(parent, pos, color)
        let label = node.addComponent(cc.Label);
        label.fontSize = 30;
        label.lineHeight = 32;
        label.string = info;
        return node;
    }

    /**
     * 创建一个 节点
     * @param color 
     * @param size 
     */
    private static createNode_csryw(parent: cc.Node, pos: cc.Vec2, color: cc.Color, size?: cc.Size) {
        let node = new cc.Node();
        if (size) {
            node.setContentSize(size);
            node.addComponent(cc.Sprite).spriteFrame = this.createSpriteFrame_csryw(size);
        }
        node.color = color;
        parent.addChild(node);
        node.x = pos.x;
        node.y = pos.y;
        node.anchorX = 0;
        node.anchorY = 0;
        return node;
    }


    /**
     * 创建一个button
     * @param info 
     * @param color 
     * @param labelColor 
     * @param size 
     * @param listener 
     */
    private static createButton_csryw(parent: cc.Node, pos: cc.Vec2, info: string, color: cc.Color, labelColor: cc.Color, size: cc.Size, listener?: Function) {
        let node = this.createNode_csryw(parent, pos, color, size);
        node.addComponent(cc.Button);
        node.color = color;
        if (listener) {
            node.on("click", listener, this);
        }
        this.createLabel_csryw(parent, pos, info, labelColor);
        return node;
    }


    /**
     * 创建纯色的spriteframe
     * @param size 
     */
    private static createSpriteFrame_csryw(size: cc.Size): cc.SpriteFrame {
        let texture = new cc.Texture2D();
        let spriteFrame = new cc.SpriteFrame();
        let count = size.width * size.height * 4;
        let imgData = new Uint8Array(count);
        for (let j = 0; j < count; j += 4) {
            imgData[j] = 255;
            imgData[j + 1] = 255;
            imgData[j + 2] = 255;
            imgData[j + 3] = 255;
        }
        texture.initWithData(imgData, cc.Texture2D.PixelFormat.RGBA8888, size.width, size.height);
        spriteFrame.setTexture(texture);
        spriteFrame.setRect(cc.rect(0, 0, size.width, size.height));
        return spriteFrame;
    }
}