import FixComponent from "../../../common/fixcomponent/FixComponent";
import FixManager from "../../../common/fixcomponent/FixManager";
import GamaManager, { GameState } from "../../manager/GamaManager";
import Character from "../Character";

const { ccclass, property } = cc._decorator;

@ccclass
export default class AIBase extends FixComponent {
    private toNext: number = 0;

    protected character: Character = null;

    @property(cc.Float)
    protected attackRange: number = 100;

    @property({ type: cc.Integer, min: 0, max: 100, slide: true })
    protected attackProb: number = 50;

    @property(cc.Float)
    protected attackInterval: number = 0;

    protected get mainCharacter(): Character {
        return GamaManager.inst.mainCharacter;
    }

    private direction: cc.Vec2 = new cc.Vec2;
    private attackTimer: number = 0;

    protected onLoad(): void {
        this.character = this.node.getComponent(Character);
    }

    protected start(): void {
        this.toNext = 1;
    }

    protected checkCanAttack(): boolean {
        var distanceSqr = cc.Vec2.squaredDistance(this.node, this.mainCharacter.node);
        return this.attackTimer < 0 && distanceSqr <= this.attackRange * this.attackRange && Math.random() * 100 < this.attackProb;
    }

    protected lookMainCharacter(): void {
        this.direction.set(cc.v2(this.mainCharacter.node.x - this.node.x, this.mainCharacter.node.y - this.node.y));
        this.direction.normalizeSelf();
        this.character.setDirection(Math.sign(this.direction.x));
    }

    protected resetAttackTime(): void {
        this.attackTimer = this.attackInterval;
    }

    protected tryCastSKill(): boolean {
        var attack = this.checkCanAttack();
        if (attack) {
            this.lookMainCharacter();
            this.character.doAction("Attack");
            this.resetAttackTime();
        }
        return attack;
    }

    protected selfAction(): void {
        this.disguiseWander(cc.v2(this.mainCharacter.node, this.mainCharacter.node.y), 120, 300);
    }

    private disguiseWander(target: cc.Vec2, offsetX: number, offsetMax: number): void {
        target.x +=  ((Math.random() - 0.5) * 2) * offsetX;
        target.y +=  ((Math.random() - 0.5) * 2) * offsetX;

        var dir = cc.v2(target.x - this.node.x, target.y - this.node.y);
        dir.normalizeSelf();
        // var rotate = (Math.random() - 0.5) * 2 * 10;
        // dir.rotateSelf(Math.PI / 180 * rotate);
        // var offset = offsetMin + Math.random() * (offsetMax - offsetMin + 1);
        // dir.mulSelf(offset);
        // dir.sub(cc.v2(this.node.x, this.node.y), this.direction);
        // this.direction.normalizeSelf();

        // var dir = cc.Vec2.RIGHT.clone();
        // dir.rotateSelf(Math.PI / 180 * 90);


        dir.normalize(this.direction);
    }

    public fixUpdate(): void {
        var dt = FixManager.fixedDeltaTime;
        if (GamaManager.inst.state != GameState.Run || !this.character || this.character.isDead == true) return;

        if (this.mainCharacter == null || this.mainCharacter.isDead) {
            return;
        }

        this.toNext -= dt;
        this.attackTimer -= dt;
        if (this.toNext <= 0) {
           this.toNext = 0.2 + Math.random() * 1;
           this.tryCastSKill() || this.selfAction();
        }
        this.character.setDirection(Math.sign(this.direction.x));
        this.character.moveByDirection(this.direction);
    }
}