import FixComponent from "./FixComponent";
const { ccclass, property } = cc._decorator;

@ccclass
export default class FixManager extends cc.Component {
    private static _ins: FixManager;
    public static get inst(): FixManager {
        if (this._ins == null) {
            var node = new cc.Node("FixMamager");
            cc.director.getScene().addChild(node);
            cc.game.addPersistRootNode(node);
            this._ins = node.addComponent(FixManager);
        }
        return this._ins;
    }

    @property(cc.Float)
    public fixedDeltaTime: number = 1 / 60;

    public pause: boolean = false;

    private _components: FixComponent[] = [];
    private _gameStartTime: number;
    private _tick: number = 0;
    private _totalTime: number = 0;

    public get totalTime(): number {
        return this._totalTime;
    }

    private get _tickSinceGameStart(): number {
        // return Math.floor(((cc.director.getTotalTime() - this._gameStartTime) / 1000) / this.Update_Deltatime);
        return Math.floor(this._totalTime / this.fixedDeltaTime);
    }

    public static get fixedDeltaTime(): number {
        return this._ins.fixedDeltaTime;
    }

    protected onLoad(): void {
        FixManager._ins = this;
        cc.game.addPersistRootNode(this.node);
    }

    protected start(): void {
        this._gameStartTime = cc.director.getTotalTime();
    }

    public addFixComponent(component: FixComponent): void {
        if (!this._components.includes(component)) {
            this._components.push(component);
        }
    }

    public removeFixComponent(component: FixComponent): void {
        var index = this._components.indexOf(component);
        if (index == -1) {
            return;
        }
        this._components.splice(index, 1);
    }

    private _checkUpdateFrame(): void {
        for (var i = 0; i < this._components.length; i++) {
            this._components[i].fixUpdate();
        }
        this._tick++;
    }

    protected update(dt: number): void {
        if (this.pause) {
            return;
        }

        this._totalTime += dt;
        while (this._tick < this._tickSinceGameStart) {
            this._checkUpdateFrame();
        }
    }
}