import FixComponent from "../../../common/fixcomponent/FixComponent";
import FixManager from "../../../common/fixcomponent/FixManager";
import GamaManager, { GameState } from "../../manager/GamaManager";
import Character from "../Character";
const { ccclass, property } = cc._decorator;

@ccclass
export default class Fbsd extends FixComponent {
    public character: Character = null;

    private direction: cc.Vec2 = new cc.Vec2;
    private cdTimer: number = 0;

    @property()
    public bulletPrefab: string = "";

    protected onLoad(): void {
        this.character = this.node.getComponent(Character);
    }

    public fire(direction: cc.Vec2, count: number): void {
        if (direction.equals(cc.Vec2.ZERO)) {
            direction.set(cc.Vec2.RIGHT);
        }
        this.direction.set(direction);
        this.direction.rotateSelf(Math.PI / 2);
        var position = cc.v2(this.node.x, this.node.y);

        var step = Math.PI / (count - 1);
        for (var i = 0; i < count; i++) {
            GamaManager.inst.createBullet(this.node, this.bulletPrefab, position, this.direction.clone());
            this.direction.rotateSelf(step);
        }
    }

    public fixUpdate(): void {
        if (GamaManager.inst.state != GameState.Run) {
            return;
        }
        var dt = FixManager.fixedDeltaTime;
        this.cdTimer -= dt;

        var count = this.character.getProperty("fbsd");
        if (this.cdTimer <= 0 && count > 0) {
            this.cdTimer = 3;
            this.fire(this.character.movement.curVelocity.normalize(), count);
        }
    }
}