import { ResSubMgr } from "../../FrameWork/Mgr/ResSubMgr";

export default class AssetManager {
    private static readonly bundleName: string = "subResGame";

    static getPrefab(name: string): cc.Prefab {
        return ResSubMgr.getPrefab(this.bundleName, name);
    }

    static loadRes(path: string, type: typeof cc.Asset, callback:Function=null) {
        return new Promise((resolve, reject) => {
            var bundle = cc.assetManager.getBundle(this.bundleName);
            bundle.load(path, cc.AudioClip, (err, ret) => {
                if (err) {
                    cc.error(path, err);
                    callback(err, null);
                    reject(null);
                }
                else {
                    if (callback)
                        callback(null, ret);
                    resolve(ret);
                }
            });
        })
    }
}