import Character from "../../../character/Character";
import { PropertyProvider } from "../../PropertyModifier";
import BuffObj from "./BuffObj";

export default class BuffProperty extends BuffObj {
    private proiver: PropertyProvider = null;

    onAdd(modifyStack: number): void {
        var name = this.buffParam[0];
        var percentage = parseFloat(this.buffParam[1]);
        var value = this.buffParam[2] ? parseFloat(this.buffParam[2]) : 0;
        this.proiver = new PropertyProvider(name, value, percentage);
        this.owner.getComponent(Character).addBuffProperty(this.proiver.property, this.proiver);
    }

    onRemoved(): void {
        this.owner.getComponent(Character).removeBuffProperty(this.proiver.property, this.proiver);
    }
}