    /**
     * 打点
     * @param eventName 事件名，注意不能超过 110 字符
     * @param data 上报的数据 可选 {}
     */
    public static reportAnalytics(eventName: string, data: { [key: string]: number | string } = {}) {
        if (AppPlatform.is_TT_GAME_csryw()) {
            window["tt"].reportAnalytics(eventName, data);
        }
    }


    //-------------------录屏-------------------------------------------
    /**
     * 配置录屏
     */
    private static inRecord: boolean = false;
    private static onRecordStopCallback: Function = null;
    private static initRecord_csryw() {
        TTAPI.record_csryw = window["tt"].getGameRecorderManager();
        if (TTAPI.record_csryw != null) {
            TTAPI.record_csryw.onStart((res) => {
                console.log("录屏开始");
                TTAPI.inRecord = true;
                TTAPI.recordRes_csryw = "";
            })

            TTAPI.record_csryw.onStop((res) => {
                console.log("录屏结束");
                TTAPI.inRecord = false;
                TTAPI.recordRes_csryw = res.videoPath;
                TTAPI.onRecordStopCallback && TTAPI.onRecordStopCallback();
                TTAPI.onRecordStopCallback = null;
            })
        }
    }

    /**
     * 开始录屏
     */
    public static startRecord_csryw(duration = 300) {
        if (!AppPlatform.is_TT_GAME_csryw()) {
            return;
        }
        console.log("开始录屏");
        TTAPI.record_csryw.start({
            duration
        })
    }

    /**
    * 停止录屏
    */
    public static stopRecord_csryw(callback?: Function) {
        if (!AppPlatform.is_TT_GAME_csryw()) {
            return;
        }
        if (this.inRecord) {
            TTAPI.record_csryw.stop();
            TTAPI.onRecordStopCallback = callback;
        } else {
            callback && callback();
        }
    }

    public static recordClip(timeRange: number[], callback: (index: number) => void) {
        if (!AppPlatform.is_TT_GAME_csryw()) {
            return;
        }
        TTAPI.record_csryw.clipVideo({
            timeRange: timeRange,
            success(r) {
                console.log(r.index); // 裁剪唯一索引
                callback && callback(r.index);
            },
        });
    }

    public static clipVideo(timeRange: number[], callback: Function) {
        if (!AppPlatform.is_TT_GAME_csryw()) {
            return;
        }
        console.log("mark1", TTAPI.recordRes_csryw);
        var args = {
            path: TTAPI.recordRes_csryw,
            timeRange: timeRange,
            success(res) {
                TTAPI.recordRes_csryw = res.videoPath;
                console.log("mark2", TTAPI.recordRes_csryw);
                callback && callback();
            },
            fail(e) {
                console.error(e);
            },
        };
        console.log("mark2", args.path);
        TTAPI.record_csryw.clipVideo(args);
    }

    //----------------------------------------------------------------------

    //---------------------分享录屏----------------------------------------
    public static shareRecord_csryw(callback: Function = null, Failcallback: Function = null) {

        if (!AppPlatform.is_TT_GAME_csryw()) {
            callback && callback();
            return;
        }
        if (TTAPI.recordRes_csryw != "") {
            window["tt"].shareAppMessage({
                channel: "video",
                extra: {
                    videoPath: TTAPI.recordRes_csryw, // 可替换成录屏得到的视频地址
                    videoTopics: [AppConfig.GameName_csryw]
                },
                success() {
                    if (callback != null) {
                        callback();
                    }
                    console.log("分享视频成功");
                },
                fail(e) {
                    console.log("分享视频失败", e);
                    let isTimeShortError = false;//分享的时间过短
                    if (e) {
                        if (e.errNo == 21105) {//分享视频时间过短
                            isTimeShortError = true;
                            window["tt"].showToast({
                                title: "分享失败，分享视频时间过短"
                            });
                        }
                    }
                    if (Failcallback != null) {
                        Failcallback(e, isTimeShortError);
                    }
                }
            });
        } else {
            if (Failcallback != null) {
                Failcallback();
            }
            console.log("分享视频为空");
        }
    }
