    /**
     * 设置写入用户的排行榜数据（游戏成绩信息）
     * @dataType  0：数字类型（得分） 、 1：枚举类（段位、评级）
     * @value 用户得分、段位、评级
     * @priority  权重（dataType为0时可不填，dataType为1时 不同段位的排名）
     * @extra 预留字段
     * @success 成功回调
     * @faild 失败回调
     */
    public static setRankData(dataType: number, value: string, priority: number, extra: string = "extra", success: Function, faild: Function) {
        if (AppPlatform.is_TT_GAME_csryw()) {
            window["tt"].setImRankData({
                dataType: dataType, //成绩为数字类型
                value: value, //该用户得了999999分
                priority: priority, //dataType为数字类型，不需要权重，直接传0
                extra: extra,
                success(res) {
                    console.log(`setImRankData success res: ${res}`);
                    success && success();
                },
                fail(res) {
                    console.log(`setImRankData fail res: ${res.errMsg}`);
                    faild && faild();
                },
            });
        }
    }


    /**
     * 获取排行榜列表，调用 API 后， 根据参数自动绘制游戏好友排行榜 native UI 
     * @relationType default: 好友及总榜都展  friend：仅好友排行榜  all：仅总榜单
     * @dataType 0：数字类型 、 1：枚举类型
     * @rankType day、week、month、all
     * @suffix 数据后缀(分)
     * @rankTitle 排行榜标题
     * @success 成功回调
     * @faild 失败回调
     */
    public static getRankList(relationType: string, dataType: number, rankType: string, suffix: string, rankTitle: string, success: Function, faild: Function) {
        window["tt"].getImRankList({
            relationType: relationType, //只展示好友榜
            dataType: dataType, //只圈选type为数字类型的数据进行排序
            rankType: rankType, //每月1号更新，只对当月1号到现在写入的数据进行排序
            suffix: suffix, //数据后缀，成绩后续默认带上 “分”
            rankTitle: rankTitle, //标题
            success(res) {
                console.log(`getImRankData success res: ${res}`);
                success && success();
            },
            fail(res) {
                console.log(`getImRankData fail res: ${res.errMsg}`);
                faild && faild();
            },
        });
    }

    /**
     * 获取排序好的「好友/总榜」数据源
     * @relationType default: 好友及总榜都展  friend：仅好友排行榜  all：仅总榜单
     * @dataType 0：数字类型 、 1：枚举类型
     * @rankType day、week、month、all
     * @success 成功回调
     * @faild 失败回调
     * https://developer.open-douyin.com/docs/resource/zh-CN/mini-game/develop/open-capacity/game-rank/getImRankData
     */
    public static getRankData(relationType: string, dataType: number, rankType: string, success: Function, faild: Function) {
        window["tt"].getImRankData({
            relationType: relationType,
            dataType: dataType,
            rankType: rankType,
            pageNum: 1,
            pageSize: 40,
            success(res, data) {
                console.log(`getImRankData success res: ${res}`);
                success && success(data);
            },
            fail(res) {
                console.log(`getImRankData fail res: ${res.errMsg}`);
                faild && faild();
            },
        });
    }

