import Gizmos from "../../tools/Gizmos";
import AABB from "../../tools/objects/AABB";
import Collision from "./Collision";

const {ccclass, property} = cc._decorator;

@ccclass
export default class BoxCollision extends Collision {
    get shape(): number {
        return cc.geomUtils.enums.SHAPE_AABB;
    }

    getShape(): AABB {
        var aabb = new AABB;
        this.node.convertToWorldSpaceAR(cc.Vec2.ZERO, aabb.center);
        aabb.halfExtents.x = this.node.width / 2;
        aabb.halfExtents.y = this.node.height / 2;
        return aabb;
    }

    public draw(): void {
        var shape = this.getShape();
        Gizmos.DrawRect(shape.center.x - shape.halfExtents.x, shape.center.y - shape.halfExtents.y, shape.halfExtents.x * 2, shape.halfExtents.y * 2);
    }
}
