export default class Common {

    /**
    * 创建一个预制体
    * @param prefab 预制体
    * @param type 类型
    * @param parent 接收的父节点 为null 则放到场景中 场景必须要有 Canvas 节点
    */
    public static createPrefab_csryw<T extends cc.Component>(prefab: cc.Prefab, type: { prototype: T }, parent?: cc.Node,): T {
        let node = cc.instantiate(prefab);
        if (parent) {
            parent.addChild(node);
        } else {
            cc.director.getScene().getChildByName('Canvas').addChild(node);
        }
        let src = node.getComponent(type);
        return src;
    }

    public static loadSpriteFrame(bundle: string, path: string, callback: (spriteFrame: cc.SpriteFrame) => void): void {
        let subres = cc.assetManager.getBundle(bundle);
        if (subres) {
            subres.load(path, cc.SpriteFrame, (err, asset: cc.SpriteFrame) => {
                if (err) {
                    console.log('加载SpriteFrame错误>:', err);
                    return;
                }
                if (callback) {
                    callback(asset);
                }
            })
        }
    }
}
