import EffectManager from "../manager/EffectManager";

const {ccclass, property} = cc._decorator;

@ccclass
export default class BattleEffect extends cc.Component {
    prefab: string;

    owner: cc.Node;

    @property(cc.Boolean)
    public autoRemove: boolean = true;

    @property(cc.Float)
    public removeDelay: number = 2;

    protected start(): void {
        if (this.autoRemove) {
            this.scheduleOnce(this.removeSelf, this.removeDelay);
        }
    }

    protected removeSelf(): void {
        EffectManager.inst.removeObj(this.prefab, this.node);
    }

    public init(data: any): void {

    }
}