import { ryw_Event } from "../../../FrameWork/Event/EventEnum";
import EventMgr from "../../../FrameWork/Event/EventMgr";
import User from "../../../FrameWork/User/User";

const {ccclass, property} = cc._decorator;

@ccclass
export default class MoneyWeight extends cc.Component {
    @property(cc.Label)
    moneyLabel: cc.Label = null;

    protected start(): void {
        this.updateMoney();
    }

    public updateMoney(): void {
        this.moneyLabel.string = User.getMoney_csryw() + '';
    }

    protected onEnable(): void {
        EventMgr.onEvent_csryw(ryw_Event.ryw_Game_OnUserMoneyChange_csryw, this.updateMoney, this);
    }

    protected onDisable(): void {
        EventMgr.offEvent_csryw(ryw_Event.ryw_Game_OnUserMoneyChange_csryw, this.updateMoney, this);
    }
}