import FixComponent from "../../../common/fixcomponent/FixComponent";
import GamaManager, { GameState } from "../../manager/GamaManager";
import Hero from "../Hero";

const { ccclass, property } = cc._decorator;

@ccclass
export default class WeaponTw extends FixComponent {
    @property(cc.Texture2D)
    normalTex: cc.Texture2D = null;

    @property(cc.Texture2D)
    superTex: cc.Texture2D = null;

    @property(cc.MotionStreak)
    motionStreak: cc.MotionStreak = null;

    @property([cc.ParticleSystem])
    particleSystems: cc.ParticleSystem[] = [];

    private hero: Hero;

    private startRange: number = 0;
    private tuoweiWidth: number = 30;
    private tuoweiMaxWidth: number = 150;

    private particleTotalCount: number = 0;

    protected onLoad(): void {
        this.motionStreak.texture = this.normalTex;
        if (this.particleSystems.length > 0) {
            this.particleTotalCount = this.particleSystems[0].totalParticles;
        }
    }

    public init(hero: Hero): void {
        this.hero = hero;
        this.startRange = hero.getProperty("range");
    }

    public updateTuowei(): void {
        this.motionStreak.stroke = Math.min(this.tuoweiWidth + (this.hero.getProperty("range") - this.startRange) * 1, this.tuoweiMaxWidth);
        this.updateTuoweiTex();
    }

    private updateTuoweiTex(): void {
        var texture = this.hero.getProperty("supper") > 0 ? this.superTex : this.normalTex;
        if (this.motionStreak.texture != texture) {
            this.motionStreak.texture = texture;
        }
    }

    public fixUpdate(): void {
        if (this.hero == null) {
            return;
        }

        if (this.hero.moving != this.motionStreak.node.active) {
            this.motionStreak.reset();
            this.motionStreak.node.active = this.hero.moving;
            this.updateTuoweiTex();
            var count = !this.hero.moving ? 0 : this.particleTotalCount;
            if (this.particleSystems.length != 0 && this.particleSystems[0].totalParticles != count) {
                this.particleSystems.forEach(item => item.totalParticles = count);
            }
        }
    }
}