import Character from "../../../character/Character";
import BuffObj from "./BuffObj";
import BuffWitchCd from "./BuffWitchCd";

export default class BuffShield extends BuffWitchCd {
    private orginShield: number = 0;

    public onAdd(count: number): void {
        super.onAdd(count);
        this.addShield();
    }

    private addShield(): void {
        var character = this.owner.getComponent(Character);
        var shield = character.getProperty("maxHp") * 0.2;
        character.addShield(shield);
        this.orginShield = shield;
    }

    protected onCdOver(): void {
        super.onCdOver();
        this.addShield();
    }

    public onTick(dt: number): void {
        super.onTick(dt);

        var character = this.owner.getComponent(Character);
        if (this.orginShield > 0 && character.shield <= 0) {
            this.resetCd();
        } 
        this.orginShield = character.shield;
    }
}