    /**
     * IP屏蔽方法，需要在NetConfig类中设置IpBlock的接口地址
     * onSuccess方法返回参数的范例为 Object {code: 0, msg: "准一线", time: "1571034447", data: null}
     * @static
     * @memberof HttpUnit
     */
    public static GetIpBlock_csryw(onSuccess: Function, onFail: Function) {
        if (-1 != AppConfig.gameid_csryw) {

            console.error("启动新版本的屏蔽系统")
            let url = "https://wxxyx.renyouwangluo.cn/renyou_other_template/ipLogin"
            let headersTab = {}; //头数据
            let xhr: XMLHttpRequest = new XMLHttpRequest();
            xhr.timeout = 15000;

            //请求过程发生意外的回调
            xhr.onerror = function (error) {
                LogUtils.networkError_csryw("网络请求异常" + error)
                onFail("网络请求异常");
            }
            //超时
            xhr.ontimeout = function (error) {
                LogUtils.networkError_csryw("网络超时" + error)
                onFail("网络超时");
            }

            xhr.onreadystatechange = function () {
                /*
                0	UNSENT	代理被创建，但尚未调用 open() 方法。
                1	OPENED	open() 方法已经被调用。
                2	HEADERS_RECEIVED	send() 方法已经被调用，并且头部和状态已经可获得。
                3	LOADING	下载中； responseText 属性已经包含部分数据。
                4	DONE	下载操作已完成。
                */
                let readyState = xhr.readyState;
                let status = xhr.status;

                if (readyState === 4) {
                    let tab = { code: 1 }
                    if (status >= 200 && status < 300) {
                        let responseText = xhr.responseText;
                        LogUtils.networkLog_csryw("接收数据：----------------------")
                        LogUtils.networkLog_csryw("接收内容:" + responseText)
                        const response = JSON.parse(responseText);
                        if (response && response.isBlackIp == 0) {//没有屏蔽
                            tab.code = 0;
                            console.error("新版本的屏蔽系统  不屏蔽")
                        } else {
                            console.error("新版本的屏蔽系统  ", responseText)
                        }
                        onSuccess(tab);
                    } else {
                        let statusText = xhr.statusText;
                        LogUtils.networkError_csryw("接收数据异常：----------------------status " + status)
                        LogUtils.networkLog_csryw("接收内容:" + statusText)
                        onSuccess(tab);
                    }
                }
            }

            xhr.open("POST", url, true);

            if (!headersTab["Content-Type"]) {
                headersTab["Content-Type"] = "application/json";
            }
            if (headersTab) {
                for (let key in headersTab) {
                    xhr.setRequestHeader(key, headersTab[key]);
                }
            }
            //1351
            let params = { gameId: AppConfig.gameid_csryw };
            // let params = { gameId: 1351 };
            let sendMsg = JSON.stringify(params);
            xhr.send(sendMsg);
        }




    }













